(function () {
    'use strict';
    var TAG = "[index.bootstrap.js] ";

    /**---------------- APPLICATION BOOTSTRAP  ----------------**/

    /* Get paramerters from the backend before the application launching. */
    console.log(TAG + "start... get(/frontend/parameters)...");
    $.get("/frontend/parameters", function (data) {

        /* Set constants with the received parameters */
        console.log(TAG + "Parameters received and put in constants...");
        console.log(TAG + "View list lenght: " + data.viewList.length);
        angular.module('tsa.bootstrap', [])
            .constant('APP_PROPERTIES', data)
            .constant('DATA_ACCESS', {
                'NAVIGATION': data.dataAccessLists.navigation,
                'WEATHER': data.dataAccessLists.weather,
                'THERMOSAL': data.dataAccessLists.thermosal
            })
            .constant('SHIP_ID', data.shipId)
            .constant('STOMP_URL', data.hasOwnProperty('brokerIP') ? 'ws://' + data.brokerIP : getDefaultUrl('ws', '/ws'))
            .constant('MD_ADDONS_URL', data.hasOwnProperty('mdAddonsURL') ? data.mdAddonsURL : '/md-addons')
            .constant('JANUS_SERVER_URL', data.hasOwnProperty('janusServerUrl') ? data.janusServerUrl : getDefaultUrl('ws', '/janus/ws'))
            .constant('ENABLE_VIDEOCALL', data.hasOwnProperty('enableVideoCall') ? JSON.parse(data.enableVideoCall) : true)
            .constant('GEOMOD_SERVER_ADDRESS', 
                data.hasOwnProperty('geomodServerAddress') ? 
                    data.geomodServerAddress : 
                    '/gis/geomod')
            .constant('QGIS_URL', 
                data.hasOwnProperty('qgisUrl') ? 
                    data.qgisUrl : 
                    '/gis/qgis')
            .constant('QGIS_MISSION_NAME', data.qgisMissionName)
            .constant('DEFAULT_DASHBOARD', data.defaultDashboard)
            .constant('DEFAULT_STATIC_GRID', data.defaultStaticGrid)
            .constant('VIEW_LIST', data.viewList)
            .constant('VIDEO_PARAMS', data.video)
            .constant('SHOW_EXTRACTION', data.hasOwnProperty('showExtraction') ? JSON.parse(data.showExtraction) : true)
            .constant('SHOW_STATICGRID', data.hasOwnProperty('showStaticGrid') ? JSON.parse(data.showStaticGrid) : true)
            .constant('SHOW_TRACKING', data.hasOwnProperty('showTracking') ? JSON.parse(data.showTracking) : true)
            .constant('ENABLE_CHAT', data.hasOwnProperty('enableChat') ? JSON.parse(data.enableChat) : true)
            .constant('MAP_BACKGROUND', data.hasOwnProperty('mapBackground') ? data.mapBackground : 'GEOMOD')
            .constant('ARCHIVES_REST_API', {
                DATA: data.hasOwnProperty('databaseIP') ?
                    'http://' + data.databaseIP + '/api/archives/table' :
                    '/api/archives/table',
                DEVICES: data.hasOwnProperty('databaseIP') ?
                    'http://' + data.databaseIP + '/api/archives/devices' :
                    '/api/archives/devices'
            });

        /* Manual bootstrap (replace "ng-app") */
        angular.element(document).ready(function () {
            console.log(TAG + "Launch the application: angular.bootstrap(...)");
            angular.bootstrap(document, ['tsa']);
        });

    })
    .fail(function (error) { /*Load error module if the request has failed */
        console.error(TAG + "Error : frontend parameters loading failed!");
        console.error(error);
        angular.module(['error']).constant('ERROR', error);
        angular.bootstrap(document, ['error']);
    });

    function getDefaultUrl(protocol, path) {
        var res;
        if(window.location.protocol === 'http:')
        	res = protocol + '://' + window.location.host + path;
        else
            res = protocol + 's://' + window.location.host + path;
        console.log('url : ' + res);
        return res;
    }


})();

(function () {
    'use strict';
    angular
        .module('tsa')
        .config(function ($mdThemingProvider) {
            //Ifremer colors
            //bleu0: 062240
            //bleu1: 093564
            //bleu2: 1A75AE
            //bleu3: 2C97CB
            //jaune: feed01

            // Ifremer palette
            $mdThemingProvider.definePalette('ifremerPalette', {
                '50': 'D50000',
                '100': 'D50000',
                '200': 'D50000',
                '300': '2C97CB',//hue-1 -> bleu3
                '400': 'D50000',
                '500': '1A75AE',//default -> bleu2
                '600': '1A75AE',//'e53935',
                '700': 'D50000',
                '800': '093564',//hue-2 ->bleu1
                '900': 'D50000',
                'A100': '062240',//hue-3 // accent - 2 -> bleu0
                'A200': '1A75AE',// accent - 1
                'A400': '1A75AE',// accent - 3
                'A700': '1A75AE',// accent - 4
                'contrastDefaultColor': 'light',    // whether, by default, text (contrast)
                                                    // on this palette should be dark or light
                'contrastDarkColors': ['50', '100', //hues which contrast should be 'dark' by default
                    '200', '300', '400', 'A100'],
                'contrastLightColors': undefined    // could also specify this if default was 'dark'
            });

            $mdThemingProvider.theme('default')
                .primaryPalette('ifremerPalette')
                .accentPalette('ifremerPalette');

            $mdThemingProvider.theme('day')
                .primaryPalette('ifremerPalette')
                .accentPalette('ifremerPalette');// define a color intention called black

            $mdThemingProvider.theme('night')
                .primaryPalette('ifremerPalette')
                .accentPalette('ifremerPalette')  // define a color intention called white
                .dark();

            // This is the absolutely vital part, without this, changes will not cascade down through the DOM.
            $mdThemingProvider.alwaysWatchTheme(true);
        })
        //-----------------  Internationalization  -------------------
        //To have warnings in console regarding forgottenID
        //.config(function ($translateProvider) {
        //    $translateProvider.useMissingTranslationHandlerLog();
        //})
        //Asynchronous loading for the translations
        .config(function ($translateProvider) {
            $translateProvider.useStaticFilesLoader({
                prefix: 'resources/locale-',// path to translations files
                suffix: '.json'// suffix, currently- extension of the translations
            });

            $translateProvider.preferredLanguage('fr_FR');// is applied on first load
            $translateProvider.fallbackLanguage('en_US');// is applied on first load
           // $translateProvider.useLocalStorage();// saves selected language to localStorage TODO: resolve synchronization issues
        })
        //direction of where to load the $locale settings files for angular-dynamic-locale:
        .config(function (tmhDynamicLocaleProvider) {
            tmhDynamicLocaleProvider.localeLocationPattern('../locales/angular-locale_{{locale}}.js');
        });
})();

