(function () {
    'use strict';
    angular.module('tsa.components.console')
        .component('tsaConsole', {
            templateUrl: "app/components/console/console.html",
            controller: consoleController,
            controllerAs: "ctrl",
            bindings: {
                container: '<'
            }
        });


    /**
     * Controller for the dataView component which displays a data and its current value.
     */
    function consoleController() {

        var TAG = '[ConsoleController] ';
        var vm = this;
        var textArea;
        var logs = [];
        var alertLogs = [];

        vm.consoleViews = {
            LOGS: 'logs',
            ALERTS: 'alerts',
            FRAMES: 'frames'
        };
        vm.consoleView = vm.consoleViews.LOGS;


        vm.$onInit = function () {
            textArea = document.getElementById('acquisition-textarea');
            vm.container.addConsole(this);
        };



        vm.addText = function addText(msg, level) {
            console.log(TAG + 'CONSOLE ADD TEXT:' + msg);

            switch (level) {
                case vm.consoleViews.LOGS:
                    logs.push(msg);
                    break;
                case vm.consoleViews.ALERTS:
                    logs.push(msg);
                    alertLogs.push(msg);
                    break;
            }

            if (level && vm.consoleView != level)
                return;

            vm.console += msg + "\n";
            if (vm.console.length > 50000)
                vm.console = vm.console.substring(vm.console.length / 3);
            if (textArea !== undefined)
                textArea.scrollTop = textArea.scrollHeight;

        };

        /**
         * Method called when a new console tab is selected.
         */
        vm.changeConsole = function changeConsole(value) {
            vm.console = "";
            vm.consoleView = value;

            var logsToDisplay = [];
            switch (vm.consoleView) {
                case vm.consoleViews.LOGS:
                    logsToDisplay = logs;
                    break;
                case vm.consoleViews.ALERTS:
                    logsToDisplay = alertLogs;
                    break;
            }

            angular.forEach(logsToDisplay, function (log) {
                vm.addText(log);
            });
        };

        /**
         * Called whenever one-way bindings (dataId, frameId, deviceId) are updated.
         * Used for initialization.
         */
        /*        vm.$onChanges = function () {
         };*/

        /**
         * Called when the view is destroyed.
         */
        /*    vm.$onDestroy = function onDestroy() {
         }*/
    }
})();


(function () {
    'use strict';

    /** Component **/
    angular.module('tsa.components.dataView', [])
        .component('tsaDataView', {
            templateUrl: "app/components/dataView/dataView.html",
            controller: dataViewController,
            controllerAs: "ctrl",
            bindings: {
                dataId: '<data', //!\ Warning: do not specify a property that could create conflicts with the global html5 property: 'data-*' /!\
                frameId: '<',
                deviceId: '<',
                optTitle: '@',
                optSubtitle: '<',
                optValue: '<',
                onUpdateTitle: '&',
                titleDisabled: '<',
                showSubtitle: '<',
                abs: '<',
                onOff: '<',
                multiplier: '<',
                redGreen: '<',
                alertValue: '<',
                format: '@',
                units: '@'
            }
        });

    /** Controller **/
    function dataViewController(acquisitionService, formatterService, statusService, $interval) {
        var vm = this;

        /**------------------------------ PROPERTIES -------------------------------**/

        /** Properties **/
        var watchdogInterval;
        var lastTimestamp;
        var dataListener;
        var missingValueReceived;

        /** View properties */
        vm.title = '';
        vm.subtitle = '';
        vm.value = '-';
        vm.dataMissing = false;
        vm.frontColor = '';

        /**-------------------------- INITIALIZATION / DESTROY METHODS -------------------------**/

        /** Init function: launch the watchdog to check data reception **/
        vm.$onInit = function () {
            watchdogInterval = $interval(watchdog, 500);
        };

        /** OnChanges: Called whenever one-way bindings (dataId, frameId, deviceId, optValue...) are updated. */
        vm.$onChanges = function (changesObj) {

            // Case 1: dataId, frameId and deviceId are specified
            if (changesObj['dataId'] || changesObj['frameId'] || changesObj['deviceId']) {
                unregister(); //unregister for previous data
                register();
            }

            //Case 2: value and title are directly specified in parameters
            if (changesObj['optValue']) {
            	if(angular.isDefined(changesObj['optValue'].currentValue)) {
                    updateValue(vm.optValue);
            	} else {
            		vm.value = '';
            	}
            }
            if (changesObj['optTitle'] && angular.isDefined(changesObj['optTitle'].currentValue))
                vm.title = vm.optTitle;
            if (changesObj['optSubtitle'] && angular.isDefined(changesObj['optSubtitle'].currentValue))
                vm.subtitle = vm.optSubtitle;
        };

        /** Called when the view is destroyed. */
        vm.$onDestroy = function onDestroy() {
            unregister();
            $interval.cancel(watchdogInterval);
        };

        /**--------------------------  METHODS -------------------------**/

        /** Register for the selected data **/
        function register() {
            if (angular.isDefined(vm.dataId) && angular.isDefined(vm.frameId) && angular.isDefined(vm.deviceId)) {
                vm.device = acquisitionService.registerForDevice(vm.deviceId);
                updateTitle();
                dataListener = vm.device.registerForData(vm.frameId, vm.dataId);
                dataListener.promise.then({}, {},
                    function callback(result) {
                        lastTimestamp = result.timestamp;
                        updateTitle();
                        updateValue(result.value);
                    });
            }
            else {
                updateTitle();
            }
        }

        /** Unregister for the data, and reset fields **/
        function unregister() {
            if (vm.device != undefined && dataListener != undefined)
                vm.device.unregisterForData(dataListener);
            vm.device = undefined;
            vm.title = vm.subtitle = '';
            vm.subtitle = '';
            vm.value = '';
            vm.dataMissing = false;
            missingValueReceived = undefined;
        }


        /** Update value  */
        function updateValue(newValue) {
            var desc = getDataDescription();

            // Get units and format (component parameters are used in priority)
            var units = angular.isDefined(vm.units) ? vm.units : angular.isDefined(desc) ? desc.shortUnits : undefined;
            var format = angular.isDefined(vm.format) ? vm.format : angular.isDefined(desc) ? desc.cFormat : undefined;

            // Force the format for coordinates and time values
            if (vm.dataId == 'lat' || vm.dataId == 'long' || vm.dataId == 'time')
                format = vm.dataId;

            // Set value
            vm.value = getFormattedValue(newValue, format, units, vm.multiplier, vm.abs, vm.onOff);

            // Determinate if the value == "missingValue"
            missingValueReceived = (angular.isDefined(desc) && angular.isDefined(desc.missingValue)) ? newValue == desc.missingValue : false;

            if(!missingValueReceived && vm.redGreen) {
                vm.frontColor = '';
                if(Math.abs(Math.round(newValue))>0) {
                    if(newValue > 0) {
                        vm.frontColor = 'green';
                    } else {
                        vm.frontColor = 'red';
                    }
                }
            }

            if  (vm.alertValue && newValue < vm.alertValue) {
                vm.frontColor = 'alert';
            } else {
                vm.frontColor = '';
            }
        }

        /** Update title and subtitle */
        function updateTitle() {
            var desc = getDataDescription();
            vm.title = (vm.optTitle && vm.optTitle != '') ? vm.optTitle : angular.isDefined(desc) ? desc.longName : vm.dataId;
            if (angular.isDefined(vm.deviceId) + angular.isDefined(vm.frameId))
                vm.subtitle = vm.deviceId + ' - ' + vm.frameId;
            else if (angular.isDefined(vm.optSubtitle)) {
                vm.subtitle = vm.optSubtitle;
            }
        }

        /* Get the data description (provided by REST service)*/
        function getDataDescription() {
            if (angular.isUndefined(vm.device)
                || angular.isUndefined(vm.device.frameList)
                || angular.isUndefined(vm.device.frameList[vm.frameId])
                || angular.isUndefined(vm.device.frameList[vm.frameId][vm.dataId]))
                return undefined;
            return vm.device.frameList[vm.frameId][vm.dataId];
        }

        /** Get the value formatted and with units **/
        function getFormattedValue(value, format, units, multiplier, abs, onOff) {

            if (Array.isArray(value))   // Display the length if the value is an array
                return value.length + (value.length > 0 ? ' values' : 'value');

            if (angular.isDefined(multiplier)) // Apply multiplier
                value *= multiplier;

            if (angular.isDefined(abs)) // Get absolute value
                value = Math.abs(value);

            if(angular.isDefined(onOff) && onOff) {
                value = value > 0 ? 'On' : 'Off';
            } else {
                if (angular.isDefined(format)) { //Apply format
                    switch (format) {
                        case 'excelDate': value = formatterService.parseExcelDateDefaultToString(value, 'UTC'); break;
                        case 'excelDateNoSec': value = formatterService.parseExcelDateNoSecToString(value, 'UTC'); break;
                        case 'excelDateNoSecTZ': value = formatterService.parseExcelDateNoSecToString(value, statusService.getLocalTimeZoneOffset()); break;
                        case 'lat': value = formatterService.parseLatToString(value); break;
                        case 'long': value = formatterService.parseLongToString(value); break;
                        case 'qualityFbx': value = formatterService.parseQualityFbxToString(value); break;
                        case 'marineDistance': (Math.abs(value)<1852) ? units="m" : units="nm";
                            value = formatterService.parseMarineDistance(value);
                            value = sprintf("%.1f", value); break;
                        case 'time': value = formatterService.parseTimeToString(value); break;
                        default:
                            value = value != 'SinV' ? sprintf(format, value) : '';
                            break;
                    }
                }

                if (angular.isDefined(units)) //Add units
                    value += ' ' + units;
            }
            return value;
        }

        /** Watch the data reception **/
        function watchdog() {
            var currentDate = statusService.getServerTime().time;
            vm.dataMissing = angular.isDefined(vm.device) &&
                ((angular.isUndefined(lastTimestamp) || currentDate - lastTimestamp > 5 * vm.device.periodList[vm.frameId] && vm.optValue == undefined)
                    || missingValueReceived);
        }

    }
})();

