/**  Data access page controller */
(function () {
    'use strict';
    angular
        .module('tsa.pages.dataAccess')
        .controller('dataAccessController', dataAccessController);

    function dataAccessController($filter, $log, $http, $window, archiveService, dialogService, userService, AUTHORITIES, Blob, FileSaver) {

        /** Authorisation checking **/
        if (!userService.checkAuthority(AUTHORITIES.MASTER))
            return;

        /**---------------- PROPERTIES ----------------**/
        var vm = this;
        vm.logger = $log.getInstance("data-access-page");
        vm.currentMode = "simple";

        /**---------------- METHODS ----------------**/

        /**  Send the request to retrieve the archived data
         *   @param format: json, csv or netcdf
         */
        vm.sendRequest = function sendRequest(format) {
            if (vm.researchInProgress || vm.csvInProgress || vm.netcdfInProgress)
                return;

            // Step 1: get inputs from the nested components
            var dateRange = vm.getDateRange();
            var params = vm.getParams();
            var variableList = vm.getVariableList();

            if(variableList.length < 1)
                return;

            // Step 2: define the request
            function request() {
                switch (format) {
                    case 'json': vm.researchInProgress = true; break;
                    case 'csv': vm.csvInProgress = true; break;
                    case 'netcdf': vm.netcdfInProgress = true; break;
                }
                archiveService.getData(format,
                    variableList,
                    dateRange.start,
                    dateRange.end,
                    params.maxValues,
                    params.timeStampingStrategy,
                    params.intervalOrRef,
                    params.intervalStampFraction)
                    .then(
                    function (response) {
                        switch (format) {
                            case 'json':
                                vm.researchInProgress = false;

                                dialogService.showToast(response.data.length + " " + $filter('translate')("pages.data-access.frames-received"));
                                vm.results = response.data;
                                break;
                            case 'csv':
                                vm.csvInProgress = false;
                                var data = new Blob([response.data], { type: "text/csv" });
                                FileSaver.saveAs(data, "table.csv");
                                break;
                            case 'netcdf':
                                vm.netcdfInProgress = false;
                                var data = new Blob([response.data], { type: "application/octet-stream" });
                                FileSaver.saveAs(data, "table.nc");
                                break;
                        }
                    },
                    function (error) {
                        switch (format) {
                            case 'json': vm.researchInProgress = false; break;
                            case 'csv': vm.csvInProgress = false; break;
                            case 'netcdf': vm.netcdfInProgress = false; break;
                        }
                        dialogService.showDialog($filter('translate')("pages.data-access.error"), $filter('translate')("pages.data-access.error-request-msg"));
                        vm.logger.error(angular.isDefined(error.data) ? error.data : angular.isDefined(error.message) ? error.message : 'no message found');
                    });
            }

            // Step 3: check params and send the request
            var dataRequested = (dateRange.end - dateRange.start) / (params.intervalOrRef * 1000);
            vm.logger.debug("Number of data requested: " + dataRequested);
            if (dataRequested > params.maxValues) {
                dialogService.showValidation(
                    $filter('translate')("pages.data-access.warning"),
                    $filter('translate')("pages.data-access.warning-msg"),
                    $filter('translate')("pages.data-access.continue"),
                    $filter('translate')("pages.data-access.cancel"))
                    .then(request);
            }
            else request();
        }
    }
})();

/***
 * Login page controller
 */
(function () {
    'use strict';
    angular
        .module('tsa.pages.login')
        .controller('loginController', loginController);

    function loginController($filter, $log, $rootScope, $scope, $state, $stateParams, userService, dialogService) {
        var vm = this;
        vm.logger = $log.getInstance("loggin-page");
        vm.connectionInProgress = false;
        vm.logger.log('Authentication, with authority type "' + $stateParams.authority + '", is required to access the ' + $stateParams.next + ' page.');

        // Check if there is not already a user connected
        if (userService.getCurrentUser() != undefined && userService.getCurrentUser().hasAuthority($stateParams.authority))
            goToNextPage();

        /** Navigate to the next page **/
        function goToNextPage() {
            if ($stateParams.next != undefined)
                $state.go($stateParams.next, $stateParams.nextParams);
        }

        /** Listen to user connection **/
        var deregistrationFunction = $rootScope.$on('current-user-changed', function () {
            if (userService.getCurrentUser() != undefined)
                goToNextPage();
        });

        /** Click on login button **/
        vm.connection = function connection() {
            if (vm.login !== undefined) {
                vm.connectionInProgress = true;
                userService.login(vm.login, vm.password).then(
                    function () {
                        vm.connectionInProgress = false;
                        goToNextPage();
                    },
                    function (error) {
                        vm.connectionInProgress = false;
                        dialogService.showDialog($filter('translate')("components.sidenav.connection-error"));
                        vm.logger.error('Authentication error: ' + error.status + ': ' + error.statusText);
                    });
            }
        };

        vm.onKeyPressed = function onKeyPressed(keyEvent) {
            if (keyEvent.which === 13)
                vm.connection();
        };

        $scope.$on("$destroy", function () {
            deregistrationFunction();
        })
    }
})();

/***
 * Static grid controller: manages the display of a card grid, or a single card.
 */
(function () {
    'use strict';

    angular.module('tsa.pages.staticGrid', []) //module declaration 
        .controller('staticGridController', StaticGridController);

    /** @ngInject */
    function StaticGridController($filter, $log, $scope, staticGridService, userService) {

        /**------------------------------ PROPERTIES -----------------------------**/
        var vm = this;
        var logger = $log.getInstance("staticGridController");
        vm.flex = 100; //Flex value (define size properties): single mode -> 100; multiple mode -> 50
        vm.cards = staticGridService.getCards();
        vm.userService = userService;
        vm.currentNavItemId = staticGridService.getSelectedCardId();
        vm.service = staticGridService;

        /**------------------------------ PROPERTIES -----------------------------**/

        vm.showRow = function showRow(index) {
            if (staticGridService.isSingleMode()) {
                return vm.cards[index][0].id === staticGridService.getSelectedCardId()
                    || vm.cards[index][1].id === staticGridService.getSelectedCardId()
                    || vm.cards[index][2].id === staticGridService.getSelectedCardId()
            }
            else return true;
        };

        vm.showCard = function showCard(item) {
            vm.currentNavItemId = staticGridService.getSelectedCardId(); //update the nav bar
            switch (staticGridService.getViewMode()) {
                case staticGridService.MODES.SINGLE:
                    vm.flex = 100;
                    return item.id === staticGridService.getSelectedCardId();
                case staticGridService.MODES.MULTIPLE_4:
                    vm.flex = 50;
                    return item.id != vm.cards[0][2].id && item.id != vm.cards[1][2].id;
                case staticGridService.MODES.MULTIPLE_6:
                    vm.flex = 33;
                    return true;
            }
        };

        /** Change the card displayed in the single view **/
        vm.selectCard = function selectCard(rowIndex, index) {
            staticGridService.setSelectedCardId(vm.cards[rowIndex][index].id);
        };

        /** Save the static view configuration (autosave) **/
        $scope.$watch(function () {
            return {
                cards: staticGridService.getCards(),
                mode: staticGridService.getViewMode(),
                selectedCard: staticGridService.getSelectedCardId()
            }
        }, function () {
            if (angular.isDefined(userService.getCurrentUser())) {
                userService.getCurrentUser().saveStaticGrid().then(
                    function success() { logger.info("Static grid configuration saved") },
                    function error(error) { logger.error('Error: ' + error); });
            }
        }, true);

        /** Solution to fix the following bug: when a card title is changed, the 'ink bar' (bottom bar under the selected
         * button) is not correctly resized. Solution: force a call to the '_updateInkBarStyles' (navBar.js from angular
         * material sources) with a set of the 'mdSelectedNavItem' parameter ( = 'currentNavItemId' in our context).
         * Todo: find a better solution to fix this bug
         **/
        $scope.$watch(function () { return vm.cards; },
            function () { vm.currentNavItemId = {}; },
            true);
    }
})();

