(function () {
    'use strict';
    angular.module("tsa.pages.staticGrid")
        .factory("staticGridService", function (DEFAULT_STATIC_GRID) {

            var MODES = { SINGLE: 'single', MULTIPLE_4: 'multiple-4', MULTIPLE_6: 'multiple-6' };

            var mode = MODES.MULTIPLE_4;

            var cards = angular.isDefined(DEFAULT_STATIC_GRID) ? DEFAULT_STATIC_GRID : [[
                { id: 'Card1', type: 'Graphics' },
                { id: 'Card2', type: 'Numerical' },
                { id: 'Card5', type: 'Devices' }
            ], [
                { id: 'Card3', type: 'Empty' },
                { id: 'Card4', type: 'Empty' },
                { id: 'Card6', type: 'Empty' }]
            ];

            var selectedCardId = cards[0][0].id;

            /** Service API **/
            return {
                /** VIEW MODE **/
                MODES: MODES,

                findVideoCall: function findVideoCall() {
                    var found = cards[0].find(
                        function(item) {
                            return item.type === 'Video Call';
                        });
                    found = found || cards[1].find(
                        function(item) {
                            return item.type === 'Video Call';
                        });
                    return found;
                },

                addVideoCallIfNeeded: function addVideoCallIfNeeded() {
                    if(!this.findVideoCall()) {
                        cards[0][0].type = 'Video Call';
                    }
                },

                changeViewMode: function changeViewMode() {
                    switch (mode) {
                        case MODES.SINGLE: mode = MODES.MULTIPLE_4; break;
                        case MODES.MULTIPLE_4: mode = MODES.MULTIPLE_6; break;
                        case MODES.MULTIPLE_6: mode = MODES.SINGLE; break;
                    }
                },

                getViewMode: function getViewMode() {
                    return mode;
                },

                setViewMode: function setViewMode(newMode) {
                    mode = newMode;
                },

                isSingleMode: function isSingleMode() {
                    return mode === MODES.SINGLE;
                },

                /** CARDS **/

                getCards: function getCards() {
                    return cards;
                },

                setCards: function setCards(newCards) {
                    //the object 'cards' is not directly replaced by 'newCards' with an assignation to preserve the
                    //data binding with the staticGrid controller.
                    cards[0] = newCards[0];
                    cards[1] = newCards[1];
                },

                getSelectedCardId: function getSelectedCardId() {
                    return selectedCardId;
                },

                setSelectedCardId: function setSelectedCardId(id) {
                    selectedCardId = id;
                }

            }
        });
})();

