/**
# USER

A user object is a representation of an user account.

It contains information about the user (login, name...) and provides methods to save applications settings (for example
a dashboard, or the configuration of the static view...).

> User objects are managed by the user service.

To instantiate a user object, declare the dependence to the user factory and call:

```javascript
var user = new User(login, name, type, config);
```

## EXAMPLE

### Method to save a user setting ('MY_CONFIG')

```javascript
function saveMY_CONFIG(configToSave){

    //STEP 0: save the old configuration, if the save request fails it's could be interesting to restore the previous state.
    var oldConfig = user.config.MY_CONFIG;

    //STEP 1: update the user 'config' property
    user.config.MY_CONFIG = configToSave;

    //STEP 2: send the request to save the current configuration
    var requestResult = user.saveConfig();

    //STEP 3: manage the request result
    requestResult.then(
                function success() {
                    console.log("The user configuration has been correctly saved!");
                },
                function error() {
                    console.error("The request has failed!");
                    user.config.MY_CONFIG = oldConfig; //Restore the previous state!
                });

    return requestResult; //return the requestResult (promise object)
}
```

## PROPERTIES
Name | Description
------------ | -------------
login | user login
name | user name
type | list of authorities owned by the user
config | application configuration parameters

## METHODS

*/
(function () {
    'use strict';
    angular.module("tsa.services").factory("User", UserFactory);
    function UserFactory($q, $log, dashboardService, restService, staticGridService) {

        var logger = $log.getInstance("userFactory");

        /*! Constructor */
        function User(login, name, roles, config) {
            this.login = login;
            this.name = name;
            this.roles = roles;
            this.config = config;
        }

        /** Check if the user has the specified authority
         * @param {string} authority
         * @return {boolean} result: true if the user has the specified authority
         */
        User.prototype.hasAuthority = function hasAuthority(authority) {
            return this.roles.indexOf(authority) != -1
        };

        /** Send the request to save the user config
         * @return {promise} request result
         */
        User.prototype.saveConfig = function saveConfig() {
            var user = this;
            var resultPromise = restService.saveUserConfig(user.login, angular.toJson(user.config));
            resultPromise.then(
                function () {//success
                    logger.debug(user.login + ' configuration saved');
                },
                function () {//error
                    logger.error('configuration save error')
                });
            return resultPromise;
        };

        /** Save the selected language
         * @param {string} language
         */
        User.prototype.saveLanguage = function saveLanguage(language) {
            this.config.language = language;
            this.saveConfig().then(function () { logger.debug("Language saved: " + language); });
        };

        /** Save the selected theme (day or night) */
        User.prototype.saveTheme = function saveTheme(theme){
            this.config.theme = theme;
            this.saveConfig().then(function () { logger.debug("Theme saved: " + theme); });
        }

        /** Save a dashboard in the current user config field
         * @param {string} title
         * @param {array} itemList dashboard's items
         * @return {promise} request result
         */
        User.prototype.saveDashboard = function saveDashboard(title, itemList) {
            var user = this;
            if (user.config.dashboardList == undefined)
                user.config.dashboardList = {};

            var oldDashboard = user.config.dashboardList[title];
            user.config.dashboardList[title] = itemList;

            var resultPromise = user.saveConfig();
            resultPromise.then(
                function () {//Success -> refresh the dashboard list
                    //refreshDashboardList();
                    dashboardService.setPersoDashboardList(angular.copy(user.config.dashboardList));
                    dashboardService.setCurrentDashboard(title, itemList);
                },
                function () {//Fail --> return the the old dashboard
                    if (oldDashboard != undefined)
                        user.config.dashboardList[title] = oldDashboard;
                    else delete user.config.dashboardList[title];
                });
            return resultPromise;
        };

        /** Remove a dashboard from the current user config field
         * @param {string} title
         * @return {promise} request result
         */
        User.prototype.removeDashboard = function removeDashboard(title) {
            var user = this;

            var oldDashboard = user.config.dashboardList[title];
            delete user.config.dashboardList[title];

            var resultPromise = user.saveConfig();
            resultPromise.then(
                function () {//Success -> refresh the dashboard list
                    //refreshDashboardList();
                    dashboardService.setPersoDashboardList(angular.copy(user.config.dashboardList));
                    dashboardService.switchToNewDashboard();
                },
                function () { //Fail --> reset the the old dashboard
                    user.config.dashboardList[title] = oldDashboard;
                });
            return resultPromise;
        };

        /** Save the static grid configuration in the current user config field
         * @return {promise} request result
         */
        User.prototype.saveStaticGrid = function saveStaticGrid() {
            var user = this;

            var oldStaticGridConfig = user.config.staticGrid;
            user.config.staticGrid = {
                cards: staticGridService.getCards(),
                mode: staticGridService.getViewMode(),
                selectedCard: staticGridService.getSelectedCardId()
            };

            var resultPromise = user.saveConfig();
            //If the save request fails, reset the previous config
            resultPromise.then({},
                function () {
                    user.config.staticGrid = oldStaticGridConfig;
                });
            return resultPromise;
        };

        return User;
    }
})();

/** # USER SERVICE

User service: this service manages the current user and provides login/logout methods.
*/
(function () {
    'use strict';
    angular.module("tsa.services").factory("userService", UserService);

    function UserService($cookies, $filter, $http, $location, $log, $q, $rootScope, $state, AUTHORITIES,
        dashboardService, dialogService, localeService, restService, staticGridService, themeService, User) {

        var logger = $log.getInstance("userService");
        var currentUser;
        var currentUserProvider = {
            currentUser: undefined
        };

        /*! PRIVATE METHODS */

        /*! Initialisation method: called when the service is launch, retrieve current user from cookies */
        (function init() {
            var token = $cookies.get('XSRF-TOKEN');
            if (token != undefined) {
                getUserFromServer().then({}, function error() {
                    // console.log('toto');
                    dialogService.showToast($filter('translate')("toast.user-disconnected"));
                    // $cookies.remove('XSRF-TOKEN');
                });
            }
        })();

        /*! Current user setter; load the user config */
        function setCurrentUser(user) {
            currentUser = user;
            currentUserProvider.currentUser = currentUser;
            if (currentUser != undefined) {
                //Load dashboard
                if (angular.isDefined(user.config) && angular.isDefined(user.config.dashboardList))
                    dashboardService.setPersoDashboardList(angular.copy(user.config.dashboardList));
                else dashboardService.setPersoDashboardList([]);
                //todo: restore the dashboard previously selected
                if (currentUser.config.dashboardList != null) {
                    var dash = Object.keys(currentUser.config.dashboardList)[0];
                    if (dash != null)
                        dashboardService.setCurrentDashboard(dash, currentUser.config.dashboardList[dash]);
                }
                //Load static grid
                if (currentUser.config.staticGrid != null) {
                    if (currentUser.config.staticGrid.cards != null)
                        staticGridService.setCards(currentUser.config.staticGrid.cards);
                    if (currentUser.config.staticGrid.mode != null)
                        staticGridService.setViewMode(currentUser.config.staticGrid.mode);
                    if (currentUser.config.staticGrid.selectedCard != null)
                        staticGridService.setSelectedCardId(currentUser.config.staticGrid.selectedCard);
                }
                //Load language
                if (angular.isDefined(currentUser.config.language)
                    && angular.isDefined(localeService.getLocaleList()[currentUser.config.language]))
                    localeService.setCurrentLocale(localeService.getLocaleList()[currentUser.config.language]);
                //Load theme
                if (angular.isDefined(currentUser.config.theme))
                    themeService.setTheme(currentUser.config.theme);

                logger.debug('user config loaded: ' + currentUser.name);

                //If the user has the authority "SDIV", the static page is displayed
                if (currentUser.hasAuthority(AUTHORITIES.SDIV))
                    $location.path('/static');
            }
            $rootScope.$emit('current-user-changed');
        }

        /*! Retrieve the current user from the server with the request '/api/user'
         * (the user is found with the CSRF Token retrieved with the login request) */
        function getUserFromServer() {
            var resultPromise = restService.getCurrentUser();
            resultPromise.then(
                function success(response) {
                    var user = response.data;
                    if (user) {
                        var config = user.config != null ? angular.fromJson(user.config) : {};
                        setCurrentUser(new User(user.login, user.name, user.roles, config));
                        dialogService.showToast($filter('translate')("toast.successfull-connection") + currentUser.name);
                        logger.debug('successful /api/user request, user retrieved: ' + currentUser.name);
                    } else {
                        logger.debug('current user not found (/api/user request error)');
                    }
                },
                function error() {
                    logger.debug('current user not found (/api/user request error)');
                }
            );
            return resultPromise;
        }

        /* ## PUBLIC METHODS */
        return {

            /** Provide access to the current user */
            currentUser: currentUser,

            /** Connection to the backend, recovery of the user properties.
             * @param {string} username
             * @param {string} password
             * @return {promise} request result
             */
            login: function login(username, password) {
                var deferred = $q.defer();
                restService.login(username, password).then(
                    function success(response) {
                        logger.debug('successful login');
                        // authenticated, use provided CSRF token for subsequent requests
                        getUserFromServer().then(
                            function success(response) {
                                deferred.resolve(response);
                            },
                            function error(response) {
                                deferred.reject(response);
                            }
                        );
                    },
                    function error(response) {
                        logger.error('login error');
                        deferred.reject(response);
                    }
                );
                return deferred.promise;
            },

            /** Disconnect the current user. */
            logout: function logout() {
                restService.logout();
                setCurrentUser(undefined);
                dialogService.showToast($filter('translate')('toast.user-disconnection'));
            },

            /** Return the current user.
             * @return {user} current user
             */
            getCurrentUser: function getCurrentUser() {
                return currentUser;
            },

            /** Return a provider for the current user (simple object which contains a reference to "currentUser",
             * useful to keep a binding between ui components and the currentUser variable, else when the currentUser is reset,
             * the ui components have to recall the getter to have the new instance. Use this provider allows to avoid this.) */
            getCurrentUserProvider: function getCurrentUserProvider() {
                return currentUserProvider;
            },

            /** Check is the current user has the specified authority.
             * @param {string} authority
             * @return {boolean} result: true if the user owns the specified authority
             */
            checkAuthority: function checkAuthority(authority) {
                if (currentUser == undefined || !currentUser.hasAuthority(authority)) {
                    logger.log('user not authorized, redirecting to the login page!');
                    $state.go('login', { authority: authority, next: $state.current.name, nextParams: $state.params });
                    return false;
                }
                return true;
            }
        }
    }

})
    ();

/** easelJsUtils : this service provides basic graphics shape to use with EaselJs */
(function () {
    'use strict';
    angular.module("tsa.services").factory("easelJsUtils", easelJsUtilsService);

	var Shape = createjs.Shape;
	var Container = createjs.Container;
	
	function easelJsUtilsService() {

	    return {
			createBackRudder: function createBackRudder(color) {
				var backRudderShape = new Shape();
				var rudderRadius = 100;
				var sqrt = rudderRadius/Math.sqrt(2);
				backRudderShape.graphics
					.beginStroke(color).setStrokeStyle(2,"square")
					.moveTo(0, 0).lineTo(sqrt, 0+sqrt)
					.arc(0, 0, rudderRadius, Math.PI/4, Math.PI/2 - 4*Math.PI/180)
					.endStroke()
					.beginStroke(color).setStrokeStyle(2,"square")
					.arc(0, 0, rudderRadius, Math.PI/2 + 4*Math.PI/180, 3*Math.PI/4)
					.moveTo(-sqrt, sqrt).lineTo(0, 0)
					.endStroke();

				return backRudderShape;
			},
	
			createRudder: function createRudder(color) {
				var rudderShape = new Shape();

				var rudderRadius = 100;
				var m = 7;
				var w = 7;
				var hw = w/2;
				
				rudderShape.graphics
					.beginStroke(color).setStrokeStyle(2,"square")
					.moveTo(hw, m)
					.lineTo(hw, rudderRadius-m)
					.lineTo(-hw, rudderRadius-m)
					.lineTo(-hw, m)
					.lineTo(hw, m)
					.endStroke();
				
				return rudderShape;
			},
			
			createBackGauge: function createBackGauge(backgroundColor) {
				var backGaugeShape = new Shape();
				var width = 50;
				var height = 10;
				
				backGaugeShape.graphics.beginFill(backgroundColor).drawRect(-50, -10, 100, 20).endFill();
				return backGaugeShape;
			},
			
			createGaugge: function createGaugge(tickColor, zeroColor, value, plusColor, minusColor) {
				var gaugeShape = new Shape();
				
				var color = value > 0 ? plusColor : minusColor;
				if(Math.abs(value)>0)
					gaugeShape.graphics.beginFill(color).drawRect( 0, -10, value/2, 20).endFill();

				gaugeShape.graphics
	    		.beginStroke(tickColor).setStrokeStyle(0.6)
	    		.moveTo(-25, -10)
	    		.lineTo(-25, 10)
	    		.endStroke();
	    	
				gaugeShape.graphics
	    		.beginStroke(tickColor).setStrokeStyle(0.6)
	    		.moveTo(25, -10)
	    		.lineTo(25, 10)
	    		.endStroke();
	    	
				gaugeShape.graphics
	    		.beginStroke(zeroColor).setStrokeStyle(0.6)
	    		.moveTo(0, -10)
	    		.lineTo(0, 10)
	    		.endStroke();
		    	
				return gaugeShape;
			},

			createVessel: function createVessel(color) {
				var vesselShape = new Shape();
				
				var vesselHeight=100;
				var vesselWidth=100;

				vesselShape.graphics.beginStroke(color).setStrokeStyle(1);

				var cp1 = 20;
				var cp2 = 20;

				var hw = vesselWidth/2;
				var hh = vesselHeight/2;
				var ch = 15*hh/50;
				var ccp1 = cp1*hw/50;
				var ccp2 = cp2*hh/50;

				vesselShape.graphics
				.moveTo(-hw, -ch)
				.lineTo(-hw, hh)
				.lineTo(hw, hh)
				.lineTo(hw, -ch)
				.bezierCurveTo(hw, -ch-ccp2, ccp1, -hh, 0, -hh)
				.bezierCurveTo(-ccp1, -hh, -hw, -ch-ccp2, -hw, -ch);

				return vesselShape;			
			},

			createArrow: function createArrow(color, value) {
				var arrowShape = new Shape();
				
				var arrowHeight=100;
				var arrowlWidth=100;

				arrowShape.graphics.beginStroke(color).setStrokeStyle(4);

				var arrowShift = 10;
				
				arrowShape.graphics
					.moveTo(0, 0)
					.lineTo(0, -value)
					.moveTo(arrowShift, -value+arrowShift)
					.lineTo(0, -value)
					.moveTo(-arrowShift, -value+arrowShift)
					.lineTo(0, -value);
				
				return arrowShape;
			}
			
    	};
	}
}());

/** formatterService: provides method to format values */
(function () {
    'use strict';
    angular.module("tsa.services").factory("formatterService", formatterService);

    function formatterService($filter) {

        function parseExcelDateToString(excelDate, format, tz){
            return $filter('date')(new Date((excelDate - (25569))*86400*1000), format, tz);
        }

        /* PUBLIC API */
        return {

            parseLatToString: function parseLatToString(lat) {
                if (angular.isUndefined(lat))
                    return "";
                var direction = lat > 0 ? 'N' : 'S';
                var degrees = sprintf("%02d", Math.abs(lat));
                var minutes = sprintf("%06.3f", Math.abs(lat) % 1 * 60);
                return degrees + "" + minutes + "" + direction;
            },

            parseLongToString: function parseLongToString(long) {
                if (angular.isUndefined(long))
                    return "";
                var direction = long > 0 ? 'E' : 'W';
                var degrees = sprintf("%03d", Math.abs(long));
                var minutes = sprintf("%06.3f", Math.abs(long) % 1 * 60);
                return degrees + "" + minutes + "" + direction;
            },

            parseTimeToString: function parseTimeToString(time){
                return $filter('date')(new Date(time), "dd/MM/yy HH:mm:ss", "+0");
            },

            parseExcelDateDefaultToString: function parseExcelDateDefaultToString(excelDate, tz){
                return parseExcelDateToString(excelDate, "dd/MM/yy HH:mm:ss", tz);
            },
            parseExcelDateNoSecToString: function parseExcelDateNoSecToString(excelDate, tz){
                return parseExcelDateToString(excelDate, "dd/MM/yy HH:mm", tz);
            },
            parseMarineDistance: function parseMarineDistance(dist){
                return (Math.abs(dist) < 1852) ? dist : dist/1852;
            },
            parseQualityFbxToString: function parseQualityFbxToString(quality){
                var res = "";
                quality = parseInt(quality);
                switch(quality) {
                    case 0: res = "operating";
                              break;
                    case 1: res = "washing";
                        break;
                    case 2: res = "waiting fresh water";
                        break;
                    case 3: res = "service";
                        break;
                    case 8: res = "cycle completed";
                        break;
                    case 9: res = "error";
                        break;
                    default : res = "undefined";
                }
                return res;
            }

        };
    }
})();

/** graphUtilsService : this service provides useful methods for the view containing a graph (chart.js) */
(function () {
    'use strict';
    angular.module("tsa.services").factory("graphUtils", graphUtilsService);

    function graphUtilsService(themeService) {

        /** Generate the chart options */
        function getChartOptions(stepSize, fill) {
            return {
                animation: false,
                maintainAspectRatio: false,
                legend: { display: false },
                tooltips: { enabled: false },
                elements: {
                    line: {
                        borderWidth: 2,
                        fill: fill,
                        tension: 0
                    },
                    point: { radius: 0 }
                },
                scales: {
                    xAxes: [{
                        type: "linear",
                        gridLines: {
                            zeroLineColor: "#9E9E9E",
                            color: themeService.getTheme().gridLinesColor
                        },
                        afterBuildTicks: function (scale) {
                            angular.forEach(scale.ticks, function (tick, index) {
                                if (index !== 0 && stepSize != undefined)
                                    scale.ticks[index] -= tick % stepSize;
                            })
                        },
                        ticks: {
                            maxRotation: 0,
                            fixedStepSize: stepSize,
                            callback: function (value, index, values) {
                                if (index === 0)
                                    return "";
                                var d = new Date(value);

                                if (values[values.length - 1] - values[0] > (25 * 60 * 60 * 1000))
                                    return  [d.getUTCHours() + "h" + d.getUTCMinutes(),  d.getUTCDate() + "/" + d.getUTCMonth() + "/" + d.getUTCFullYear()];

                                var label = d.getUTCHours() + "h";  //Hours
                                if (d.getUTCMinutes() < 10)         //Minutes
                                    label += 0;
                                label += d.getUTCMinutes();
                                if (d.getUTCSeconds() !== 0)        //Seconds
                                    label += ":" + d.getUTCSeconds();
                                return label;
                            }
                        }
                    }]
                }
            }
        }

        /* PUBLIC API */
        return {

            /** Graphics scales */
            scales: {
                "1 min": { scale: 60000, stepSize: 15000, bufferId: 'bufferOneMinute' },
                "5 min": { scale: 300000, stepSize: 60000, bufferId: 'bufferFiveMinutes' },
                "30 min": { scale: 1800000, stepSize: 300000, bufferId: 'bufferThirtyMinutes' },
                "1h": { scale: 3600000, stepSize: 600000, bufferId: 'bufferOneHour' },
                "3h": { scale: 10800000, stepSize: 1800000, bufferId: 'bufferThreeHours' },
                "6h": { scale: 21600000, stepSize: 3600000, bufferId: 'bufferSixHours' },
                "12h": { scale: 43200000, stepSize: 7200000, bufferId: 'bufferTwelveHours' },
                "24h": { scale: 86400000, stepSize: 14400000, bufferId: 'bufferOneDay' }
            },

            /** Generate the chart options
            * @param stepSize
            * @param fill
            */
            getChartOptions: getChartOptions


        };
    }
})();

/** localeService : provides methods to select the application'language 
/* To see: https://scotch.io/tutorials/internationalization-of-angularjs-applications */
(function () {
    'use strict';
    angular.module('tsa.services').service('localeService', LocaleService);

    function LocaleService($translate, LOCALES, $rootScope, tmhDynamicLocale) {

        // EVENTS TODO: Useful ? 
        // on successful applying translations by angular-translate
        $rootScope.$on('$translateChangeSuccess', function (event, data) {
            document.documentElement.setAttribute('lang', data.language);// sets "lang" attribute to html

            // asking angular-dynamic-locale to load and apply proper AngularJS $locale setting
            tmhDynamicLocale.set(data.language.toLowerCase().replace(/_/g, '-'));
        });

        return {
            /* Get the current locale (=language used) */
            getCurrentLocale: function () {
                return $translate.proposedLanguage() ? LOCALES.locales[$translate.proposedLanguage()] : LOCALES.locales[LOCALES.preferredLocale];
            },

            /* Get the locale list */
            getLocaleList: function () { return LOCALES.locales; },

            /* Set the current locale */
            setCurrentLocale: function (locale) { $translate.use(locale.id); },
        }
    }
})();

/**
 * ThemeService: angular service used to manage the theme
 */
(function () {
    'use strict';
    angular.module("tsa.services")
        .factory("themeService", function ($log, $rootScope) {
            /** PROPERTIES */
            var logger = $log.getInstance("themeService");
            var THEME = {
                DAY: {id: "day", icon:"wb_sunny", gridLinesColor: "#E0E0E0"},
                NIGHT: {id: "night", icon: "brightness_3", gridLinesColor: "#424242"}
            };
            var currentTheme = angular.copy(THEME.DAY);

            /** PUBLIC API */
            return {
                // Theme enum
                THEME: THEME,
                // Getter
                getTheme: function getTheme() {
                    return currentTheme;
                },
                // Setter
                setTheme: function setTheme(newTheme) {
                    currentTheme.id = newTheme.id;
                    currentTheme.icon = newTheme.icon;
                    currentTheme.gridLinesColor = newTheme.gridLinesColor;
                    logger.log("theme changed to "+ newTheme.id);
                    this.notify();
                },

                subscribe: function subscribe(scope, callback, callOnSubscribe) {
                	if (typeof callOnSubscribe === 'undefined') { callOnSubscribe = true; }

                    var handler = $rootScope.$on('changeTheme-themeService-event', callback);
                    if(callOnSubscribe) callback(null, currentTheme);
                    scope.$on('$destroy', handler);
                },

                notify: function notify() {
                    $rootScope.$emit('changeTheme-themeService-event', currentTheme);
                }

            }
        });
})();

(function () {
  'use strict';
  angular.module("tsa.services.filters",[]);

  /**
   * Filter used to sort device/data lists by alphabetic order.
   */
  angular
    .module('tsa.services.filters')
    .filter('orderObjectBy', function () {
      return function (items, field, reverse) {
        var filtered = [];
        angular.forEach(items, function (item) {
          filtered.push(item);
        });
        filtered.sort(function (a, b) {
          return (a[field] > b[field]) ? 1 : ((a[field] < b[field]) ? -1 : 0);
        });
        if (reverse) filtered.reverse();
        return filtered;
      };
    })
    .filter('marineDistance', function () {
      return function (distance) {
        var res = '';
        if(distance >= 1852) {
          res = sprintf('%.1f nm', distance/1852);
        } else {
          res = sprintf('%.1f m', distance);
        }
        return res;
      };
    })
    .filter('timeInHoursMins', function () {
      return function (time) {
        var timeInMinutes = Math.round(time/60000.0);
        var minutes = timeInMinutes%60;
        var hours = Math.round((timeInMinutes - minutes)/60);
        return sprintf('%02d:%02d', hours, minutes);
      };
    });
})();

