(function () {
    'use strict';

    /** Propulsion component **/
    angular.module('tsa.tsaViews.atpropulsion', []).component('tsaAtPropulsion', {
        templateUrl: "app/tsa-views/atpropulsion/atpropulsion.html",
        controllerAs: "ctrl",
        controller: atPropulsionController,
        bindings: {
            config: '=',
            cardId: '@',
            onChangeCardType: '&'
        }
    });

    /** Propulsion controller **/
    function atPropulsionController(acquisitionService, $timeout, $scope, easelJsUtils, themeService) {
        var vm = this;
        var TAG = '[atPropulsionController] ';

        /**------------------------------ PROPERTIES -------------------------------**/

        //Canvas
        vm.canvasId = "canvas-atpropulsion-" + vm.cardId;

        var canvas;
        var stage;
        var backContainer;
        var frontContainer;

        var canvasWidth;
        var canvasHeight;

        vm.readyToDraw = false;

        vm.colors = {
        	background: '#CCCCCC',
        	zero: '#EE2222',
        	tick: '#888888',
        	rudder: '#000000',
        	green: '#11CC11',
        	red: '#EE2222',
        	forward: '#1111DD',
        	backward: '#000000',
        	funkyBackground: '#F3BF52'
        };

        vm.themeChanged = function themeChanded(event, theme) {
        	console.log("propulsion themechanged");
        };

        themeService.subscribe($scope, vm.themeChanged);

        /**------------------------------ METHODS -------------------------------**/

        /** Initialization function **/
        vm.$onInit = function init() {

            console.log(TAG + '$onInit: deviceId = ' + (this.config ? this.config.deviceId : 'config undefined'));

            if (this.config == undefined || this.config.deviceId == undefined) {
                console.error(TAG + 'device Id not found!');
                return;
            }

            vm.deviceId = this.config.deviceId; //Component parameter (mandatory)
            vm.device = acquisitionService.registerForDevice(vm.deviceId);

            vm.frameId = 'atpropulsion';

            //Create object to add x and y properties
            vm.frontTrans = { id: 'fronttrans' };
            vm.frontPropAngle= { id: 'frontpropangle' };
            vm.mainLeft = { id: 'mainleft' };
            vm.mainRight = { id: 'mainright' };
            vm.angle = {id: 'angle' };
            vm.fontsize = 1;

            vm.frameListener = acquisitionService.registerForDevice(vm.deviceId).registerForFrame(vm.frameId);
            vm.frameListener.promise.then({}, {}, function (frame) {
            	vm.frontTrans.value = frame.value[vm.frontTrans.id];
            	vm.frontPropAngle.value = frame.value[vm.frontPropAngle.id];
            	vm.mainLeft.value = frame.value[vm.mainLeft.id];
                vm.mainRight.value = frame.value[vm.mainRight.id];
            	vm.angle.value = frame.value[vm.angle.id];

            	if(vm.readyToDraw) {
                    updateFrontContainer();
            		stage.update();
            	}
            });

            $timeout(function () {
                initCanvas();
                vm.readyToDraw = true;
            }, 500);

        };

        /** Destroy function: remove the data listeners */
        vm.$onDestroy = function destroy() {
            if (angular.isDefined(vm.frameListener))
                acquisitionService.registerForDevice(vm.deviceId).unregisterForFrame(vm.frameListener);
        };

        /**Initialize the canvas **/
        function initCanvas() {
            canvas = document.getElementById(vm.canvasId);
            if (canvas != null) {
                stage = new createjs.Stage(canvas);

                backContainer = new createjs.Container();
                frontContainer = new createjs.Container();

                stage.addChild(backContainer, frontContainer);

                $scope.$watch(function () {
                    return canvas.offsetHeight
                }, function () {
                    updateCanvasSize();
                });
                $scope.$watch(function () {
                    return canvas.offsetWidth
                }, function () {
                    updateCanvasSize();
                });
                updateCanvasSize();
            }
        }

        /** Create the stage if needed, reset the canvas and redraw all the shapes **/
        function updateCanvasSize() {
            if (canvas != null) {
                canvasWidth = canvas.offsetWidth;
                canvasHeight = canvas.offsetHeight;

                //Canvas size properties setting
                canvas.setAttribute('width', canvasWidth);
                canvas.setAttribute('height', canvasHeight);

                angular.element(canvas).parent().find("tsa-data-view .value").css({
                	"font-size": ""+canvasHeight/200+"em"
        		});

                angular.element(canvas).parent().find("tsa-data-view").css({
                	"width": ""+Math.round(canvasHeight/3)+"px !important",
                	"max-width": ""+Math.round(canvasHeight/3)+"px",
                	"background-color": "green"
                });

                stage.x = canvasWidth/2;
                stage.y = canvasHeight/2;
            	var scale = canvasHeight/100;
            	stage.scaleX = stage.scaleY = scale;

                updateBackContainer();
                backContainer.cache(-canvasWidth/2, -canvasHeight/2, canvasWidth, canvasHeight, scale);
                updateFrontContainer();
                updateLabels();

                stage.update();
            }
        }


        function updateBackContainer() {
        	backContainer.removeAllChildren();

            var vesselShape = easelJsUtils.createVessel(vm.colors.tick);
            vesselShape.scaleX = 0.6;
            vesselShape.scaleY = 0.95;

            var backRudderShape =  easelJsUtils.createBackRudder(vm.colors.background);
            backRudderShape.y = 25;
            backRudderShape.scaleX = backRudderShape.scaleY = 18/100;

            var rearPropBackGaugeShape = easelJsUtils.createBackGauge(vm.colors.background);
            rearPropBackGaugeShape.y = 18;
            rearPropBackGaugeShape.scaleX = 0.4;
            rearPropBackGaugeShape.scaleY = 0.3;

			var frontPropBackGaugeShape = easelJsUtils.createBackGauge(vm.colors.background);
			frontPropBackGaugeShape.y = -38;
			frontPropBackGaugeShape.scaleX = 0.4;
			frontPropBackGaugeShape.scaleY = 0.3;

			var mainBackGaugeShape = easelJsUtils.createBackGauge(vm.colors.background);
			mainBackGaugeShape.y = -10;
			mainBackGaugeShape.scaleX = 0.4;
			mainBackGaugeShape.scaleY = 0.3;
			mainBackGaugeShape.rotation = 270;

            backContainer.addChild(vesselShape, backRudderShape, rearPropBackGaugeShape, frontPropBackGaugeShape, mainBackGaugeShape);
        }

        function updateFrontContainer() {
        	frontContainer.removeAllChildren();

            var rudderColor = vm.colors.rudder;
			if(Math.abs(Math.round(vm.angle.value))>0) {
				rudderColor = vm.angle.value > 0 ? vm.colors.red : vm.colors.green;
			}

            var rudderShape = easelJsUtils.createRudder(rudderColor);
            rudderShape.y = 25;
            rudderShape.scaleX = rudderShape.scaleY = 18/100;
            rudderShape.rotation = vm.angle.value;


            var rearPropGaugeShape = easelJsUtils.createGaugge(vm.colors.tick, vm.colors.zero,
            		vm.frontPropAngle.value, vm.colors.green, vm.colors.red);
            rearPropGaugeShape.y = 18;
            rearPropGaugeShape.scaleX = 0.4;
            rearPropGaugeShape.scaleY = 0.3;


            var frontPropGaugeShape = easelJsUtils.createGaugge(vm.colors.tick, vm.colors.zero,
            		vm.frontTrans.value, vm.colors.green, vm.colors.red);
            frontPropGaugeShape.y = -38;
            frontPropGaugeShape.scaleX = 0.4;
            frontPropGaugeShape.scaleY = 0.3;

            var mainLeftGaugeShape = easelJsUtils.createGaugge(vm.colors.tick, vm.colors.zero,
            		vm.mainLeft.value*100/150, vm.colors.forward, vm.colors.backward);
            mainLeftGaugeShape.y = -10;
            mainLeftGaugeShape.scaleX = 0.4;
            mainLeftGaugeShape.scaleY = 0.3;
            mainLeftGaugeShape.rotation = 270;

            frontContainer.addChild(rudderShape, rearPropGaugeShape, frontPropGaugeShape, mainLeftGaugeShape);
        }


        function updateLabels() {
            canvasWidth = canvas.offsetWidth;
            canvasHeight = canvas.offsetHeight;

            var delta = canvasHeight*5/100;

            vm.frontTrans.x = canvasWidth/2 + canvasHeight*25/100;
            vm.frontTrans.y = canvasHeight*(50 - 38)/100 - delta;

/*
            vm.rearTrans.x = vm.frontTrans.x;
            vm.rearTrans.y = canvasHeight*(50 + 18)/100 - delta;
*/

            vm.mainLeft.x = vm.frontTrans.x;
            vm.mainLeft.y = canvasHeight*(50 - 10)/100 - delta;

            vm.angle.x = vm.frontTrans.x;
            vm.angle.y = canvasHeight*(50 + 32)/100 - delta;
        }
    }
})();

(function () {
    'use strict';

    /** Component declaration **/
    angular.module('tsa.tsaViews.attitude', []).component('tsaAttitude', {
        templateUrl: "app/tsa-views/attitude/attitude.html",
        controllerAs: "ctrl",
        controller: attitudeController,
        bindings: {
            config: '=',
            cardId: '@',
            onChangeCardType: '&'
        }
    });

    /** Component controller */
    function attitudeController($log, ATTITUDE, acquisitionService) {

        /**------------------------------ PROPERTIES -----------------------------**/
        var vm = this;
        var frameListener;
        vm.logger = $log.getInstance("Attitude-tsa-view");

        /**------------------------------- METHODS -------------------------------**/

        /** Initialization function **/
        vm.$onInit = function init() {
            if (angular.isUndefined(this.config) || angular.isUndefined(this.config.deviceId) || angular.isUndefined(this.config.frameId)) {
                vm.logger.error('config and/or deviceId and/or frameId not defined!');
                return;
            }

            vm.deviceId = this.config.deviceId;
            vm.frameId = this.config.frameId;

            vm.heading = ATTITUDE.HEADING;
            vm.roll = ATTITUDE.ROLL;
            vm.pitch = ATTITUDE.PITCH;
            vm.heave = ATTITUDE.HEAVE;

            frameListener = acquisitionService.registerForDevice(vm.deviceId).registerForFrame(vm.frameId);
            frameListener.promise.then({}, {}, function (receivedFrame) {
                parseAttitudeFrame(receivedFrame.value);
            });
        };

        /** Destroy function **/
        vm.$onDestroy = function onDestroy() {
            if (angular.isDefined(frameListener)) {
                acquisitionService.registerForDevice(vm.deviceId).unregisterForFrame(frameListener);
            }
        };


        /** Called when a frame has been received **/
        function parseAttitudeFrame(frame) {
            var roll = frame[ATTITUDE.ROLL];
            var pitch = frame[ATTITUDE.PITCH];

            if (angular.isUndefined(vm.list) || vm.list < -90) {
                vm.list = roll;
                vm.trim = pitch;
            }
            else {
                vm.list = 0.999167 * vm.list + 0.000833 * roll;
                vm.trim = 0.999167 * vm.trim + 0.000833 * pitch;
            }
        }
    }
})();

(function () {
    'use strict';

    /** Casino component declaration **/
    angular.module('tsa.tsaViews.casino', []).component('tsaCasino', {
        templateUrl: "app/tsa-views/casino/casino.html",
        controllerAs: "ctrl",
        controller: casinoController,
        bindings: {
            config: '=',
            cardId: '@',
            onChangeCardType: '&'
        }
    });

    /** Casino controller */
    function casinoController($log, $interval, $rootScope, $scope, casinoService) {
        var vm = this;

        /**-------------------- PROPERTIES ----------------**/
        var intervalPromise;

        vm.logger = $log.getInstance("casino-tsa-view");
        vm.casinoLoading = false;
        vm.casinoErrorConnect;
        vm.casinoErrorLoad;

        vm.selectedCruise;
        vm.eventsListToDisplay = [];
        vm.period = "24";

        /**-------------------- PUBLIC METHODS ----------------**/
        vm.casinoCanConnect = function () {
            return !vm.config.connectionParams ||
                !vm.config.connectionParams.url.trim() ||
                !vm.config.connectionParams.login.trim() ||
                !vm.config.connectionParams.password.trim() ||
                vm.casinoLoading;
        };

        vm.casinoConnection = function () {
            vm.casinoLoading = true;
            casinoService.connect(vm.config.connectionParams.url, vm.config.connectionParams.login, vm.config.connectionParams.password)
                .then(function () {
                    vm.casinoErrorConnect = false;
                    vm.refreshCruise(true);
                })
        };

        vm.refreshCruise = function(sendEvent) {
            vm.selectedCruise = undefined;
            casinoService.getCurrentCruise()
                .then(function(currentCruise) {
                    vm.casinoLoading = false;
                    if (currentCruise && currentCruise.length === 1) {
                        vm.casinoErrorLoad = false;
                        vm.selectedCruise = currentCruise[0];
                        vm.updateEventsTable();
                    } else {
                        vm.casinoErrorLoad = true;
                    }
                })
                .catch(function () {
                    vm.casinoErrorConnect = true;
                    vm.casinoLoading = false;
                });
        };

        vm.updateEventsTable = function() {
            var isDateParam = "false";
            var limitParam = vm.config.limit;
            if (parseInt(vm.period) > 0) {
                isDateParam = "true";
                limitParam = vm.period;
            }
            casinoService.getCruiseEvents(vm.selectedCruise.FullName, limitParam, isDateParam)
                .then(function(data) {
                    vm.eventsListToDisplay = data;
                })
        };

        /**
         * Init function
         */
        vm.$onInit = function init() {
            // Database connection
            vm.casinoConnection();

            // refresh eventsListToDisplay
            intervalPromise = $interval(function () {
                if (angular.isDefined(vm.selectedCruise)) {
                    vm.updateEventsTable();
                }
            }, 8000);
        };

        /**
         * Destroy function
         */
        vm.$onDestroy = function onDestroy() {
            if (angular.isDefined(intervalPromise))
                $interval.cancel(intervalPromise);
        };
    }
})();

(function () {
    'use strict';

    /** Casino component declaration **/
    angular.module('tsa.tsaViews.casinoAddEvent', []).component('tsaCasinoAddEvent', {
        templateUrl: "app/tsa-views/casino/casinoAddEvent.html",
        controllerAs: "ctrl",
        controller: casinoAddEventController,
        bindings: {
            config: '=',
            cardId: '@',
            onChangeCardType: '&'
        }
    });

    /** Casino controller */
    function casinoAddEventController($log, $rootScope, $scope, $filter, dialogService, casinoService, CasinoEvent, CasinoOperationId) {
        var vm = this;

        /**-------------------- PROPERTIES ----------------**/
        vm.logger = $log.getInstance("casino-tsa-view");
        vm.casinoLoading = false;
        vm.casinoErrorConnect;
        vm.casinoErrorLoad;
        vm.error;

        vm.isFollow = false;
        vm.validExitTitle = undefined;
        vm.validExitMsg = undefined;

        vm.selectedCruise;
        vm.casinoEvent = new CasinoEvent();
        vm.operationId = new CasinoOperationId();
        vm.deviceId;
        vm.listCasinoDevices = undefined;
        vm.listDeviceActions = undefined;

        vm.eventTypeEnum = {
            PHA: "PHA",
            OPE: "OPE",
            OBS: "OBS"
        };

        vm.phaseTypeEnum = {
            PROFIL: "PROFIL",
            STATION: "STATION",
            TRANSIT: "TRANSIT",
            GIRATION: "GIRATION",
            ESCALE: "ESCALE"
        };

        /**-------------------- PUBLIC METHODS ----------------**/
        vm.casinoCanConnect = function () {
            return !vm.config.connectionParams ||
                !vm.config.connectionParams.url.trim() ||
                !vm.config.connectionParams.login.trim() ||
                !vm.config.connectionParams.password.trim() ||
                vm.casinoLoading;
        };

        vm.casinoConnection = function () {
            vm.casinoLoading = true;
            casinoService.connect(vm.config.connectionParams.url, vm.config.connectionParams.login, vm.config.connectionParams.password)
                .then(function () {
                    vm.refreshCruise(true);
                })
        };

        vm.initErrors = function(){
            vm.casinoErrorConnect = false;
            vm.casinoErrorLoad = false;
            vm.error = undefined;
        };

        vm.onResetClick = function() {
            if (vm.isFollow) {
                dialogService.showValidation(
                    vm.validExitTitle,
                    vm.validExitMsg)
                    .then(
                        function accept() {
                            vm.onResetForm();
                        },
                        function decline() {
                            // do nothing
                        }
                    );
            } else {
                vm.onResetForm();
            }
        };

        vm.onResetForm = function() {
            vm.isFollow = false;
            vm.updateValidExit();
            vm.initErrors();
            vm.listDeviceActions = undefined;
            vm.casinoEvent.init();
            vm.operationId.init();
            vm.updatePrefixOperation();
        };

        vm.onSubmit = function() {
            vm.initErrors();
            if (vm.selectedCruise) {
                // Clean casinoEvent object
                vm.casinoEvent.operationId = vm.operationId.getStringOperationId();
                vm.casinoEvent.cleanByType();

                var date = new Date();
                vm.casinoEvent.eventDate = date.toISOString();

                // create New CasinoEvent
                casinoService.postCruiseEvent(vm.selectedCruise.FullName, vm.casinoEvent)
                    .then(function(data) {
                        var followIdx = vm.checkFollow();
                        if (followIdx > -1) {
                            vm.updateAction(vm.listDeviceActions[followIdx]);
                            vm.isFollow = true;
                            vm.updateValidExit();
                        } else {
                            // clear form
                            vm.onResetForm();
                        }
                    })
                    .catch(function (error){
                        if (error.status === 400) {
                            vm.error = error.data;
                        } else {
                            vm.casinoErrorConnect = true;
                        }
                    });
            }
        };

        vm.checkFollow = function() {
            var followIdx = -1;
            if (vm.casinoEvent.type === 'OPE' && vm.listDeviceActions) {
                var idx = vm.getActionIdx(vm.casinoEvent.actionCode);
                if ((idx > -1) && (idx < vm.listDeviceActions.length -1)) {
                    // not the last
                    followIdx = idx+1;
                }
            }
            return followIdx;
        };

        vm.getActionIdx = function(actionCode) {
            if (actionCode && vm.listDeviceActions) {
                for (var i=0; i<vm.listDeviceActions.length; i++) {
                    if(vm.listDeviceActions[i].code === actionCode) {
                        return i;
                    }
                }
            }
            return -1;
        };

        vm.refreshCruise = function(sendEvent) {
            // init form
            vm.onResetForm();
            casinoService.getCurrentCruise()
                .then(function(currentCruise) {
                    vm.casinoLoading = false;
                    if (currentCruise && currentCruise.length === 1) {
                        vm.selectedCruise = currentCruise[0];
                        vm.updateCruise();
                    } else {
                        vm.casinoErrorLoad = true;
                    }
                })
                .catch(function () {
                    vm.casinoErrorConnect = true;
                    vm.casinoLoading = false;
                });
        };

        vm.updateCruise = function() {
            vm.updateListCasinoDevices();
            vm.updatePrefixOperation();
        };

        vm.updatePrefixOperation = function() {
            vm.operationId.prefix = '';
            if (vm.selectedCruise) {
                vm.operationId.updatePrefix(vm.selectedCruise.OperationPrefix, vm.casinoEvent.deviceCode);
                vm.updateNumberOperation();
            }
        };

        vm.updateNumberOperation = function() {
            vm.operationId.updateNumber(undefined);
            if (vm.selectedCruise && vm.operationId.prefix != '' && vm.casinoEvent.deviceCode != ""){
                casinoService.getLastOperationId(vm.selectedCruise.FullName, vm.operationId.prefix)
                    .then(function(data) {
                        if (data && data.length === 1) {
                            vm.operationId.updateNumber(data[0].OperationId, vm.operationId.prefix);
                        }
                    })
            }
        };

        vm.updateListCasinoDevices = function() {
            vm.listCasinoDevices = undefined;
            vm.listDeviceActions = undefined;
            vm.casinoEvent.initDevice();
            if (vm.selectedCruise) {
                casinoService.getCruiseDevices(vm.selectedCruise.FullName)
                    .then(function(data) {
                        vm.listCasinoDevices = data;
                    })
            }
        };

        vm.updateDevice = function(item) {
            vm.casinoEvent.initDevice();
            vm.deviceId = undefined;
            if (item) {
                vm.deviceId = item.id;
                vm.casinoEvent.deviceCode = item.code;
                vm.casinoEvent.deviceFullName = item.name;
            }
            vm.updateListDeviceActions();
            vm.updatePrefixOperation();
        };

        vm.updateListDeviceActions = function() {
            vm.listDeviceActions = undefined;
            if (vm.selectedCruise && vm.deviceId) {
                casinoService.getDeviceActions(vm.selectedCruise.FullName, vm.deviceId)
                    .then(function(data) {
                        vm.listDeviceActions = data;
                    })
            }
        };

        vm.updateAction = function(item) {
            if (item) {
                vm.casinoEvent.initAction();
                vm.casinoEvent.actionCode = item.code;
                vm.casinoEvent.actionFullName = item.name;
            }
        };

        vm.updateValidExit = function() {
            vm.validExitTitle = undefined;
            vm.validExitMsg = undefined;
            if (vm.isFollow) {
                vm.validExitTitle = $filter('translate')("views.casino.add-event.follow-exit.title");
                vm.validExitMsg = $filter('translate')("views.casino.add-event.follow-exit.message");
            }
        };

        /**
         * Init function
         */
        vm.$onInit = function init() {
            // Database connection
            vm.casinoConnection();
        };

        /**
         * Destroy function
         */
        vm.$onDestroy = function onDestroy() {
            // nothing to do
        };
    }
})();

(function () {
    'use strict';
    angular.module('tsa.tsaViews.debug', []).component('tsaDebug', {
        templateUrl: "app/tsa-views/debug/debug.html",
        controllerAs: "ctrl",
        controller: debugController,
        bindings: {
            onChangeCardType: '&',
            cardId: '@'
        }
    });

    function debugController(acquisitionService, stompService, acquisitionBufferService, dialogService, restService) {
        var vm = this;

        /**-------------------- PROPERTIES ----------------**/
        vm.view = 'Devices';
        //Devices
        vm.deviceList = {};
        //Topics
        vm.topicList = {};
        vm.stompConsole = "";
        //Topic
        vm.topiConsole = '';
        vm.topic = undefined;
        var currentTopicListener = undefined;
        vm.currentTopic = undefined;
        //Buffers
        vm.bufferDeviceList = {};
        //Config
        vm.configId = 'default';
        //Requests
        vm.requestStack = undefined;
        vm.restService = restService;

        /**-------------------- PUBLIC METHODS ----------------**/

        vm.$onInit = function init() {
            vm.topicList = stompService.getTopicList();
            vm.deviceList = acquisitionService.getDeviceList();
            vm.stompService = stompService;
            vm.bufferDeviceList = acquisitionBufferService.getDataList();
            vm.requestStack = restService.getRequestStack();
        };


        /**
         * Destroy function, called when the view is destroyed: closes the used resources.
         */
        vm.$onDestroy = function onDestroy() {
            vm.disconnectTopic();
        };


        vm.startStompService = function startStompService() {
            stompService.start().then(
                function () {
                    dialogService.showToast("STOMP connection successful!");
                },
                function (error) {
                    dialogService.showToast("STOMP connection error: " + error);
                }
            );
        };

        vm.stopStompService = function stopStompService() {
            stompService.stop();
        };


        vm.connectToTopic = function connectToTopic() {
            if (vm.topic !== undefined) {
                vm.currentTopic = '/topic/' + vm.topic;
                currentTopicListener = stompService.registerForTopic(vm.currentTopic);
                vm.topicConsole = "";
                currentTopicListener.promise.then({}, {}, function (msg) {
                    vm.topicConsole = JSON.stringify(msg.data.headers) + "\n" + msg.data.body + "\n" + vm.topicConsole;
                    if (vm.topicConsole.length > 50000)
                        vm.topicConsole = vm.topicConsole.substring(vm.topicConsole.length / 3);
                });
            }
        };

        vm.disconnectTopic = function disconnectTopic() {
            if (currentTopicListener !== undefined && vm.currentTopic !== undefined) {
                stompService.unregisterForTopic(vm.currentTopic, currentTopicListener);
                vm.currentTopic = undefined;
                currentTopicListener = undefined;
            }
        };

        vm.clearTopicConsole = function clearTopicConsole() {
            vm.topicConsole = "";
        };

        vm.cancelRequest = function cancelRequest(request){
            restService.cancelRequest(request.id);

        };
    }
})();

(function () {
    'use strict';

    /** Depth component declaration **/
    (function () {
        'use strict';
        angular.module('tsa.tsaViews.depth', []).component('tsaDepth', {
            templateUrl: "app/tsa-views/depth/depth.html",
            controllerAs: "ctrl",
            controller: depthController,
            bindings: {
                config: '=',
                cardId: '@',
                onChangeCardType: '&'
            }
        });
    })();

    /** Depth controller */
    function depthController($timeout, $interval, $log, $scope, acquisitionService, NAVIGATION, statusService, themeService, graphUtils) {
        var vm = this;

        /**------------------------------ PROPERTIES -------------------------------**/

        vm.logger = $log.getInstance("depth-tsa-view");
        vm.canvasId = "canvas-depth-" + vm.cardId;
        var intervalPromise;
        var timeoutPromise;

        /**  Time scales **/
        vm.scales = graphUtils.scales;
        vm.selectedScale = '1 min';
        vm.bufferLoading = {};

        /** Chart properties **/
        var chart = undefined;
        var chartData = {
            datasets: [{
                label: "Data 1",
                xAxisID: "x-axis-1",
                yAxisID: "y-axis-1",
                borderColor: 'rgb(26, 117, 174)',
                backgroundColor: 'rgba(26, 117, 174, 0.3)',
                data: []
            }]
        };

        /**-------------------------- INITIALIZATION / DESTROY METHODS -------------------------**/

        /** Initialization function **/
        vm.$onInit = function init() {
            if (angular.isUndefined(this.config) || angular.isUndefined(this.config.deviceId) || angular.isUndefined(this.config.frameId)) {
                vm.logger.error('config and/or deviceId and/or frameId not defined!');
                return;
            }

            vm.deviceId = this.config.deviceId;
            vm.frameId = this.config.frameId;

            vm.dataId = NAVIGATION.SOUNDER;

            vm.device = acquisitionService.registerForDevice(vm.deviceId);
            vm.device.recordData(vm.frameId, vm.dataId);

            if (angular.isDefined(vm.config.scale))
                vm.selectedScale = vm.config.scale;

            //Wait the canvas creation, and then create the chart
            timeoutPromise = $timeout(initTimer, 200);

            // Change gridLinesColor of Graph on every change of service variable
            themeService.subscribe($scope, function(event, theme) {
        		vm.theme = theme;
        		reloadChart();
        	});

        };

        /** Destroy function **/
        vm.$onDestroy = function () {
            vm.device.stopRecordData(vm.frameId, vm.dataId);
            $timeout.cancel(timeoutPromise);
            if (intervalPromise !== undefined)
                $interval.cancel(intervalPromise);
        };

        /**------------------------------ PRIVATE METHODS -------------------------------**/

        /** Generate the chart options */
        function getChartOptions() {
            var chartOptions = graphUtils.getChartOptions(vm.scales[vm.selectedScale].stepSize, true);
            chartOptions.scales.yAxes = [{
                id: "y-axis-1",
                gridLines: { color: vm.theme.gridLinesColor },
                ticks: { beginAtZero: true, suggestedMin: -10 }
            }];
            return chartOptions;
        };

        /** Redraw the chart **/
        function reloadChart() {
            if (chart !== undefined)
                chart.destroy();
            var canvas = document.getElementById(vm.canvasId);
            if (canvas) {
                chart = Chart.Scatter(canvas, { data: chartData, options: getChartOptions() });
                updateGraph();
            }
            vm.config.scale = vm.selectedScale;
        }

        /** Start or update the timer (intervalPromise) */
        var initTimer = function initTimer() {
            if (intervalPromise !== undefined)
                $interval.cancel(intervalPromise);

            //Load archived data
            if (angular.isDefined(vm.device) && angular.isDefined(vm.device.getData(vm.frameId, vm.dataId))) {
                var bufferId = vm.scales[vm.selectedScale].bufferId;
                vm.bufferLoading[vm.selectedScale] = vm.bufferLoading[vm.selectedScale] ? ++vm.bufferLoading[vm.selectedScale] : 1;
                var selectedScale = vm.selectedScale;
                var requestCallback = function () {
                    vm.bufferLoading[selectedScale]--;
                    if (chart !== undefined)
                        updateGraph();
                };
                vm.device.getData(vm.frameId, vm.dataId).buffers[bufferId].loadArchivedFrames().then(requestCallback, requestCallback);
            }
            reloadChart();
            intervalPromise = $interval(updateGraph, vm.scales[vm.selectedScale].scale / 1000);
        };

        /** Update the graph with the recorded data, called by the timer (intervalPromise) */
        function updateGraph() {
            //Step 1: update X axis
            var currentDate = statusService.getServerTime().time;
            chart.options.scales.xAxes[0].ticks.min = (currentDate - vm.scales[vm.selectedScale].scale);
            chart.options.scales.xAxes[0].ticks.max = currentDate;

            //Step 2: update data
            var bufferId = vm.scales[vm.selectedScale].bufferId;
            chartData.datasets[0].data = [];
            var recordedData = vm.device.getData(vm.frameId, vm.dataId);
            if (recordedData !== undefined)
                angular.forEach(recordedData.buffers[bufferId].frames, function (frame) {
                    chartData.datasets[0].data.push({ x: frame.timestamp, y: -frame.value });
                });

            chart.update();
        }

        /** Called when a new X scale is selected */
        vm.onChangeXScale = initTimer;

    }
})();

(function () {
    'use strict';

    /** Component declaration **/
    angular.module('tsa.tsaViews.devices', []).component('tsaDevices', {
        templateUrl: "app/tsa-views/devices/devices.html",
        controllerAs: "ctrl",
        controller: devicesController,
        bindings: {
            config: '=',
            cardId: '@',
            onChangeCardType: '&'
        }
    });

    /** Component controller */
    function devicesController($log, $interval, acquisitionService, statusService) {
        var vm = this;

        /**------------------------------ PROPERTIES -----------------------------**/
        var logger = $log.getInstance("Device-tsa-view");

        vm.itemList = [];
        var deviceListListener;
        var intervalPromise;


        /**------------------------------ Item class -------------------------------**/
        /*** Item: represents a device, its files stats and its frame number.*/
        function Item(device) {
            var self = this;
            this.device = device;
            this.lastJSONMessageTime = undefined;
            this.isReceivingJSON = false;

            //Subscription to JSON data messages
            this.JSONDataListener = this.device.subscribeToJSONData();
            if (this.JSONDataListener !== undefined) {
                this.JSONDataListener.promise.then({}, {}, function (msg) {
                    self.lastJSONMessageTime = JSON.parse(msg.data.body).acquisitionTime;
                });
            }
            else {
                console.error(TAG + 'error with the subscription to the JSON topic for the device: ' + device.name);
            }
        }


        /**---------------------- INITIALIZATION / DESTROY METHODS -------------------**/

        /** Initialization function **/
        vm.$onInit = function init() {
            logger.debug('init function');

            //Watch device list
            refreshItemList(acquisitionService.getDeviceList());
            deviceListListener = acquisitionService.registerForDeviceList();
            deviceListListener.promise.then({}, {}, refreshItemList);

            intervalPromise = $interval(watchdog, 1000);
        };

        /** Destroy function **/
        vm.$onDestroy = function () {
            logger.debug('destroy function');
            angular.forEach(vm.itemList, function (item) {
                item.device.unsubscribeToJSONData(item.JSONDataListener);
            });
            acquisitionService.unregisterForDeviceList(deviceListListener);
            if (intervalPromise !== undefined)
                $interval.cancel(intervalPromise);
        };

        /**--------------------------------- METHODS ----------------------------------**/

        /*** Check the good reception of JSON messages **/
        function watchdog() {
            angular.forEach(vm.itemList, function (item) {
                var currentDate = statusService.getServerTime().time;

                // Get max period
                var period = 0;
                angular.forEach(item.device.periodList, function (p) {
                    period = (period<p) ? p : period;
                });

                // JSON message reception checking
                if (item.lastJSONMessageTime !== undefined)
                    item.isReceivingJSON = (currentDate - item.lastJSONMessageTime) <= (5 * period);
                else item.isReceivingJSON = false;
            });
        }


        /** Refresh the item list with a specified device list **/
        function refreshItemList(deviceList) {
            //Step 1: remove item which are obsolete
            var itemToDelete = [];
            angular.forEach(vm.itemList, function (item) {
                var itemDeviceExists = false;
                angular.forEach(deviceList, function (device) {
                    if (device.id === item.device.id && device.isEnabled)
                        itemDeviceExists = true; //the device exists and is enabled
                });
                if (!itemDeviceExists) {
                    itemToDelete.push(item);
                    itemToDelete.push(item);
                    itemToDelete.push(item);
                    itemToDelete.push(item);
                    itemToDelete.push(item);
                }
            });
            angular.forEach(itemToDelete, function (item) {
                item.device.unsubscribeToJSONData(item.JSONDataListener);
                vm.itemList.splice(vm.itemList.indexOf(item), 1);
            });


            //Step 2: add a new item for each new device found.
            angular.forEach(deviceList, function (device) {
                var itemAlreadyExists = false;
                angular.forEach(vm.itemList, function (item) {
                    if (device.id === item.device.id)
                        itemAlreadyExists = true;
                });
                if (!itemAlreadyExists && device.isEnabled) //If the device is not already in the list and is enabled
                    vm.itemList.push(new Item(device)); // ---> add the device in the list
            })
        }

    }
})();

(function () {
    'use strict';

    /** Component declaration **/
    angular.module('tsa.tsaViews.driverPreview', []).component('tsaDriverPreview', {
        templateUrl: "app/tsa-views/driverPreview/driverPreview.html",
        controllerAs: "ctrl",
        controller: driverPreviewController,
        bindings: {
            config: '=',
            cardId: '@',
            onChangeCardType: '&'
        }
    });

    /** Component controller */
    function driverPreviewController($log, restService) {
        var vm = this;

        /**------------------------------ PROPERTIES -----------------------------**/
        var logger = $log.getInstance("driverPreview-tsa-view");
        vm.text = "This is a driverPreview...";
        vm.title = "Driver Preview";
        /** Driver list **/
        vm.driverList = [];
        restService.getConverters().then(function (result) {
            vm.driverList = result;
        });
        vm.driver = {
            configuration: {
                id: "",
                header: "",
                driverClassName: "",
                fields: {},
                parameters: {
                    regex: "",
                    separator: ","
                }
            },
            message: ""
        };
        /** Fields **/
        function Field(name, number) {
            this.name = name;
            this.number = number;
        }
        vm.fieldList = [];

        /**------------------------------- METHODS -------------------------------**/

        /** Private function **/
        function privateFct() {
        }

        vm.addField = function addField() {
            vm.fieldList.push(new Field());
        };

        vm.result = function result() {
            angular.forEach(vm.fieldList, function (field) {
                vm.driver.configuration.fields[field.name] = field.number;
            });

            restService.postConvertersPreview(vm.driver).then(function (result) {
                angular.forEach(result.data, function(value, key) {
                    angular.forEach(vm.fieldList, function(field) {
                        if (field.name === key) {
                            field.result = value;
                        }
                    })
                })
            });
        };

        vm.removeField = function removeField(field) {
            var index = vm.fieldList.indexOf(field);
            if (index != -1)
                vm.fieldList.splice(index, 1);
        };

        /** Initialization function **/
        vm.$onInit = function init() {
            logger.debug('init function');
        };

        /** Destroy function **/
        vm.$onDestroy = function () {
            logger.debug('destroy function');
        };

    }
})();

