(function () {
    'use strict';
    angular.module('tsa.tsaViews.EFmap', []).component('tsaEfMap', {
        templateUrl: "app/tsa-views/ef-map/ef-map.html",
        controllerAs: "ctrl",
        controller: efmapController,
        bindings: {
            onChangeCardType: '&',
            cardId: '@',
            config: '=',
            mode: '@'
        }
    });

    /** Navigation map controller **/
    function efmapController($log, $scope, $timeout, $interval, formatterService,
                            themeService, acquisitionService, 
                            PEFPOS, GEOMOD_SERVER_ADDRESS, MAP_BACKGROUND) {

        /**-------------------- PROPERTIES ----------------**/
        var vm = this;

        vm.logger = $log.getInstance("map-tsa-view");

        var intervalPromise;

        //Default settings (overridden by user config)
        vm.traceEnabled = true;
        vm.autoCenterEnabled = true;
        vm.gridEnabled = false;
        vm.bottomBarEnabled = true;

        vm.mapComponent = undefined;

        // Change the luminosity propert
        themeService.subscribe($scope, function (event, theme) {
            vm.theme = theme;
            if (vm.mapComponent !== undefined) {
                // force a reload of the style
                var luminosity;
                if ( themeService.getTheme().id === themeService.THEME.NIGHT.id) {
                    luminosity = geolayer.carto.Luminosity.NIGHT;
                } else {
                    luminosity = geolayer.carto.Luminosity.DAY;
                }
                vm.mapComponent.setLuminosity(luminosity);
            }
        }, true);

        /* Device ID */
        var frameNavId;
        var EFPosFrameListener;

        /** Map properties **/
        vm.mapId = 'map-' + vm.cardId;

        var background = geolayer.carto.BackgroundProviderType[MAP_BACKGROUND];
        var shipSymbol = geolayer.carto.OwnShipSymbolType.CST_SIZE;

        var lengthVector = 10;

        //Ship movement
        vm.latitude = undefined;
        vm.longitude = undefined;
        vm.heading = undefined;
        vm.gndcourse = undefined;
        vm.gndspeed = undefined;
        vm.surfspeed = undefined;
        vm.mousePositionControl = undefined;
        vm.stringMouseLatitude = '';
        vm.stringMouseLongitude = '';
        vm.stringLatitude = '';
        vm.stringLongitude = '';
        vm.position = {};

        //Ship trace
        var traceIndex = 0;
        var coordinateHistoric = [];
        vm.traceArray = [];

        //Measure tools
        vm.measureToolList = ['Free', 'From boat', 'Disabled'];
        vm.currentMeasureTool = vm.measureToolList[2];

        /**-------------------------- INITIALIZATION / DESTROY METHODS -------------------------**/

        /* Initialisation method */
        vm.$onInit = function init() {
            if (angular.isUndefined(this.config) || angular.isUndefined(this.config.deviceId) || angular.isUndefined(this.config.frameId)) {
                vm.logger.error('config and/or deviceId and/or frameId not defined!');
                return;
            }

            vm.deviceId = this.config.deviceId;
            frameNavId = this.config.frameId;

            loadUserConfig();

            $timeout(initMap, 500); //initialize the map (wait the element creation)
        };

        /* Destroy function, called when the view is destroyed: closes the used resources. */
        vm.$onDestroy = function onDestroy() {
            if (angular.isDefined(intervalPromise))
                $interval.cancel(intervalPromise);

            if (EFPosFrameListener !== undefined)
                acquisitionService.registerForDevice(frameNavId).unregisterForFrame(EFPosFrameListener);
        };

        /** Refresh the map */
        function renderMap() {

            //Set position
            vm.mapComponent.setOwnShipData(vm.latitude, vm.longitude,
                vm.heading, vm.surfspeed, vm.gndcourse,
                vm.gndspeed, lengthVector);

            //Trace
            vm.mapComponent.setOwnShipTrace(vm.traceArray);

            if (vm.mapComponent !== undefined) {
                vm.mapComponent.render();
            }
        }

        /** Initialize the map **/
        function initMap() {

            var options = [];
            options.serverAddress = GEOMOD_SERVER_ADDRESS;
            options['OSMNightMode'] = themeService.getTheme().id.toUpperCase();
            options['map'] = vm.mapId;
            vm.mapComponent = new geolayer.carto.CartoComponent(options, "resources/images"); // Create component
            vm.mapComponent.showStationView(false);
            vm.mapComponent.setOwnShipGeometry(10.0, 10.0, 0.0, 0.0);
            vm.mapComponent.setMarineBackground(background);
            //addQGISLayers();

            vm.mapComponent.setOwnShipSymbolType(shipSymbol);
            vm.mapComponent.showSMF(false);
            vm.mapComponent.showGraticule(vm.gridEnabled);
            vm.mapComponent.setAutoCenterMode(vm.autoCenterEnabled);
            vm.mapComponent.setShowRouteMode(false);
            vm.mapComponent.setShowAISMode(false);
            vm.mapComponent.setShowTraceMode(vm.traceEnabled);
            vm.onMeasureToolChange(vm.currentMeasureTool);

            vm.mapComponent.getMap().on("pointermove", function(e) {
                var coordinate = e.coordinate;
                //var xy = ol.coordinate.toStringXY(coordinate);

                var lonlat = proj.transform(coordinate,'EPSG:3857', 'EPSG:4326');
                //Mouse
                vm.stringMouseLatitude = formatterService.parseLatToString(lonlat[1]);
                vm.stringMouseLongitude = formatterService.parseLongToString(lonlat[0]);
            });

            //Set Luminosity
            var luminosity;
            if ( themeService.getTheme().id === themeService.THEME.NIGHT.id) {
                luminosity = geolayer.carto.Luminosity.DUSK;
            } else {
                luminosity = geolayer.carto.Luminosity.DAY;
            }
            vm.mapComponent.setLuminosity(luminosity);

            // Watches the map element size and calls the 'updateSize()' openlayers
            var mapElement = document.getElementById(vm.mapId);
            $scope.$watch(function () {
                return mapElement.offsetHeight;
            }, renderMap);
            $scope.$watch(function () {
                return mapElement.offsetWidth;
            }, renderMap);

            EFPosFrameListener = acquisitionService.registerForDevice(vm.deviceId).registerForFrame(frameNavId);
            EFPosFrameListener.promise.then({}, {}, parseEFPosFrame);

            intervalPromise = $interval(renderMap, 1000);
        }

        /** Called when a Shipnav frame has been received **/
        function parseEFPosFrame(receivedFrame) {
            var timestamp = receivedFrame.timestamp;
            vm.latitude     = parseFloat(receivedFrame.value[PEFPOS.LATITUDE]);
            vm.longitude    = parseFloat(receivedFrame.value[PEFPOS.LONGITUDE]);
            vm.heading      = parseFloat(receivedFrame.value[PEFPOS.HEADING]);
            vm.gndcourse    = parseFloat(receivedFrame.value[PEFPOS.COG]);
            vm.gndspeed     = parseFloat(receivedFrame.value[PEFPOS.SOG]).toFixed(2);
            vm.surfspeed    = parseFloat(receivedFrame.value[PEFPOS.SPEED]);

            vm.stringLatitude = formatterService.parseLatToString(vm.latitude);
            vm.stringLongitude = formatterService.parseLongToString(vm.longitude);

            // Simulate mobile
            //vm.mobileArray[0] = [vm.position.latitude, vm.position.longitude + 0.001, vm.position.heading+90, geolayer.carto.MobileType.MOBILE, geolayer.carto.MobileStatus.ALIVE];

            if (vm.longitude !== undefined && vm.latitude !== undefined) {

                //Ship trace (add 1 point every 60 frames)
                traceIndex++;
                if (traceIndex === 60) {
                    coordinateHistoric.push({
                        latitude: vm.position.latitude,
                        longitude: vm.position.longitude,
                        timestamp: timestamp
                    });
                    if (coordinateHistoric.length > 500) {
                        coordinateHistoric = coordinateHistoric.slice(1);  //Remove the too old coordinates
                    }
                    vm.traceArray = [];
                    angular.forEach(coordinateHistoric, function (coordinate, key) {
                        vm.traceArray.push(coordinate.latitude, coordinate.longitude);
                    }, this);
                    traceIndex = 0;
                }
            }
        }


        /** Load user configuration from the 'config' property **/
        function loadUserConfig() {
            if (angular.isDefined(vm.config.traceEnabled))
                vm.traceEnabled = vm.config.traceEnabled;
            if (angular.isDefined(vm.config.autoCenterEnabled))
                vm.autoCenterEnabled = vm.config.autoCenterEnabled;
            if (angular.isDefined(vm.config.gridEnabled))
                vm.gridEnabled = vm.config.gridEnabled;
            if (angular.isDefined(vm.config.bottomBarEnabled))
                vm.bottomBarEnabled = vm.config.bottomBarEnabled;
        }

        /**-------------------- VIEW - MODEL METHODS ----------------**/

        /* Measure tools */
        vm.onMeasureToolChange = function onMeasureToolChange(selection) {
            vm.currentMeasureTool = selection;
            vm.mapComponent.getMeasureTool().setActive(false);
            vm.mapComponent.getOwnShipMeasureTool().setActive(false);
            switch (selection) {
                case 'Free':
                    vm.mapComponent.getMeasureTool().setActive(true);
                    break;
                case 'From boat':
                    vm.mapComponent.getOwnShipMeasureTool().setActive(true);
                    break;
            }
        };
        vm.clearMeasures = function clearMeasures() {
            vm.mapComponent.getMeasureTool().resetTool();
            vm.mapComponent.getOwnShipMeasureTool().resetTool();
        };

        vm.focusOnBoat = function focusOnBoat() {
            vm.mapComponent.setAutoCenterMode(vm.autoCenterEnabled = !vm.autoCenterEnabled);
            vm.config.autoCenterEnabled = vm.autoCenterEnabled;
        };

        vm.showTrace = function showTrace() {
            vm.mapComponent.setShowTraceMode(vm.traceEnabled = !vm.traceEnabled);
            vm.config.traceEnabled = vm.traceEnabled;
        };

        vm.showGrid = function showGrid() {
            vm.mapComponent.showGraticule(vm.gridEnabled = !vm.gridEnabled);
            vm.config.gridEnabled = vm.gridEnabled;
        };

        vm.showBottomBar = function showBottomBar() {
            vm.bottomBarEnabled = !vm.bottomBarEnabled;
            vm.config.bottomBarEnabled = vm.bottomBarEnabled;
        };
    }
})();

