(function () {
    'use strict';

    /** FerryboxFluo component declaration **/
    (function () {
        'use strict';
        angular.module('tsa.tsaViews.ferryboxFluo', []).component('tsaFerryboxFluo', {
            templateUrl: "app/tsa-views/ferryboxFluo/ferryboxFluo.html",
            controllerAs: "ctrl",
            controller: ferryboxFluoController,
            bindings: {
                config: '=',
                cardId: '@',
                onChangeCardType: '&'
            }
        });
    })();

    /** FerryboxFluo controller */
    function ferryboxFluoController($timeout, $interval, $log, $scope, acquisitionService, graphUtils, statusService, themeService, FERRYBOX_SCIENCE) {
        var vm = this;
        vm.logger = $log.getInstance("ferryboxFluo-tsa-view");

        /**------------------------------ PROPERTIES -------------------------------**/

        vm.canvasId = "canvas-ferryboxFluo-" + vm.cardId;
        var intervalPromise;
        var timeoutPromise;
        vm.bufferLoading = {};

        vm.FERRYBOX_SCIENCE = FERRYBOX_SCIENCE;
        vm.dataList = [
            { id: FERRYBOX_SCIENCE.AOA_GREEN_ALGAE, cssClass: 'green-algae', color: 'rgb(139, 195, 74)', showInChart: true },
            { id: FERRYBOX_SCIENCE.AOA_BLUE_ALGAE, cssClass: 'blue-algae', color: 'rgb(26, 117, 174)', showInChart: true },
            { id: FERRYBOX_SCIENCE.AOA_DIATOMS, cssClass: 'diatoms', color: 'rgb(255, 171, 0)', showInChart: true },
            { id: FERRYBOX_SCIENCE.AOA_CRYPTOPHYTA, cssClass: 'cryptophyta', color: 'rgb(213, 0, 0)', showInChart: true },
            { id: FERRYBOX_SCIENCE.AOA_YELLOW_SUB, cssClass: 'yellow-sub', color: 'rgb(27, 94, 32)', showInChart: true },
            { id: FERRYBOX_SCIENCE.AOA_TOTAL_ALGAE, cssClass: 'total-algae', color: 'rgb(117, 117, 117)', showInChart: true }];

        /**  Time scales **/
        vm.scales = graphUtils.scales;
        vm.selectedScale = '1 min';

        /** Chart properties **/
        var chart = undefined;
        var chartData = {
            datasets: []
        };

        /**-------------------------- INITIALIZATION / DESTROY METHODS -------------------------**/

        /** Initialization function **/
        vm.$onInit = function init() {
            if (angular.isUndefined(this.config) || angular.isUndefined(this.config.deviceId) || angular.isUndefined(this.config.frameId)) {
                vm.logger.error('config and/or deviceId and/or frameId not defined!');
                return;
            }

            vm.deviceId = this.config.deviceId;
            vm.frameId = this.config.frameId;

            vm.device = acquisitionService.registerForDevice(vm.deviceId);

            angular.forEach(vm.dataList, function (data) {
                vm.device.recordData(vm.frameId, data.id);
                chartData.datasets.push({
                    xAxisID: "x-axis-1",
                    yAxisID: "y-axis-1",
                    borderColor: data.color,
                    backgroundColor: 'rgba(26, 117, 174, 0.3)',
                    data: []
                });
            });

            //Wait the canvas creation, and then create the chart
            timeoutPromise = $timeout(initTimer, 200);

            // Change gridLinesColor of Graph on every change of service variable
            themeService.subscribe($scope, function(event, theme) {
            	vm.theme = theme; 
            	reloadChart();
            });

            if (angular.isDefined(vm.config.scale))
                vm.selectedScale = vm.config.scale;
        };

        /** Destroy function **/
        vm.$onDestroy = function () {
            angular.forEach(vm.dataList, function (data) {
                vm.device.stopRecordData(vm.frameId, data.id);
            });
            $timeout.cancel(timeoutPromise);
            if (intervalPromise !== undefined)
                $interval.cancel(intervalPromise);
        };

        /**------------------------------ METHODS -------------------------------**/

        /** Generate the chart options */
        function getChartOptions() {
            var chartOptions = graphUtils.getChartOptions(vm.scales[vm.selectedScale].stepSize);
            chartOptions.scales.yAxes = [{
                id: "y-axis-1",
                gridLines: { color: vm.theme.gridLinesColor }
            }];
            return chartOptions;
        };

        /** Redraw the chart **/
        function reloadChart() {
            if (chart !== undefined)
                chart.destroy();
            var canvas = document.getElementById(vm.canvasId);
            if (canvas) {
                chart = Chart.Scatter(canvas, { data: chartData, options: getChartOptions() });
                updateGraph();
            }
            vm.config.scale = vm.selectedScale;
        }

        /** Start or update the timer (intervalPromise) */
        var initTimer = function initTimer() {
            if (intervalPromise !== undefined)
                $interval.cancel(intervalPromise);

            //Load archived data
            angular.forEach(vm.dataList, function (data) {
                if (angular.isDefined(vm.device.getData(vm.frameId, data.id))) {
                    var bufferId = vm.scales[vm.selectedScale].bufferId;
                    vm.bufferLoading[vm.selectedScale] = vm.bufferLoading[vm.selectedScale] ? ++vm.bufferLoading[vm.selectedScale] : 1;
                    var selectedScale = vm.selectedScale;

                    var requestCallback = function () {
                        vm.bufferLoading[selectedScale]--;
                        if (chart !== undefined)
                            updateGraph();
                    }
                    vm.device.getData(vm.frameId, data.id).buffers[bufferId].loadArchivedFrames().then(requestCallback, requestCallback);
                }
            });

            reloadChart();
            intervalPromise = $interval(updateGraph, vm.scales[vm.selectedScale].scale / 1000);
        };

        /** Update the graph with the recorded data (called by the timer (intervalPromise)) */
        function updateGraph() {
            //Step 1: update X axis
            var currentDate = statusService.getServerTime().time;
            chart.options.scales.xAxes[0].ticks.min = (currentDate - vm.scales[vm.selectedScale].scale);
            chart.options.scales.xAxes[0].ticks.max = currentDate;

            //Step 2: update data
            var bufferId = vm.scales[vm.selectedScale].bufferId;
            var i = 0;
            angular.forEach(vm.dataList, function (data) {
                chartData.datasets[i].data = [];
                if (data.showInChart) {
                    var recordedData = vm.device.getData(vm.frameId, data.id);
                    if (recordedData !== undefined)
                        angular.forEach(recordedData.buffers[bufferId].frames, function (frame) {
                            chartData.datasets[i].data.push({ x: frame.timestamp, y: frame.value });
                        });
                }
                i++;
            });

            chart.update();
        };

        /** Called when a new X scale is selected */
        vm.onChangeXScale = initTimer;

        /** Called when a checkbox is checked */
        vm.showInChart = function showInChart(data){
            data.showInChart = !data.showInChart;
            updateGraph();
        }

    }
})();

