(function () {
    'use strict';

    /** Component declaration **/
    angular.module('tsa.tsaViews.ephemeride', []).component('tsaEphemeride', {
        templateUrl: "app/tsa-views/ephemeride/ephemeride.html",
        controllerAs: "ctrl",
        controller: ephemerideController,
        bindings: {
            config: '=',
            cardId: '@',
            onChangeCardType: '&'
        }
    });

    /** Component controller */
    function ephemerideController($log, $scope, ephemerideService,
                                  acquisitionService, NAVIGATION,
                                  themeService, statusService) {

        /**------------------------------ PROPERTIES -----------------------------**/
        var vm = this;
        vm.logger = $log.getInstance("tsa-ephemeride");
        vm.luminosity = "day";
        vm.offset = 0;
        vm.times;

        var days = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
        var months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];

        /**------------------------------- METHODS -------------------------------**/
        // Change the luminosity propert
        themeService.subscribe($scope, function (event, theme) {
            if ( themeService.getTheme().id === themeService.THEME.NIGHT.id) {
                    vm.luminosity = "night";
            } else {
                vm.luminosity = "day";
            }
        }, true);

        /** Initialization function **/
        vm.$onInit = function init() {

            if (angular.isUndefined(this.config) || angular.isUndefined(this.config.deviceId) || angular.isUndefined(this.config.frameId)) {
                vm.logger.error('config and/or deviceId and/or frameId not defined!');
                return;
            }
            vm.deviceId = this.config.deviceId;
            vm.frameId = this.config.frameId;
            vm.timeOffset = (statusService.getLocalTimeZoneOffset() ? statusService.getLocalTimeZoneOffset() : "UTC");
            vm.timeLabel = (statusService.getLocalTimeZoneOffset() ? "" : "(UTC)");

            var shipnavFrameListener = acquisitionService.registerForDevice(vm.deviceId).registerForFrame(NAVIGATION.FRAME_SHIPNAV);
            shipnavFrameListener.promise.then({}, {}, parseShipnav);
            function parseShipnav(receivedFrame) {
                vm.latitude = receivedFrame.value[NAVIGATION.LATITUDE].map(parseFloat);
                vm.longitude = receivedFrame.value[NAVIGATION.LONGITUDE].map(parseFloat);

                updateDays(vm.offset);
            }

            vm.nextDay = function nextDay() {
                vm.offset ++;
                updateDays(vm.offset);
            };

            vm.prevDay = function prevDay() {
                vm.offset --;
                updateDays(vm.offset);
            };

            function updateDays(offset) {
                var ldate = new Date();
                ldate.setTime(statusService.getServerTime().time ); //Date UTC
                vm.timeOffset = (statusService.getLocalTimeZoneOffset() ? statusService.getLocalTimeZoneOffset() : "UTC");
                vm.timeLabel = (statusService.getLocalTimeZoneOffset() ? "" : "(UTC)");

                vm.date = ldate;
                vm.date.setDate(vm.date.getDate() + offset);

                vm.day = "date.day."+days[vm.date.getDay()];
                vm.nbDate = vm.date.getDate();
                vm.month = "date.month."+months[vm.date.getMonth()];
                vm.year = vm.date.getFullYear();

                var timeOffset = ldate.getTimezoneOffset() / 60;

                vm.saint = ephemerideService.getSaint(vm.date.getDate(), vm.date.getMonth()+1);

                // get sunlight times
                vm.times = SunCalc.getTimes(vm.date, vm.latitude , vm.longitude);

                vm.moon = SunCalc.getMoonIllumination(/*Date*/ vm.date);

                vm.moonImage = "new-moon-" + vm.luminosity + ".png";
                if (vm.moon.phase < 1.0/16)
                    vm.moonImage = "new-moon-" + vm.luminosity + ".png";
                else if (vm.moon.phase < 3.0/16) //0.18
                    vm.moonImage = "waxing-crescent-" + vm.luminosity + ".png";
                else if (vm.moon.phase < 5.0/16) //0.31
                    vm.moonImage = "first-quarter-" + vm.luminosity + ".png";
                else if (vm.moon.phase < 7.0/16) //0.43
                    vm.moonImage = "waxing-gibbous-" + vm.luminosity + ".png";
                else if (vm.moon.phase < 9.0/16)
                    vm.moonImage = "full-moon-" + vm.luminosity + ".png";
                else if (vm.moon.phase < 11.0/16)
                    vm.moonImage = "waning-gibbous-" + vm.luminosity + ".png";
                else if (vm.moon.phase < 13.0/16)
                    vm.moonImage = "last-quarter-" + vm.luminosity + ".png";
                else if (vm.moon.phase < 15.0/16)
                    vm.moonImage = "waning-crescent-" + vm.luminosity + ".png";
            }

        };


    }
})();

