(function () {
    'use strict';
    angular.module('tsa.tsaViews.graphics', ['tsa.services.filters']).component('tsaGraphics', {
        templateUrl: "app/tsa-views/graphics/graphics.html",
        controller: graphicsController,
        controllerAs: "ctrl",
        bindings: {
            cardId: '@',
            config: '=',
            onChangeCardType: '&'
        }
    });

    /** Graphics controller **/
    function graphicsController(themeService, graphUtils, statusService, acquisitionService, $interval, $scope, $timeout) {
        var vm = this;
        vm.theme = themeService.getTheme();
        var yAxes1Min = -1, yAxes2Min = -1, yAxes1Max = 1, yAxes2Max = 1;

        /** Canvas Id **/
        vm.canvasId = "canvas-" + vm.cardId;

        /** Data colors **/
        vm.dataColor1 = 'rgb(26, 117, 174)';
        vm.dataColor1bis = 'rgba(26, 117, 174, 0.3)';
        vm.dataColor2 = 'rgb(255, 136, 56)';
        vm.dataColor2bis = 'rgba(255, 136, 56,0.3)';

        /**  Time scales **/
        vm.scales = graphUtils.scales;
        vm.selectedScale = '1 min';

        vm.YScaleModes = {
            AUTO: 'views.graphics.auto',
            MANUAL: 'views.graphics.manual'
        };
        vm.selectedYScaleMode = vm.YScaleModes.AUTO;
        vm.zeroOption = false;

        /** Device lists **/
        vm.deviceList = {};

        /** view- model properties **/
        vm.doubleYAxes = true;
        vm.fill = false;
        vm.showSettings = false;
        vm.dataLoaders = 0;

        /** Device 1 **/
        var device1 = undefined;
        vm.deviceId1 = undefined;
        vm.frameList1 = {};
        vm.frameDataId1 = undefined;
        vm.frameId1 = undefined;
        vm.dataId1 = undefined;
        vm.chartTitle1 = "";
        var dataLegend1;

        /** Device 2 **/
        var device2 = undefined;
        vm.deviceId2 = undefined;
        vm.frameList2 = {};
        vm.frameDataId2 = undefined;
        vm.frameId2 = undefined;
        vm.dataId2 = undefined;
        vm.chartTitle2 = "";
        var dataLegend2;

        /** Chart properties **/
        var chart = undefined;
        var chartData = new ChartDataset();

        var intervalPromise = undefined;
        var timeoutPromise = undefined;

        /**-------------------------- INITIALIZATION / DESTROY METHODS -------------------------**/

        /** Initialization function **/
        vm.$onInit = function init() {
            vm.deviceList = acquisitionService.getDeviceList();

            timeoutPromise = $timeout(initTimer, 500); //Wait the canvas creation, and then create the chart

            //Change gridLinesColor of Graph on every change of service variable
            themeService.subscribe($scope, function(event, theme) {
            	vm.theme = theme;
            	reloadChart();
            });

            loadUserConfig();
        };

        /** Destroy function **/
        vm.$onDestroy = function () {
            // Stop the record
            if (device1 !== undefined && vm.frameId1 !== undefined && vm.dataId1 !== undefined)
                device1.stopRecordData(vm.frameId1, vm.dataId1);
            if (device2 !== undefined && vm.frameId2 !== undefined && vm.dataId2 !== undefined)
                device2.stopRecordData(vm.frameId2, vm.dataId2);

            $timeout.cancel(timeoutPromise);
            if (intervalPromise !== undefined)
                $interval.cancel(intervalPromise);
        };

        /** -------------------  PRIVATE METHODS -------------------- */

        /** Generate a new chart dataset */
        function ChartDataset() {
            this.datasets = [{
                label: "Data 1",
                xAxisID: "x-axis-1",
                yAxisID: "y-axis-1",
                borderColor: vm.dataColor1,
                backgroundColor: vm.dataColor1bis,
                data: []
            }, {
                label: "Data 2",
                xAxisID: "x-axis-1",
                yAxisID: (vm.frameDataId1 !== undefined && vm.frameDataId2 !== undefined && vm.doubleYAxes) ? "y-axis-2" : "y-axis-1",
                borderColor: vm.dataColor2,
                backgroundColor: vm.dataColor2bis,
                pointRadius: 0,
                data: []
            }];
        }

        /** Generate the chart options */
        function getChartOptions() {
            var doubleAxesEnabled = (vm.frameDataId1 === undefined || vm.frameDataId2 === undefined) ? false : vm.doubleYAxes;
            var data1 = (vm.frameList1 !== undefined && vm.frameList1[vm.frameId1] !== undefined) ? vm.frameList1[vm.frameId1][vm.dataId1] : undefined;
            var data2 = (vm.frameList2 !== undefined && vm.frameList2[vm.frameId2] !== undefined) ? vm.frameList2[vm.frameId2][vm.dataId2] : undefined;
            if (angular.isDefined(data1))
                dataLegend1 = data1.longName + (data1.units ? ' (' + data1.units + ')' : '');
            if (angular.isDefined(data2))
                dataLegend2 = data2.longName + (data2.units ? ' (' + data2.units + ')' : '');

            var chartOptions = graphUtils.getChartOptions(vm.scales[vm.selectedScale].stepSize, vm.fill);
            chartOptions.scales.yAxes = [
                {
                    id: "y-axis-1",
                    position: "left",
                    scaleLabel: {
                        display: doubleAxesEnabled,
                        fontColor: vm.dataColor1,
                        labelString: dataLegend1 ? dataLegend1 : 'data 1'
                    },
                    gridLines: { color: vm.theme.gridLinesColor },
                    ticks: {
                        beginAtZero: vm.zeroOption,
                        min: vm.selectedYScaleMode == vm.YScaleModes.MANUAL ? yAxes1Min : undefined,
                        max: vm.selectedYScaleMode == vm.YScaleModes.MANUAL ? yAxes1Max : undefined
                    }
                },
                {
                    id: "y-axis-2",
                    position: "right",
                    display: doubleAxesEnabled,
                    reverse: true,
                    scaleLabel: {
                        display: true,
                        fontColor: vm.dataColor2,
                        labelString: dataLegend2 ? dataLegend2 : 'data 2'
                    },
                    gridLines: { drawOnChartArea: false },// only want the grid lines for one axis to show up
                    ticks: {
                        beginAtZero: vm.zeroOption,
                        min: vm.selectedYScaleMode == vm.YScaleModes.MANUAL ? yAxes2Min : undefined,
                        max: vm.selectedYScaleMode == vm.YScaleModes.MANUAL ? yAxes2Max : undefined
                    }
                }];
            return chartOptions;
        }

        /** Redraw the chart */
        function reloadChart() {
            if (chart !== undefined)
                chart.destroy();

            var canvas = document.getElementById(vm.canvasId);
            if (canvas) {
                chartData = new ChartDataset();
                chart = Chart.Scatter(canvas, { data: chartData, options: getChartOptions() });
                updateGraph();
                saveUserConfig();
            }
        }


        /** Load archived data */
        function loadArchivedData() {
            var bufferId = vm.scales[vm.selectedScale].bufferId;

            function loadArchivedDataEnd() { // Called when the archived request are finished
                vm.dataLoaders--;
                if (chart !== undefined)
                    updateGraph();
            }

            if (device1 !== undefined && vm.frameId1 !== undefined && vm.dataId1 !== undefined) {
                var recordedData1 = device1.getData(vm.frameId1, vm.dataId1);
                if (recordedData1 !== undefined) {
                    vm.dataLoaders++;
                    recordedData1.buffers[bufferId].loadArchivedFrames().then(loadArchivedDataEnd, loadArchivedDataEnd);
                }
            }

            if (device2 !== undefined && vm.frameId2 !== undefined && vm.dataId2 !== undefined) {
                var recordedData2 = device2.getData(vm.frameId2, vm.dataId2);
                if (recordedData2 !== undefined) {
                    vm.dataLoaders++;
                    recordedData2.buffers[bufferId].loadArchivedFrames().then(loadArchivedDataEnd, loadArchivedDataEnd);
                }
            }
        }

        /** Start or update the timer (intervalPromise) */
        function initTimer() {
            if (intervalPromise !== undefined)
                $interval.cancel(intervalPromise);

            loadArchivedData();
            reloadChart();
            intervalPromise = $interval(updateGraph, vm.scales[vm.selectedScale].scale / 1000);
        }

        /** Update the graph with the recorded data, called by the timer (intervalPromise) */
        function updateGraph() {
            //Step 1: update X axis
            var currentDate = statusService.getServerTime().time;
            chart.options.scales.xAxes[0].ticks.min = (currentDate - vm.scales[vm.selectedScale].scale);
            chart.options.scales.xAxes[0].ticks.max = currentDate;

            //Step 2: update data
            var bufferId = vm.scales[vm.selectedScale].bufferId;
            chartData.datasets[0].data = [];
            chartData.datasets[1].data = [];
            // Data 1
            if (device1 !== undefined && vm.frameId1 !== undefined && vm.dataId1 !== undefined) {
                var recordedData1 = device1.getData(vm.frameId1, vm.dataId1);
                if (recordedData1 !== undefined) {
                    angular.forEach(recordedData1.buffers[bufferId].frames, function (frame) {
                        chartData.datasets[0].data.push({ x: frame.timestamp, y: frame.value });
                    });
                }
            }
            // Data 2
            if (device2 !== undefined && vm.frameId2 !== undefined && vm.dataId2 !== undefined) {
                var recordedData2 = device2.getData(vm.frameId2, vm.dataId2);
                if (recordedData2 !== undefined) {
                    angular.forEach(recordedData2.buffers[bufferId].frames, function (frame) {
                        chartData.datasets[1].data.push({ x: frame.timestamp, y: frame.value });
                    });
                }
            }
            chart.update();
        }

        /** Save user configuration in a 'config' property **/
        function saveUserConfig() {
            vm.config = {
                type: 'Graphics',
                scale: vm.selectedScale,
                deviceId1: vm.deviceId1,
                frameDataId1: vm.frameDataId1,
                title1: vm.chartTitle1,
                dataLegend1: dataLegend1,
                deviceId2: vm.deviceId2,
                frameDataId2: vm.frameDataId2,
                title2: vm.chartTitle2,
                dataLegend2: dataLegend2,
                doubleYAxes: vm.doubleYAxes,
                fill: vm.fill,
                zeroOption: vm.zeroOption,
                selectedYScaleMode: vm.selectedYScaleMode,
                yAxes1Min: yAxes1Min,
                yAxes1Max: yAxes1Max,
                yAxes2Min: yAxes2Min,
                yAxes2Max: yAxes2Max
            };
        }

        /** Load user configuration from the 'config' property **/
        function loadUserConfig() {
            if (vm.config && vm.config.type === 'Graphics') {
                // Global settings
                vm.selectedScale = vm.config.scale;
                vm.doubleYAxes = vm.config.doubleYAxes;
                vm.fill = vm.config.fill;
                vm.zeroOption = vm.config.zeroOption;
                vm.selectedYScaleMode = vm.config.selectedYScaleMode;
                yAxes1Min = vm.config.yAxes1Min;
                yAxes1Max = vm.config.yAxes1Max;
                yAxes2Min = vm.config.yAxes2Min;
                yAxes2Max = vm.config.yAxes2Max;
                //Device 1
                vm.chartTitle1 = vm.config.title1;
                dataLegend1 = vm.config.dataLegend1;
                vm.deviceId1 = vm.config.deviceId1;
                vm.updateDataList1();
                vm.frameDataId1 = vm.config.frameDataId1;
                vm.onChangeData1();
                //Device 2
                vm.chartTitle2 = vm.config.title2;
                dataLegend2 = vm.config.dataLegend2;
                vm.deviceId2 = vm.config.deviceId2;
                vm.updateDataList2();
                vm.frameDataId2 = vm.config.frameDataId2;
                vm.onChangeData2();
            }
        }

        /** Get Y max value from a dataset **/
        function getYMaxValue(dataset) {
            var yValues = [];
            angular.forEach(dataset, function (data) {
                if (data.y != null)
                    yValues.push(data.y);
            });
            return Math.max.apply(null, yValues);
        }

        /** Get Y min value from a dataset **/
        function getYMinValue(dataset) {
            var yValues = [];
            angular.forEach(dataset, function (data) {
                if (data.y != null)
                    yValues.push(data.y);
            });
            return Math.min.apply(null, yValues);
        }

        /** -------------------  VIEW-MODEL PUBLIC METHODS -------------------- */

        /** Called when a chart option changed */
        vm.updateChartOptions = reloadChart;

        /** Called when a new X scale is selected */
        vm.onChangeXScale = initTimer;

        /** Called when a new device 1 is selected: reset the current data and update the data list **/
        vm.updateDataList1 = function updateDataList1() {
            //Reset the data
            vm.frameDataId1 = undefined;
            vm.onChangeData1();
            //Refresh the frame list
            vm.frameList1 = {};
            if (vm.deviceId1 !== undefined) {
                device1 = acquisitionService.registerForDevice(vm.deviceId1);
                vm.frameList1 = device1.frameList;
            }
        };

        /** Called when a new device 2 is selected: reset the current data and update the data list **/
        vm.updateDataList2 = function updateDataList2() {
            //Reset the data
            vm.frameDataId2 = undefined;
            vm.onChangeData2();
            //Refresh the frame list
            vm.frameList2 = {};
            if (vm.deviceId2 !== undefined) {
                device2 = acquisitionService.registerForDevice(vm.deviceId2);
                vm.frameList2 = device2.frameList;
            }
        };

        /** Called when a data 1 is selected **/
        vm.onChangeData1 = function onChangeData1() {
            if (device1 !== undefined) {
                if (vm.frameId1 !== undefined && vm.dataId1 !== undefined)
                    device1.stopRecordData(vm.frameId1, vm.dataId1);//Stop the record of the previous data
                vm.frameId1 = vm.dataId1 = undefined;
                if (vm.frameDataId1 !== undefined) {
                    vm.frameId1 = vm.frameDataId1.split('!')[0]; //Set vm.frameId and data properties
                    vm.dataId1 = vm.frameDataId1.split('!')[1];
                    device1.recordData(vm.frameId1, vm.dataId1); //Record the new data
                }
                reloadChart();
                loadArchivedData();
            }
        };

        /** Called when a data 2 is selected **/
        vm.onChangeData2 = function onChangeData2() {
            if (device2 !== undefined) {
                if (vm.frameId2 !== undefined && vm.dataId2 !== undefined)
                    device2.stopRecordData(vm.frameId2, vm.dataId2);//Stop the record of the previous data
                vm.frameId2 = vm.dataId2 = undefined;
                if (vm.frameDataId2 !== undefined) {
                    vm.frameId2 = vm.frameDataId2.split('!')[0]; //Set vm.frameId and data properties
                    vm.dataId2 = vm.frameDataId2.split('!')[1];
                    device2.recordData(vm.frameId2, vm.dataId2); //Record the new data
                }
                reloadChart();
                loadArchivedData();
            }
        };

        /** Called when the clear button is clicked: remove the data and device selection **/
        vm.onClearData1 = function onClearData1() {
            vm.frameDataId1 = undefined;
            vm.onChangeData1();
            vm.deviceId1 = undefined;
            device1 = undefined;
            vm.frameList1 = {};
        };

        /** Called when the clear button is clicked: remove the data and device selection **/
        vm.onClearData2 = function onClearData1() {
            vm.frameDataId2 = undefined;
            vm.onChangeData2();
            vm.deviceId2 = undefined;
            device2 = undefined;
            vm.frameList2 = {};
        };

        /** Called when the Y axes mode switch between 'auto' and 'manual' **/
        vm.updateYAxesMode = function updateYAxesMode() {
            if (vm.selectedYScaleMode === vm.YScaleModes.MANUAL) { //Set min / max properties
                yAxes1Max = chartData.datasets[0].data.length > 0 ? getYMaxValue(chartData.datasets[0].data) : 1;
                yAxes1Min = chartData.datasets[0].data.length > 0 ? getYMinValue(chartData.datasets[0].data) : -1;
                yAxes2Max = chartData.datasets[1].data.length > 0 ? getYMaxValue(chartData.datasets[1].data) : 1;
                yAxes2Min = chartData.datasets[1].data.length > 0 ? getYMinValue(chartData.datasets[1].data) : -1;
            }
            reloadChart();
        };

        /** Called when the 'Zoom +'/'Zoom -' button is clicked **/
        vm.onZoomY = function onZoomY(out) {
            if (yAxes1Max !== undefined && yAxes1Min !== undefined) {
                var i = 0.1 * (yAxes1Max - yAxes1Min);
                yAxes1Max += out ? i : -i;
                yAxes1Min += out ? -i : i;
            }
            if (yAxes2Max !== undefined && yAxes2Min !== undefined) {
                var i = 0.1 * (yAxes2Max - yAxes2Min);
                yAxes2Max += out ? i : -i;
                yAxes2Min += out ? -i : i;
            }
            reloadChart();
        };

        /** Called when the 'Center' button is clicked: translate the graph to the center **/
        vm.onCenterYAxes = function onCenterYAxes() {
            if (vm.selectedYScaleMode === vm.YScaleModes.MANUAL) {
                //if there is one axes for 2 charts, the center is the middle between the max and the min of all values
                if (vm.frameDataId1 !== undefined && vm.frameDataId2 !== undefined && !chart.options.scales.yAxes[1].display) {
                    var minVal = Math.min(getYMinValue(chartData.datasets[0].data), getYMinValue(chartData.datasets[1].data));
                    var maxVal = Math.max(getYMaxValue(chartData.datasets[0].data), getYMaxValue(chartData.datasets[1].data));
                    var middle = (maxVal + minVal) / 2;
                    var height = yAxes1Max - yAxes1Min;
                    yAxes1Min = middle - height / 2;
                    yAxes1Max = middle + height / 2;
                }
                else {
                    if (chartData.datasets[1].data.length > 0) {
                        var middle = (getYMaxValue(chartData.datasets[1].data) + getYMinValue(chartData.datasets[1].data)) / 2;
                        var height = yAxes2Max - yAxes2Min;
                        yAxes2Min = middle - height / 2;
                        yAxes2Max = middle + height / 2;
                    }
                    if (chartData.datasets[0].data.length > 0) {
                        var middle = (getYMaxValue(chartData.datasets[0].data) + getYMinValue(chartData.datasets[0].data)) / 2;
                        var height = yAxes1Max - yAxes1Min;
                        yAxes1Min = middle - height / 2;
                        yAxes1Max = middle + height / 2;
                    }
                    else {
                        yAxes1Min = yAxes2Min;
                        yAxes1Max = yAxes2Max;
                    }
                }
                reloadChart();
            }
        };
    }

})();

