(function () {
    'use strict';

    /** Component declaration **/
    angular.module('tsa.tsaViews.haulComposition', []).component('tsaHaulComposition', {
        templateUrl: "app/tsa-views/haulComposition/haulComposition.html",
        controllerAs: "ctrl",
        controller: haulCompositionController,
        bindings: {
            config: '=',
            cardId: '@',
            onChangeCardType: '&'
        }
    });

    /** Component controller */
    function haulCompositionController($interval, $location, $log, $scope, acquisitionService, staticGridService, HAUL) {
        var vm = this;
        vm.logger = $log.getInstance("HaulComposition-tsa-view");

        /**------------------------------ PROPERTIES -----------------------------**/
        var frameListener;
        var previousHaul;
        var previousPocket;
        var intervalPromise;
        var speciesList = [];
        vm.speciesListToDisplay = [];
        vm.sortedPercentage;
        vm.pageArray = [];
        vm.selectedPage = 1;
        vm.playEnabled = true;

        /**-------------------------- INITIALIZATION / DESTROY METHODS -------------------------**/

        /** Initialization function **/
        vm.$onInit = function init() {
            if (angular.isUndefined(this.config) || angular.isUndefined(this.config.deviceId) || angular.isUndefined(this.config.frameId)) {
                vm.logger.error('config and/or deviceId and/or frameId not defined!');
                return;
            }

            vm.deviceId = this.config.deviceId;
            vm.frameId = this.config.frameId;

            vm.haul = HAUL.HAUL_ID;
            vm.pocket = HAUL.POCKET;
            vm.totalPocket = HAUL.TOTAL_POCKET_WEIGHT;
            vm.totalSortedSpecies = HAUL.TOTAL_SORTED_SPECIES_WEIGHT;

            frameListener = acquisitionService.registerForDevice(vm.deviceId).registerForFrame(vm.frameId);
            frameListener.promise.then({}, {}, function (receivedFrame) {
                vm.logger.debug('blcar frame received: ' + angular.toJson(receivedFrame.value));
                parseBlcarFrame(receivedFrame.value);
            });

            $scope.$watch(function () {
                return document.getElementById('array').offsetHeight;
            }, function () {
                updateSpeciesTable();
            });

            intervalPromise = $interval(function () {
                if (vm.playEnabled) {
                    vm.selectedPage = (vm.selectedPage >= vm.pageArray.length) ? 1 : ++vm.selectedPage;
                    updateSpeciesTable();
                }
            }, 8000);
        };

        /** Destroy function **/
        vm.$onDestroy = function () {
            if (intervalPromise !== undefined)
                $interval.cancel(intervalPromise);
            if (angular.isUndefined(vm.deviceId))
                acquisitionService.registerForDevice(vm.deviceId).unregisterForFrame(frameListener);
        };

        /**------------------------------- PRIVATE METHODS -------------------------------**/

        /** Called when a Blcar frame has been received **/
        function parseBlcarFrame(frame) {
            var haul = frame[HAUL.HAUL_ID][0];
            var pocketId = frame[HAUL.POCKET][0];
            var totalSorted = frame[HAUL.TOTAL_SORTED_SPECIES_WEIGHT][0];
            var totalPocket = frame[HAUL.TOTAL_POCKET_WEIGHT][0];
            var newSpecies = {
                'id': frame[HAUL.SPECIES_ID][0],
                'sign': frame[HAUL.SPECIES_SIGN][0],
                'weight': frame[HAUL.SPECIES_WEIGHT][0],
                'estimation': sprintf('%.2f', frame[HAUL.SPECIES_WEIGHT][0] / totalSorted)
            };

            vm.sortedPercentage = sprintf('%.2f', (totalSorted / totalPocket) * 100) + ' %';

            //If the pocket has changed, reset the species list
            if (previousHaul != haul || previousPocket != pocketId) {
                speciesList = [];
                previousPocket = pocketId;
                previousHaul = haul;
            }

            //All the properties of the current species have been received -> add the species to the list
            var speciesAlreadyExist = false;
            angular.forEach(speciesList, function (species) {
                //If the species exist, just update the sign and weight values
                if (species.id == newSpecies.id) {
                    speciesAlreadyExist = true;
                    species.sign = newSpecies.sign;
                    species.weight = newSpecies.weight;
                    species.estimation = newSpecies.estimation;
                }
            });
            if (!speciesAlreadyExist)
                speciesList.push(newSpecies);

            // Sort the list
            speciesList.sort(function (a, b) {
                return b.weight - a.weight;
            });
        }

        /** Update the Species table (adjust the row number in function of the table height) */
        function updateSpeciesTable() {
            var arrayHeight = document.getElementById('array').offsetHeight;
            var rowHeight = ($location.path() == '/static' && staticGridService.isSingleMode()) ? 64 : 42;
            var pageCapacity = Math.floor(arrayHeight / rowHeight);

            if (pageCapacity != 0) {
                vm.pageArray = [];
                for (var i = 0; i < Math.ceil(speciesList.length / pageCapacity); i++)
                    vm.pageArray.push(i + 1);
                vm.speciesListToDisplay = speciesList.slice((vm.selectedPage - 1) * pageCapacity, vm.selectedPage * pageCapacity);
            }
        }

        /** Select a page **/
        vm.selectPage = function selectPage(page) {
            vm.selectedPage = page;
            updateSpeciesTable();
        }
    }
})();

(function () {
    'use strict';

    /** Generic view component **/
    angular.module('tsa.tsaViews.hullSoundSpeed',[]).component('tsaHullSoundSpeed', {
        templateUrl: "app/tsa-views/hullSoundSpeed/hullSoundSpeed.html",
        controllerAs: "ctrl",
        controller: hullSoundSpeedController,
        bindings: {
            config: '=',
            cardId: '@',
            onChangeCardType: '&'
        }
    });

    /** Generic view controller */
    function hullSoundSpeedController($timeout, $interval, $scope, acquisitionService, graphUtils, $log, statusService, themeService, HULLSOUNDSPEED) {
        var vm = this;
        vm.logger = $log.getInstance("tsa-hullsoundspeed");

        /**------------------------------ PROPERTIES -------------------------------**/

        vm.canvasId = "canvas-hss-" + vm.cardId;
        var intervalPromise;
        var timeoutPromise;
        vm.bufferLoading = {};

        /**  Time scales **/
        vm.scales = graphUtils.scales;
        vm.selectedScale = '1 min';

        /** Chart properties **/
        var chart = undefined;
        var chartData = {
            datasets: [{
                label: "Data 1",
                xAxisID: "x-axis-1",
                yAxisID: "y-axis-1",
                borderColor: 'rgb(26, 117, 174)',
                backgroundColor: 'rgba(26, 117, 174, 0.3)',
                data: []
            }]
        };

        /** Initialization function */
        vm.$onInit = function init() {
            if (angular.isUndefined(vm.config)
                || angular.isUndefined(vm.config.deviceId)
                || angular.isUndefined(vm.config.frameId)) {
                vm.logger.error('deviceId or/and frameId or/and hullsoundspeedDeviceId not defined!');
                return;
            }

            vm.deviceId = vm.config.deviceId;

            // properties defined in the front-end configuration file (parameter.json)
            vm.frameId = vm.config.frameId;
            vm.HULLSOUNDSPEED = HULLSOUNDSPEED;

            vm.device = acquisitionService.registerForDevice(vm.deviceId);
            vm.device.recordData(vm.frameId, vm.HULLSOUNDSPEED.DATA);

            //Wait the canvas creation, and then create the chart
            timeoutPromise = $timeout(initTimer, 200);

            // Change gridLinesColor of Graph on every change of service variable
            themeService.subscribe($scope, function(event, theme) {
                vm.theme = theme;
                reloadChart();
            });

            if (angular.isDefined(vm.config.scale))
                vm.selectedScale = vm.config.scale;
        };

        /** Destroy function **/
        vm.$onDestroy = function () {
            vm.device.stopRecordData(vm.frameId, vm.HULLSOUNDSPEED.DATA);
            $timeout.cancel(timeoutPromise);
            if (intervalPromise !== undefined)
                $interval.cancel(intervalPromise);
        };
        /**------------------------------ METHODS -------------------------------**/

        /** Generate the chart options */
        function getChartOptions() {
            var chartOptions = graphUtils.getChartOptions(vm.scales[vm.selectedScale].stepSize);
            chartOptions.scales.yAxes = [{
                id: "y-axis-1",
                gridLines: { color: vm.theme.gridLinesColor },
                ticks: {suggestedMax: 10 }
            }];
            return chartOptions;
        }

        /** Redraw the chart **/
        function reloadChart() {
            if (chart !== undefined)
                chart.destroy();
            var canvas = document.getElementById(vm.canvasId);
            if (canvas) {
                chart = Chart.Scatter(canvas, { data: chartData, options: getChartOptions() });
                updateGraph();
            }
            vm.config.scale = vm.selectedScale;
        }

        /** Start or update the timer (intervalPromise) */
        var initTimer = function initTimer() {
            if (intervalPromise !== undefined)
                $interval.cancel(intervalPromise);

            //Load archived data
            if (angular.isDefined(vm.device.getData(vm.frameId, vm.HULLSOUNDSPEED.DATA))) {
                var bufferId = vm.scales[vm.selectedScale].bufferId;
                vm.bufferLoading[vm.selectedScale] = vm.bufferLoading[vm.selectedScale] ? ++vm.bufferLoading[vm.selectedScale] : 1;
                var selectedScale = vm.selectedScale;
                var requestCallback = function () {
                    vm.bufferLoading[selectedScale]--;
                    if (chart !== undefined)
                        updateGraph();
                };
                vm.device.getData(vm.frameId, vm.HULLSOUNDSPEED.DATA).buffers[bufferId].loadArchivedFrames().then(requestCallback, requestCallback);
            }
            reloadChart();
            intervalPromise = $interval(updateGraph, vm.scales[vm.selectedScale].scale / 1000);
        };

        /** Update the graph with the recorded data (called by the timer (intervalPromise)) */
        function updateGraph() {
            //Step 1: update X axis
            var currentDate = statusService.getServerTime().time;
            chart.options.scales.xAxes[0].ticks.min = (currentDate - vm.scales[vm.selectedScale].scale);
            chart.options.scales.xAxes[0].ticks.max = currentDate;

            //Step 2: update data
            var bufferId = vm.scales[vm.selectedScale].bufferId;
            chartData.datasets[0].data = [];
            var recordedData = vm.device.getData(vm.frameId, vm.HULLSOUNDSPEED.DATA);
            if (recordedData !== undefined)
                angular.forEach(recordedData.buffers[bufferId].frames, function (frame) {
                    chartData.datasets[0].data.push({ x: frame.timestamp, y: frame.value });
                });

            chart.update();
        }

        /** Called when a new X scale is selected */
        vm.onChangeXScale = initTimer;

    }
})();

