(function () {
    'use strict';
    
    /** Propulsion component **/
    angular.module('tsa.tsaViews.landingAt', []).component('tsaLandingAt', {
        templateUrl: "app/tsa-views/landing-at/landing-at.html",
        controllerAs: "ctrl",
        controller: landingControllerAt,
        bindings: {
            config: '=',
            cardId: '@',
            onChangeCardType: '&'
        }
    });

    /** Propulsion controller **/
    function landingControllerAt(acquisitionService, $timeout, $scope, easelJsUtils, themeService, NAVIGATION, WEATHER) {
        var vm = this;
        var TAG = '[landingControllerAt] ';

        /**------------------------------ PROPERTIES -------------------------------**/

        //Canvas
        vm.propulsionCanvasId = "canvas-propulsion-landing-at-" + vm.cardId;
        var propulsionCanvas;
        var propulsionStage;
        var propulsionBackContainer;
        var propulsionFrontContainer;
        
        var propulsionCanvasWidth;
        var propulsionCanvasHeight;
       
        vm.windCanvasId = "canvas-wind-landing-at-" + vm.cardId;
        var windCanvas;
        var windCanvasSize;
        var windCanvasWidth;
        var windCanvasHeight;
        var windStage;
        //Shapes
        var boatShape;
        var boatHeadingShape;
        var trueWindShape;
        var relWindShape;
        var relHeading;

        // Shape properties
        var trueWindColor = "#E53935";
        var relWindColor = "#1a75ae";
        
        vm.readyToDraw = false;

        vm.colors = {
        	background: '#CCCCCC',
        	zero: '#EE2222',
        	tick: '#888888',
        	rudder: '#000000',
        	green: '#11CC11',
        	red: '#EE2222',
        	forward: '#1111DD',
        	backward: '#000000',
        	funkyBackground: '#F3BF52'
        };
        
        vm.themeChanged = function themeChanded(event, theme) {
        	console.log("landing at themechanged");
        }
        
        themeService.subscribe($scope, vm.themeChanged);
        
        /**------------------------------ METHODS -------------------------------**/

        /** Initialization function **/
        vm.$onInit = function init() {

            console.log(TAG + '$onInit: deviceId = ' + (this.config ? this.config.deviceId : 'config undefined'));

            if (this.config == undefined || this.config.deviceId == undefined) {
                console.error(TAG + 'device Id not found!');
                return;
            }

            vm.deviceId = this.config.deviceId; //Component parameter (mandatory)
            vm.navId = this.config.navId; 
            vm.device = acquisitionService.registerForDevice(vm.deviceId);
            vm.frameId = 'atpropulsion'
            vm.navFrameId = 'shipnav';

            vm.weatherFrame = 'weather';
            vm.weatherDeviceId = this.config.weatherId;
            vm.trueWindHeadingId = 'trueairheading';
            vm.trueWindSpeedId = 'trueairspeed';
            vm.relWindHeadingId = 'relairheading';
            vm.relWindSpeedId = 'relairspeed';
            
            //Create object to add x and y properties
            vm.frontTrans = { id: 'fronttrans' };
            vm.frontPropAngle = { id: 'frontpropangle' };
            vm.mainLeft = { id: 'mainleft' };
            vm.mainRight= {id: 'mainright' };
            vm.angle = {id: 'angle' };
            vm.fontsize = 1;

            vm.frameListener = acquisitionService.registerForDevice(vm.deviceId).registerForFrame(vm.frameId);
            vm.frameListener.promise.then({}, {}, function (frame) {
            	vm.frontTrans.value = frame.value[vm.frontTrans.id];
            	vm.frontPropAngle.value = frame.value[vm.frontPropAngle.id];
            	vm.mainLeft.value = frame.value[vm.mainLeft.id];
            	vm.mainRight.value = frame.value[vm.mainRight.id];
            	vm.angle.value = frame.value[vm.angle.id];

            	if(vm.readyToDraw) {
                    updatePropulsionFrontContainer();
            		propulsionStage.update();
            	}
            }); 
            	
			vm.heading		= { id: 'heading'};
			vm.gndCourse	= { id: 'gndcourse'};
			vm.gndSpeed		= { id: 'gndspeed'};
			vm.surfCourse	= { id: 'surfcourse'};
			vm.surfSpeed	= { id: 'surfspeed'};
			
			vm.surfNorth = vm.surfWest = vm.surfEast = vm.surfSouth = undefined;
			vm.gndNorth = vm.gndWest = vm.gndEast = vm.gndSouth = undefined;
			
			vm.navFrameListener = acquisitionService.registerForDevice(vm.navId).registerForFrame('shipnav');
			vm.navFrameListener.promise.then({}, {}, function (frame) {
				vm.heading.value = frame.value[vm.heading.id];
				vm.gndCourse.value = frame.value[vm.gndCourse.id];
				vm.gndSpeed.value = frame.value[vm.gndSpeed.id];
				vm.surfCourse.value = frame.value[vm.surfCourse.id];
				vm.surfSpeed.value = frame.value[vm.surfSpeed.id];

                if (windStage != null && boatShape == null) {
                    drawBoat();
                }
                if (angular.isDefined(boatShape)) {
                    boatShape.rotation = vm.heading.value - 90;
                    boatHeadingShape.rotation = vm.heading.value - 90;

                    if (angular.isDefined(relWindShape))
                        relWindShape.rotation = parseFloat(vm.heading.value) + parseFloat(relHeading) - 90;
                    windStage.update();
                }
                
				var surfA = vm.surfSpeed.value * Math.cos((vm.surfCourse.value-vm.heading.value)*Math.PI/180.0);
				if(surfA>=0) {
					vm.surfNorth = surfA;
					vm.surfSouth = undefined;
				} else {
					vm.surfNorth = undefined;
					vm.surfSouth = -surfA;
				}
				
				var surfB = vm.surfSpeed.value * Math.sin((vm.surfCourse.value-vm.heading.value)*Math.PI/180.0);
				if(surfB<0) {
					vm.surfWest = -surfB;
					vm.surfEast = undefined;
				} else {
					vm.surfWest = undefined;
					vm.surfEast = surfB;
				}
				
				var gndA = vm.gndSpeed.value * Math.cos((vm.gndCourse.value-vm.heading.value)*Math.PI/180.0);
				if(gndA>=0) {
					vm.gndNorth = gndA;
					vm.gndSouth = undefined;
				} else {
					vm.gndNorth = undefined;
					vm.gndSouth = -gndA;
				}
				
				var gndB = vm.gndSpeed.value * Math.sin((vm.gndCourse.value-vm.heading.value)*Math.PI/180.0);
				if(gndB<0) {
					vm.gndWest = -gndB;
					vm.gndEast = undefined;
				} else {
					vm.gndWest = undefined;
					vm.gndEast = gndB;
				}
			});
			
			
            vm.trueWindSpeedId = WEATHER.TRUE_AIR_SPEED;
            vm.trueWindHeadingId = WEATHER.TRUE_AIR_HEADING;
            vm.relWindSpeedId = WEATHER.REL_AIR_SPEED;
            vm.relWindHeadingId = WEATHER.REL_AIR_HEADING;

            // Wind listener initisalisation
            vm.weatherFrameListener = acquisitionService.registerForDevice(vm.weatherDeviceId).registerForFrame(WEATHER.FRAME);
            vm.weatherFrameListener.promise.then({}, {}, function (frame) {
                if (angular.isDefined(windStage)) {
                    var trueSpeed = frame.value[WEATHER.TRUE_AIR_SPEED];
                    var trueHeading = frame.value[WEATHER.TRUE_AIR_HEADING];
                    var relSpeed = frame.value[WEATHER.REL_AIR_SPEED];
                    relHeading = frame.value[WEATHER.REL_AIR_HEADING];

                    windStage.removeChild(trueWindShape);
                    windStage.removeChild(relWindShape);
                    if (angular.isDefined(vm.heading.value)) { //If boath heading is known, draw rel and true flags
                        trueWindShape = drawWindFlag(trueSpeed, trueWindColor, trueHeading);
                        relWindShape = drawWindFlag(relSpeed, relWindColor, parseFloat(vm.heading.value) + parseFloat(relHeading));
                    }
                    else relWindShape = drawWindFlag(relSpeed, relWindColor, parseFloat(relHeading)); // else draw only rel flag
                    windStage.update();
                }
            });
			
            	
            $timeout(function () {
                initPropulsionCanvas();
                initWindCanvas();
                vm.readyToDraw = true;
            }, 500); 

        };

        /** Destroy function: remove the data listeners */
        vm.$onDestroy = function destroy() {
            if (angular.isDefined(vm.frameListener))
                acquisitionService.registerForDevice(vm.deviceId).unregisterForFrame(vm.frameListener);
            if (angular.isDefined(vm.weatherFrameListener))
                acquisitionService.registerForDevice(vm.weatherDeviceId).unregisterForFrame(vm.weatherFrameListener);
            if (angular.isDefined(vm.navFrameListener))
                acquisitionService.registerForDevice(vm.navId).unregisterForFrame(vm.navFrameListener);
        }
        
        /**Initialize the canvas **/
        function initPropulsionCanvas() {
            propulsionCanvas = document.getElementById(vm.propulsionCanvasId);
            if (propulsionCanvas != null) {
                propulsionStage = new createjs.Stage(propulsionCanvas);

                propulsionBackContainer = new createjs.Container();
                propulsionFrontContainer = new createjs.Container();
                
                propulsionStage.addChild(propulsionBackContainer, propulsionFrontContainer);
                
                $scope.$watch(function () {
                    return propulsionCanvas.offsetHeight
                }, function () {
                    updatePropulsionCanvasSize();
                });
                $scope.$watch(function () {
                    return propulsionCanvas.offsetWidth
                }, function () {
                    updatePropulsionCanvasSize();
                });
                updatePropulsionCanvasSize();
            }
        }

        /** Create the stage if needed, reset the canvas and redraw all the shapes **/
        function updatePropulsionCanvasSize() {
            if (propulsionCanvas != null) {
                propulsionCanvasWidth = propulsionCanvas.offsetWidth;
                propulsionCanvasHeight = propulsionCanvas.offsetHeight;

                //Canvas size properties setting
                propulsionCanvas.setAttribute('width', propulsionCanvasWidth);
                propulsionCanvas.setAttribute('height', propulsionCanvasHeight);

                angular.element(propulsionCanvas).parent().find("tsa-data-view .value").css({
                	"font-size": ""+propulsionCanvasHeight/220+"em"
        		}); 
                
                angular.element(propulsionCanvas).parent().find("tsa-data-view").css({
                	"width": ""+Math.round(propulsionCanvasHeight/3.5)+"px !important",
                	"max-width": ""+Math.round(propulsionCanvasHeight/3.5)+"px",
                	"background-color": "green"
                }); 

                propulsionStage.x = propulsionCanvasWidth/2;
                propulsionStage.y = propulsionCanvasHeight/2;
            	var scale = propulsionCanvasHeight/100;
            	propulsionStage.scaleX = propulsionStage.scaleY = scale;
            	                
                updatePropulsionBackContainer();
                propulsionBackContainer.cache(-propulsionCanvasWidth/2, -propulsionCanvasHeight/2, 
                		propulsionCanvasWidth, propulsionCanvasHeight, scale);
                updatePropulsionFrontContainer();
                updatePropulsionLabels();

                propulsionStage.update();
            }
        }
        
        
        function updatePropulsionBackContainer() {
        	propulsionBackContainer.removeAllChildren();
        	
            var vesselShape = easelJsUtils.createVessel(vm.colors.tick);
            vesselShape.scaleX = 0.3;
            vesselShape.scaleY = 0.65;
            vesselShape.y = -12;
            
            var backLeftRudderShape =  easelJsUtils.createBackRudder(vm.colors.background)
            backLeftRudderShape.y = 22;
            backLeftRudderShape.x = -10;
            backLeftRudderShape.scaleX = backLeftRudderShape.scaleY = 12/100;
            
            var backRightRudderShape =  easelJsUtils.createBackRudder(vm.colors.background)
            backRightRudderShape.y = 22;
            backRightRudderShape.x = +10;
            backRightRudderShape.scaleX = backRightRudderShape.scaleY = 12/100;
            
			var mainLeftBackGaugeShape = easelJsUtils.createBackGauge(vm.colors.background);
			mainLeftBackGaugeShape.y = 2;
			mainLeftBackGaugeShape.x = -10;
			mainLeftBackGaugeShape.scaleX = 0.23;
			mainLeftBackGaugeShape.scaleY = 0.3;
			mainLeftBackGaugeShape.rotation = 270;

			var mainRightBackGaugeShape = easelJsUtils.createBackGauge(vm.colors.background);
			mainRightBackGaugeShape.y = 2;
			mainRightBackGaugeShape.x = +10;
			mainRightBackGaugeShape.scaleX = 0.23;
			mainRightBackGaugeShape.scaleY = 0.3;
			mainRightBackGaugeShape.rotation = 270;

			propulsionBackContainer.addChild(vesselShape, backLeftRudderShape, backRightRudderShape, mainLeftBackGaugeShape, mainRightBackGaugeShape);
        }
        
        function updatePropulsionFrontContainer() {
        	propulsionFrontContainer.removeAllChildren();        	
            var rudderColor = vm.colors.rudder;
			if(Math.abs(Math.round(vm.angle.value))>0) {
				rudderColor = vm.angle.value > 0 ? vm.colors.red : vm.colors.green;
			}
            
            var leftRudderShape = easelJsUtils.createRudder(rudderColor);
            leftRudderShape.y = 22;
            leftRudderShape.x = -10;
            leftRudderShape.scaleX = leftRudderShape.scaleY = 12/100;
            leftRudderShape.rotation = vm.angle.value;
            
            var rightRudderShape = easelJsUtils.createRudder(rudderColor);
            rightRudderShape.y = 22;
            rightRudderShape.x = +10;
            rightRudderShape.scaleX = rightRudderShape.scaleY = 12/100;
            rightRudderShape.rotation = vm.angle.value;
            
            var mainLeftGaugeShape = easelJsUtils.createGaugge(vm.colors.tick, vm.colors.zero, 
            		vm.mainLeft.value*100/150, vm.colors.forward, vm.colors.backward);
            mainLeftGaugeShape.y = 2;
            mainLeftGaugeShape.x = -10;
			mainLeftGaugeShape.scaleX = 0.23;
			mainLeftGaugeShape.scaleY = 0.3;
			mainLeftGaugeShape.rotation = 270;

            var mainRightGaugeShape = easelJsUtils.createGaugge(vm.colors.tick, vm.colors.zero, 
            		vm.mainRight.value*100/150, vm.colors.forward, vm.colors.backward);
            mainRightGaugeShape.y = 2;
            mainRightGaugeShape.x = +10;
            mainRightGaugeShape.scaleX = 0.23;
			mainRightGaugeShape.scaleY = 0.3;
			mainRightGaugeShape.rotation = 270;
			            
			var arrowColor = vm.colors.rudder;
			if((vm.frontPropAngle.value <= 180) && (vm.frontPropAngle.value >=0)) {
				arrowColor = vm.colors.green;
			} else {
				arrowColor = vm.colors.red;
			}
            var frontTransArrowShape = easelJsUtils.createArrow(
            		arrowColor, vm.frontTrans.value/2.0);
			frontTransArrowShape.y = -31.5;
			frontTransArrowShape.x = 0;
			frontTransArrowShape.scaleX = frontTransArrowShape.scaleY = 0.1;
			frontTransArrowShape.rotation = vm.frontPropAngle.value;
			            
            propulsionFrontContainer.addChild(leftRudderShape, rightRudderShape, mainLeftGaugeShape, mainRightGaugeShape, frontTransArrowShape);
        }
        

        function updatePropulsionLabels() {
            propulsionCanvasWidth = propulsionCanvas.offsetWidth;
            propulsionCanvasHeight = propulsionCanvas.offsetHeight;

            var canvasX = propulsionCanvas.offsetLeft;
            var canvasY = propulsionCanvas.offsetTop;
        	
            var delta = propulsionCanvasHeight*5/100;
            
            vm.frontTrans.x = canvasX + 0.1*propulsionCanvasWidth/2;
            vm.frontTrans.y = canvasY + propulsionCanvasHeight*(50 - 38)/100;

            vm.frontPropAngle.x = vm.frontTrans.x;
            vm.frontPropAngle.y = canvasY + propulsionCanvasHeight*(50 - 25)/100;
        
            vm.mainLeft.x = vm.frontTrans.x;
            vm.mainLeft.y = canvasY + propulsionCanvasHeight*(50 + 0)/100;

            vm.mainRight.x = canvasX + 1.4*propulsionCanvasWidth/2;
            vm.mainRight.y = vm.mainLeft.y

            vm.angle.x = canvasX + 1*propulsionCanvasWidth/2;
            vm.angle.y = canvasY + propulsionCanvasHeight*(50 + 37)/100;
        }
        
        
        /**------------------------------ DRAW METHODS -------------------------------**/

        /** Initialize the canvas **/
        function initWindCanvas() {
            windCanvas = document.getElementById(vm.windCanvasId);
            if (windCanvas != null) {
                windStage = new createjs.Stage(windCanvas);
                drawBoat();
                $scope.$watch(function () {
                    return windCanvas.offsetHeight
                }, function () {
                    updateWindCanvasSize();
                });
                $scope.$watch(function () {
                    return windCanvas.offsetWidth
                }, function () {
                    updateWindCanvasSize();
                });
                updateWindCanvasSize();
            }
        }

        /** Create the stage if needed, reset the canvas and redraw all the shapes **/
        function updateWindCanvasSize() {
            if (windCanvas != null) {
                windCanvasSize = windCanvas.offsetWidth < windCanvas.offsetHeight ? windCanvas.offsetWidth : windCanvas.offsetHeight;
                windCanvasWidth = windCanvas.offsetWidth;
                windCanvasHeight = windCanvas.offsetHeight;

                //Canvas size properties setting
                windCanvas.setAttribute('width', windCanvasWidth);
                windCanvas.setAttribute('height', windCanvasHeight);

                // Reset canvas
                windStage.removeAllChildren();

                // Redraw shapes
                drawCompass();
                drawBoat();
                if (trueWindShape != null)
                    trueWindShape = drawWindFlag(trueWindShape.speed, trueWindColor, trueWindShape.rotation + 90);
                if (relWindShape != null)
                    relWindShape = drawWindFlag(relWindShape.speed, relWindColor, relWindShape.rotation + 90);
                windStage.update();
            }
        }

        /** Draw the boat and its heading **/
        function drawBoat() {
            // Create shape and set properties (color, size...)
            boatShape = new createjs.Shape();
            boatShape.x = windCanvasWidth / 2;
            boatShape.y = windCanvasHeight / 2;
            boatShape.rotation = angular.isDefined(vm.heading.value) ? vm.heading.value - 90 : -90;
            boatShape.graphics.beginStroke('#757575').setStrokeStyle(1, 'round');
            windStage.addChild(boatShape);

            boatHeadingShape = new createjs.Shape();
            boatHeadingShape.x = windCanvasWidth / 2;
            boatHeadingShape.y = windCanvasHeight / 2;
            boatHeadingShape.rotation = boatShape.rotation;
            boatHeadingShape.graphics.beginStroke('#757575').setStrokeStyle(1).setStrokeDash([10, 5]);
            windStage.addChild(boatHeadingShape);

            var boatLength = windCanvasSize * 0.5;
            var boatWidth = boatLength / 4;

            //Heading line
            boatHeadingShape.graphics.moveTo(-boatLength * (3 / 4), 0);
            boatHeadingShape.graphics.lineTo(boatLength * 3 / 4, 0);
            //Boat
            boatShape.graphics.moveTo(boatLength / 5, boatWidth / 2)
                .lineTo(-boatLength / 2, boatWidth / 2)
                .lineTo(-boatLength / 2, -boatWidth / 2)
                .lineTo(boatLength / 5, -boatWidth / 2)
                .bezierCurveTo(boatLength / 1.5, -boatWidth / 4, boatLength / 1.5, boatWidth / 4, boatLength / 5, boatWidth / 2);
        }

        /** Draw wind shape **/
        function drawWindFlag(speed, color, angle) {
            var shape = new createjs.Shape();
            shape.x = windCanvasWidth / 2;
            shape.y = windCanvasHeight / 2;
            shape.rotation = angle - 90;
            shape.speed = speed;
            windStage.addChild(shape);
            shape.graphics.beginStroke(color).setStrokeStyle(2);
            var flagLength = windCanvasSize / 3;

            if (speed === 0) {//if the true wind is null
                shape.graphics.drawCircle(0, 0, 20);
            }
            else if (speed > 200) {//if the true wind is too big
                vm.logger.error("Error true wind speed too high");
            }
            else {//if the true wind is not null and not too big
                shape.graphics.moveTo(0, 0);
                shape.graphics.lineTo(flagLength, 0);
                shape.graphics.beginFill(color);
                drawBarb(shape, speed, flagLength);
            }
            return shape;
        }

        /** function used to draw the pennants and the flags on the wind barb (hampe de vent) **/
        function drawBarb(shape, windSpeed, flagLength) {
            var currentPosition = flagLength; // virtual mark in order to know where to put pennants,flags and half-flags
            var pennant = Math.floor(windSpeed / 50);           // for each 50 knots we add one pennant
            var flag = Math.floor((windSpeed % 50) / 10);       // for each 10 knots we add one flag (max 4)
            var halfFlag = Math.floor((windSpeed % 10) / 5);    // for each 5 knots we add one half-flag (max 1)

            //Draw pennants
            for (var i = 0; i < pennant; i++) {
                shape.graphics.moveTo(currentPosition, 0).lineTo(currentPosition - 12, 20).lineTo(currentPosition - 20, 0);
                currentPosition -= 20;
            }
            if (pennant !== 0)
                currentPosition -= 10;
            //Draw flags
            for (i = 0; i < flag; i++) {
                shape.graphics.moveTo(currentPosition, 0).lineTo(currentPosition + 8, 20);
                currentPosition -= 10;
            }
            //Draw half flags
            for (i = 0; i < halfFlag; i++) {
                shape.graphics.moveTo(currentPosition, 0).lineTo(currentPosition + 4, 10);
                currentPosition -= 10;
            }
        }

        /** Draw the compass **/
        function drawCompass() {
            var compassShape = new createjs.Shape();
            compassShape.x = windCanvasWidth / 2;
            compassShape.y = windCanvasHeight / 2;
            windStage.addChild(compassShape);
            compassShape.graphics.beginStroke('#757575').setStrokeStyle(3);
            var radius = windCanvasSize / 2 - 20;

            for (var i = 0; i < 24; i++) {
                var angle = i * Math.PI / 12;
                var strokeLength = i % 3 === 0 ? 7 : 2;
                //Draw text
                if (i % 2 === 0 && i % 6 !== 0) {
                    var xPosition = (radius + strokeLength + 20) * Math.cos(angle) - 6; //offset to adjust the text position
                    var yPosition = (radius + strokeLength + 20) * Math.sin(angle);
                    var text = ((i + 6) % 24) * 360 / 24;
                    xPosition -= 10; //offset to adjust the text position
                    drawText(text, xPosition, yPosition);
                }
                //Draw stroke
                compassShape.graphics.moveTo((radius - strokeLength) * Math.cos(angle), (radius - strokeLength) * Math.sin(angle));
                compassShape.graphics.lineTo((radius + strokeLength) * Math.cos(angle), (radius + strokeLength) * Math.sin(angle));

            }
        }

        /** Draw a text at a specific position **/
        function drawText(text, x, y) {
            var textShape = new createjs.Text(text, "10px Arial", '#757575');
            textShape.x = windCanvasWidth / 2 + x;
            textShape.y = windCanvasHeight / 2 + y;
            textShape.textBaseline = "middle";
            return windStage.addChild(textShape);

        }

        
    }
})();

