(function () {
    'use strict';

    /** Component declaration **/
    (function () {
        'use strict';
        angular.module('tsa.tsaViews.multibeam', []).component('tsaMultibeam', {
            templateUrl: "app/tsa-views/multibeam/multibeam.html",
            controllerAs: "ctrl",
            controller: multibeamController,
            bindings: {
                config: '=',
                cardId: '@',
                onChangeCardType: '&'
            }
        });
    })();

    /** Controller */
    function multibeamController($interval, $timeout, $scope, $log, MULTIBEAM, acquisitionService, statusService, themeService, graphUtils) {
        var vm = this;
        vm.logger = $log.getInstance("multibeam-tsa-view");

        /**------------------------------ PROPERTIES -------------------------------**/

        vm.canvasId = "canvas-mb-" + vm.cardId;
        var dataMissing = true;
        var lastFrameTimestamp;
        var canvas;
        var intervalPromise;
        var timeoutPromise;
        var portColor = '#F44336';
        var starboardColor = '#4CAF50';
        var frameListener;

        /** Chart properties **/
        var chart = undefined;
        var chartData = {
            datasets: [{ // PORT
                xAxisID: "x-axis-1",
                yAxisID: "y-axis-1",
                borderColor: portColor,
                data: []
            }, { // STARBORD
                xAxisID: "x-axis-1",
                yAxisID: "y-axis-1",
                borderColor: starboardColor,
                data: []
            }]
        };
        var chartOptions = {
            animation: false,
            maintainAspectRatio: false,
            legend: {
                display: false
            },
            showLines: false,
            tooltips: {
                enabled: false
            },
            elements: {
                point: {
                    radius: 1,
                    borderWidth: 2
                }
            },
            scales: {
                xAxes: [{
                    type: "linear",
                    position: "top",
                    gridLines: {
                        zeroLineColor: "#9E9E9E",
                        color: "#BDBDBD"
                    }
                }],
                yAxes: [
                    {
                        id: "y-axis-1",
                        position: "left",
                        display: true,
                        scaleLabel: {
                            display: true,
                            labelString: 'Depth (m)'
                        },
                        gridLines: {
                            zeroLineColor: "#9E9E9E",
                            color: "#BDBDBD"
                        },
                        afterTickToLabelConversion: function (scale) {
                            angular.forEach(scale.ticks, function (tick, index) {
                                scale.ticks[index] = Math.abs(tick);
                            });
                        },
                        ticks: {
                            suggestedMin: -10
                        }
                    }]
            }
        };

        /**-------------------------- INITIALIZATION / DESTROY METHODS -------------------------**/

        /** Initialization function **/
        vm.$onInit = function init() {

            if (angular.isUndefined(this.config)
                || angular.isUndefined(this.config.deviceId)
                || angular.isUndefined(this.config.frameId)) {
                vm.logger.error('deviceId or/and frameId  not defined!');
                return;
            }

            vm.deviceId = this.config.deviceId;
            vm.frameId = this.config.frameId;

            //Wait the canvas creation, and then create the chart...
            timeoutPromise = $timeout(function () {
                createChart();
                initDataListener();
                initDataWatcher();
                initThemeWatcher();
            }, 500);
        };

        /** Destroy function **/
        vm.$onDestroy = function () {
            if (angular.isDefined(frameListener))
                acquisitionService.registerForDevice(vm.deviceId).unregisterForFrame(frameListener);
            if (angular.isDefined(timeoutPromise))
                $timeout.cancel(timeoutPromise);
            if (angular.isDefined(intervalPromise))
                $interval.cancel(intervalPromise);
        };

        /**------------------------------ PRIVATE METHODS -------------------------------**/

        /** Redraw the chart **/
        function createChart() {
            if (chart !== undefined)
                chart.destroy();

            canvas = document.getElementById(vm.canvasId);
            if (canvas != undefined)
                chart = Chart.Scatter(canvas, { data: chartData, options: chartOptions });
        }

        /** Initialize the data listener **/
        function initDataListener() {
            frameListener = acquisitionService.registerForDevice(vm.deviceId).registerForFrame(vm.frameId);
            frameListener.promise.then({}, {}, function (frame) {
                if (dataMissing) {
                    dataMissing = false;
                    chartData.datasets[0].borderColor = portColor;
                    chartData.datasets[1].borderColor = starboardColor;
                }

                lastFrameTimestamp = frame.timestamp;

                var minAcross, maxAcross, verticalDepth;
                var acrossValues = frame.value[MULTIBEAM.ACROSS];
                vm.nbValue = frame.value[MULTIBEAM.DEPTH].length;

                var i = 0;
                chartData.datasets[0].data = [];
                chartData.datasets[1].data = [];
                angular.forEach(frame.value[MULTIBEAM.DEPTH], function (value) {
                    var acrossValue = parseFloat(acrossValues[i]);

                    if (acrossValue < 0)
                        chartData.datasets[0].data.push({ x: acrossValue, y: -parseFloat(value) });
                    else chartData.datasets[1].data.push({ x: acrossValue, y: -parseFloat(value) });

                    if (angular.isUndefined(minAcross) || acrossValue < minAcross) {
                        minAcross = acrossValue;
                        verticalDepth = parseFloat(value);
                    }

                    if (angular.isUndefined(maxAcross) || acrossValue > maxAcross)
                        maxAcross = acrossValue;

                    i++;
                });

                if (angular.isDefined(minAcross) && angular.isDefined(maxAcross)) {
                    vm.swathValue = maxAcross - minAcross;
                    vm.verticalDepth = verticalDepth;

                    //TODO: must be tested
                    var maxAbs = Math.max(Math.abs(minAcross), Math.abs(maxAcross));
                    chart.chart.config.options.scales.xAxes[0].ticks.suggestedMin = -maxAbs;
                    chart.chart.config.options.scales.xAxes[0].ticks.suggestedMax = maxAbs;
                }

                chart.update();
            });

        }

        /** Check the data reception **/
        function initDataWatcher() {
            intervalPromise = $interval(function () {
                var devicePeriod = acquisitionService.registerForDevice(vm.deviceId).periodList[vm.frameId];
                if (angular.isUndefined(devicePeriod))
                    devicePeriod = 1000;
                if (angular.isDefined(lastFrameTimestamp) &&
                    statusService.getServerTime().time - lastFrameTimestamp > 5 * devicePeriod) {
                    dataMissing = true;
                    chartData.datasets[0].borderColor = "#BDBDBD";
                    chartData.datasets[1].borderColor = "#BDBDBD";
                    chart.update();
                }
            }, 1000);
        }

        /** Change gridLinesColor of Graph on every change of service variable  **/
        function initThemeWatcher() {
            themeService.subscribe($scope, function(event, theme) {
                chartOptions.scales.xAxes[0].gridLines.color = theme.gridLinesColor;
                chartOptions.scales.yAxes[0].gridLines.color = theme.gridLinesColor;
                createChart();
            });
        }
    }
})();

(function () {
    'use strict';

    /** Navigation component **/
    angular.module('tsa.tsaViews.navigation', []).component('tsaNavigation', {
        templateUrl: "app/tsa-views/navigation/navigation.html",
        controllerAs: "ctrl",
        controller: navigationController,
        bindings: {
            config: '=',
            cardId: '@',
            onChangeCardType: '&'
        }
    });

    /** Navigation controller */
    function navigationController($log, acquisitionService, NAVIGATION, WAYPOINTS, SHIP_ID, statusService) {
        var vm = this;
        vm.logger = $log.getInstance("navigation-tsa-view");

        /** Initialization function */
        vm.$onInit = function init() {
            if (angular.isUndefined(this.config) || angular.isUndefined(this.config.deviceId) || angular.isUndefined(this.config.frameId)) {
                vm.logger.error('config and/or deviceId and/or frameId not defined!');
                return;
            }
            vm.MODE = { NAVIGATION: 'navigation', STATION: 'station' };
            vm.currentMode = vm.MODE.NAVIGATION;

            vm.shipId = SHIP_ID;
            vm.isMD = ('MD' === SHIP_ID);
            vm.currentWptName = '';

            vm.statusService = statusService;

            vm.deviceId = this.config.deviceId;
            vm.frameId = this.config.frameId;

            vm.latitude = NAVIGATION.LATITUDE;
            vm.longitude = NAVIGATION.LONGITUDE;
            vm.heading = NAVIGATION.HEADING;
            vm.sounder = NAVIGATION.SOUNDER;
            vm.groundSpeed = NAVIGATION.GROUND_SPEED;
            vm.groundCourse = NAVIGATION.GROUND_COURSE;
            vm.surfaceSpeed = NAVIGATION.SURFACE_SPEED;
            vm.surfaceCourse = NAVIGATION.SURFACE_COURSE;
            vm.driftSpeed = NAVIGATION.DRIFT_SPEED;
            vm.driftCourse = NAVIGATION.DRIFT_COURSE;

            vm.frameRoutingId = NAVIGATION.FRAME_ROUTING;
            vm.curDist = NAVIGATION.CUR_DIST;
            vm.curCourse = NAVIGATION.CUR_COURSE;
            vm.prevDist = NAVIGATION.PREV_DIST;
            vm.prevCourse = NAVIGATION.PREV_COURSE;
            vm.eta = NAVIGATION.ETA;

            vm.dataViewSize = 0.8;
            vm.dataFormat = '%.1f';
            vm.angleFormat = '%.0f';
            vm.positionFormat = 'coordinate';

            var waypointsFrameListener = acquisitionService.registerForDevice(vm.deviceId).registerForFrame(WAYPOINTS.FRAME);
            waypointsFrameListener.promise.then({}, {}, parseWaypoints);
            function parseWaypoints(receivedFrame) {
                var wptsLat = receivedFrame.value[WAYPOINTS.LATITUDE].map(parseFloat);
                var wptsLon = receivedFrame.value[WAYPOINTS.LONGITUDE].map(parseFloat);
                var wptsId = receivedFrame.value[NAVIGATION.ID];
                var wptsIndex = receivedFrame.value[NAVIGATION.INDEX].map(parseInt);

                //Profil mode
                if (wptsLat.lenght != 1 || wptsLon.lenght != 1) {
                    vm.currentMode = vm.MODE.NAVIGATION;
                }
                else vm.currentMode = vm.MODE.STATION;

                if(wptsIndex.length && (wptsId.length >= wptsIndex[0])) {
                    vm.currentWptName = wptsId[wptsIndex[0]-1];
                }
            }

        };
    }
})();

(function () {
    'use strict';
    angular.module('tsa.tsaViews.numerical', ['tsa.services.filters']).component('tsaNumerical', {
        templateUrl: "app/tsa-views/numerical/numerical.html",
        controller: numericalController,
        controllerAs: "numerical",
        bindings: {
            cardId: '@',
            config: '=',
            onChangeCardType: '&'
        }
    });

    /** Controller **/
    function numericalController(acquisitionService, $mdDialog, dialogService) {
        var vm = this;

        /** -------------------  PROPERTIES -------------------- */
        vm.itemList = [];
        vm.editionMode = false;
        
        /*** -------------------  Item class: represents a item line --------------------*/

        function Item(deviceId, frameId, dataId, title) {
            /*** Properties ***/
            this.deviceId = deviceId;
            this.frameId = frameId;
            this.dataId = dataId;
            this.title = title;

            this.frameDataId = function (newFrameDataId) {
                if (arguments.length) {
                    this.frameId = newFrameDataId.split('!')[0];
                    this.dataId = newFrameDataId.split('!')[1];
                }
                return this.frameId + '!' + this.dataId;
            };

            this.remove = function remove() {
                vm.itemList.splice(vm.itemList.indexOf(this), 1);
                saveConfig();
            };

            this.up = function up() {
                var currentIndex = vm.itemList.indexOf(this);
                var nextIndex = currentIndex - 1;
                if (nextIndex < 0)
                    return;
                var tmp = vm.itemList[nextIndex];
                vm.itemList[currentIndex] = tmp;
                vm.itemList[nextIndex] = this;
                saveConfig();
            };

            this.down = function down() {
                var currentIndex = vm.itemList.indexOf(this);
                var nextIndex = currentIndex + 1;
                if (nextIndex > vm.itemList.length - 1)
                    return;
                var tmp = vm.itemList[nextIndex];
                vm.itemList[nextIndex] = this;
                vm.itemList[currentIndex] = tmp;
                saveConfig();
            }
        }

        /*** -------------------  PRIVATE METHODS --------------------*/

        /** Initialization function **/
        vm.$onInit = function init() {
            loadConfig();
        };

        /** Save the current configuration in the vm.config property **/
        function saveConfig() {
            vm.config = {};
            vm.config.type = 'Numerical';
            vm.config.dataList = [];
            angular.forEach(vm.itemList, function (item) {
                vm.config.dataList.push({
                    deviceId: item.deviceId,
                    frameId: item.frameId,
                    dataId: item.dataId,
                    title: item.title !== undefined ? item.title : ""
                });
            }, vm);
        }

        /** Restore the current configuration from the vm.config property **/
        function loadConfig() {
            if (vm.config !== undefined && vm.config.type === 'Numerical') {
                angular.forEach(vm.config.dataList, function (data) {
                    vm.itemList.push(new Item(data.deviceId, data.frameId, data.dataId, data.title));
                });
            }
        }

        /** -------------------  VIEW-MODEL METHODS -------------------- */

        /** Show "Add data" dialog box **/
        vm.addItem = function addItem() {
            dialogService.showSelectData(acquisitionService.getEnabledDeviceList())
                .then(function (dataList) {
                    angular.forEach(dataList, function (data) {
                        vm.itemList.push(new Item(data.deviceId, data.frameId, data.id));
                    });
                    saveConfig();
                });
        };
    }

})();

