(function () {
    'use strict';
    
    /** Propulsion component **/
    angular.module('tsa.tsaViews.propulsionAt', []).component('tsaPropulsionAt', {
        templateUrl: "app/tsa-views/propulsion-at/propulsion-at.html",
        controllerAs: "ctrl",
        controller: propulsionControllerAt,
        bindings: {
            config: '=',
            cardId: '@',
            onChangeCardType: '&'
        }
    });

    /** Propulsion controller **/
    function propulsionControllerAt(acquisitionService, $timeout, $scope, easelJsUtils, themeService, NAVIGATION) {
        var vm = this;
        var TAG = '[propulsionControllerAt] ';

        /**------------------------------ PROPERTIES -------------------------------**/

        //Canvas
        vm.canvasId = "canvas-propulsion-at-" + vm.cardId;

        var canvas;
        var stage;
        var backContainer;
        var frontContainer;
        
        var canvasWidth;
        var canvasHeight;
       
        vm.readyToDraw = false;

        vm.colors = {
        	background: '#CCCCCC',
        	zero: '#EE2222',
        	tick: '#888888',
        	rudder: '#000000',
        	green: '#11CC11',
        	red: '#EE2222',
        	forward: '#1111DD',
        	backward: '#000000',
        	funkyBackground: '#F3BF52'
        };
        
        vm.themeChanged = function themeChanded(event, theme) {
        	console.log("propulsion at themechanged");
        }
        
        themeService.subscribe($scope, vm.themeChanged);
        
        /**------------------------------ METHODS -------------------------------**/

        /** Initialization function **/
        vm.$onInit = function init() {
            console.log(TAG + '$onInit: deviceId = ' + (this.config ? this.config.deviceId : 'config undefined'));

            if (this.config == undefined || this.config.deviceId == undefined) {
                console.error(TAG + 'device Id not found!');
                return;
            }

            vm.deviceId = this.config.deviceId; //Component parameter (mandatory)
            vm.navId = this.config.navId; 
            vm.device = acquisitionService.registerForDevice(vm.deviceId);
            
            vm.frameId = 'atpropulsion';
            vm.navFrameId = 'shipnav';

            //Create object to add x and y properties
            vm.frontTrans = { id: 'fronttrans' };
            vm.frontPropAngle = { id: 'frontpropangle' };
            vm.mainLeft = { id: 'mainleft' };
            vm.mainRight= {id: 'mainright' };
            vm.angle = {id: 'angle' };
            vm.fontsize = 1;

            vm.frameListener = acquisitionService.registerForDevice(vm.deviceId).registerForFrame(vm.frameId);
            vm.frameListener.promise.then({}, {}, function (frame) {
            	vm.frontTrans.value = frame.value[vm.frontTrans.id];
            	vm.frontPropAngle.value = frame.value[vm.frontPropAngle.id];
            	vm.mainLeft.value = frame.value[vm.mainLeft.id];
            	vm.mainRight.value = frame.value[vm.mainRight.id];
            	vm.angle.value = frame.value[vm.angle.id];

            	if(vm.readyToDraw) {
                    updateFrontContainer();
            		stage.update();
            	}
            });

            vm.heading		= { id: 'heading'};
            vm.gndCourse	= { id: 'gndcourse'};
            vm.gndSpeed		= { id: 'gndspeed'};
            vm.surfCourse	= { id: 'surfcourse'};
            vm.surfSpeed	= { id: 'surfspeed'};
            
            vm.surfNorth = vm.surfWest = vm.surfEast = vm.surfSouth = undefined;
            vm.gndNorth = vm.gndWest = vm.gndEast = vm.gndSouth = undefined;
            
            vm.navFrameListener = acquisitionService.registerForDevice(vm.navId).registerForFrame('shipnav');
            vm.navFrameListener.promise.then({}, {}, function (frame) {
            	vm.heading.value = frame.value[vm.heading.id];
            	vm.gndCourse.value = frame.value[vm.gndCourse.id];
            	vm.gndSpeed.value = frame.value[vm.gndSpeed.id];
            	vm.surfCourse.value = frame.value[vm.surfCourse.id];
            	vm.surfSpeed.value = frame.value[vm.surfSpeed.id];

            	var surfA = vm.surfSpeed.value * Math.cos((vm.surfCourse.value-vm.heading.value)*Math.PI/180.0);
            	if(surfA>=0) {
            		vm.surfNorth = surfA;
            		vm.surfSouth = undefined;
            	} else {
            		vm.surfNorth = undefined;
            		vm.surfSouth = -surfA;
            	}
            	
            	var surfB = vm.surfSpeed.value * Math.sin((vm.surfCourse.value-vm.heading.value)*Math.PI/180.0);
            	if(surfB<0) {
            		vm.surfWest = -surfB;
            		vm.surfEast = undefined;
            	} else {
            		vm.surfWest = undefined;
            		vm.surfEast = surfB;
            	}
            	
            	var gndA = vm.gndSpeed.value * Math.cos((vm.gndCourse.value-vm.heading.value)*Math.PI/180.0);
            	if(gndA>=0) {
            		vm.gndNorth = gndA;
            		vm.gndSouth = undefined;
            	} else {
            		vm.gndNorth = undefined;
            		vm.gndSouth = -gndA;
            	}
            	
            	var gndB = vm.gndSpeed.value * Math.sin((vm.gndCourse.value-vm.heading.value)*Math.PI/180.0);
            	if(gndB<0) {
            		vm.gndWest = -gndB;
            		vm.gndEast = undefined;
            	} else {
            		vm.gndWest = undefined;
            		vm.gndEast = gndB;
            	}
            	
            	if(vm.readyToDraw) {
//                    updateFrontContainer();
//            		stage.update();
            	}
            });


            $timeout(function () {
                initCanvas();
                vm.readyToDraw = true;
            }, 500); 

        };

        /** Destroy function: remove the data listeners */
        vm.$onDestroy = function destroy() {
            if (angular.isDefined(vm.frameListener)) {
                acquisitionService.registerForDevice(vm.deviceId).unregisterForFrame(vm.frameListener);
                acquisitionService.registerForDevice(vm.navId).unregisterForFrame(vm.navFrameListener);
            }
        }
        
        /**Initialize the canvas **/
        function initCanvas() {
            canvas = document.getElementById(vm.canvasId);
            if (canvas != null) {
                stage = new createjs.Stage(canvas);
                
                backContainer = new createjs.Container();
                frontContainer = new createjs.Container();
                
                stage.addChild(backContainer, frontContainer);
                
                $scope.$watch(function () {
                    return canvas.offsetHeight
                }, function () {
                    updateCanvasSize();
                });
                $scope.$watch(function () {
                    return canvas.offsetWidth
                }, function () {
                    updateCanvasSize();
                });
                updateCanvasSize();
            }
        }

        /** Create the stage if needed, reset the canvas and redraw all the shapes **/
        function updateCanvasSize() {
            if (canvas != null) {
                canvasWidth = canvas.offsetWidth;
                canvasHeight = canvas.offsetHeight;

                //Canvas size properties setting
                canvas.setAttribute('width', canvasWidth);
                canvas.setAttribute('height', canvasHeight);

                angular.element(canvas).parent().find("tsa-data-view .value").css({
                	"font-size": ""+canvasHeight/220+"em"
        		}); 
                
                angular.element(canvas).parent().find("tsa-data-view").css({
                	"width": ""+Math.round(canvasHeight/3.5)+"px !important",
                	"max-width": ""+Math.round(canvasHeight/3.5)+"px",
                }); 

                stage.x = canvasWidth/2;
                stage.y = canvasHeight/2;
            	var scale = canvasHeight/100;
            	stage.scaleX = stage.scaleY = scale;
            	                
                updateBackContainer();
                backContainer.cache(-canvasWidth/2, -canvasHeight/2, canvasWidth, canvasHeight, scale);
                updateFrontContainer();
                updateLabels();

                stage.update();
            }
        }
        
        
        function updateBackContainer() {
        	backContainer.removeAllChildren();
        	
            var vesselShape = easelJsUtils.createVessel(vm.colors.tick);
            vesselShape.scaleX = 0.3;
            vesselShape.scaleY = 0.65;
            vesselShape.y = -12;
            
            var backLeftRudderShape =  easelJsUtils.createBackRudder(vm.colors.background)
            backLeftRudderShape.y = 22;
            backLeftRudderShape.x = -10;
            backLeftRudderShape.scaleX = backLeftRudderShape.scaleY = 12/100;
            
            var backRightRudderShape =  easelJsUtils.createBackRudder(vm.colors.background)
            backRightRudderShape.y = 22;
            backRightRudderShape.x = +10;
            backRightRudderShape.scaleX = backRightRudderShape.scaleY = 12/100;
            
			var mainLeftBackGaugeShape = easelJsUtils.createBackGauge(vm.colors.background);
			mainLeftBackGaugeShape.y = 2;
			mainLeftBackGaugeShape.x = -10;
			mainLeftBackGaugeShape.scaleX = 0.23;
			mainLeftBackGaugeShape.scaleY = 0.3;
			mainLeftBackGaugeShape.rotation = 270;

			var mainRightBackGaugeShape = easelJsUtils.createBackGauge(vm.colors.background);
			mainRightBackGaugeShape.y = 2;
			mainRightBackGaugeShape.x = +10;
			mainRightBackGaugeShape.scaleX = 0.23;
			mainRightBackGaugeShape.scaleY = 0.3;
			mainRightBackGaugeShape.rotation = 270;

            backContainer.addChild(vesselShape, backLeftRudderShape, backRightRudderShape, mainLeftBackGaugeShape, mainRightBackGaugeShape);
        }
        
        function updateFrontContainer() {
        	frontContainer.removeAllChildren();        	

            var rudderColor = vm.colors.rudder;
			if(Math.abs(Math.round(vm.angle.value))>0) {
				rudderColor = vm.angle.value > 0 ? vm.colors.red : vm.colors.green;
			}
            
            var leftRudderShape = easelJsUtils.createRudder(rudderColor);
            leftRudderShape.y = 22;
            leftRudderShape.x = -10;
            leftRudderShape.scaleX = leftRudderShape.scaleY = 12/100;
            leftRudderShape.rotation = vm.angle.value;
            
            var rightRudderShape = easelJsUtils.createRudder(rudderColor);
            rightRudderShape.y = 22;
            rightRudderShape.x = +10;
            rightRudderShape.scaleX = rightRudderShape.scaleY = 12/100;
            rightRudderShape.rotation = vm.angle.value;
            
            var mainLeftGaugeShape = easelJsUtils.createGaugge(vm.colors.tick, vm.colors.zero, 
            		vm.mainLeft.value*100/150, vm.colors.forward, vm.colors.backward);
            mainLeftGaugeShape.y = 2;
            mainLeftGaugeShape.x = -10;
			mainLeftGaugeShape.scaleX = 0.23;
			mainLeftGaugeShape.scaleY = 0.3;
			mainLeftGaugeShape.rotation = 270;

            var mainRightGaugeShape = easelJsUtils.createGaugge(vm.colors.tick, vm.colors.zero, 
            		vm.mainRight.value*100/150, vm.colors.forward, vm.colors.backward);
            mainRightGaugeShape.y = 2;
            mainRightGaugeShape.x = +10;
            mainRightGaugeShape.scaleX = 0.23;
			mainRightGaugeShape.scaleY = 0.3;
			mainRightGaugeShape.rotation = 270;
			            
			var arrowColor = vm.colors.rudder;
			if((vm.frontPropAngle.value <= 180) && (vm.frontPropAngle.value >=0)) {
				arrowColor = vm.colors.green;
			} else {
				arrowColor = vm.colors.red;
			}
            var frontTransArrowShape = easelJsUtils.createArrow(
            		arrowColor, vm.frontTrans.value/2.0);
			frontTransArrowShape.y = -31.5;
			frontTransArrowShape.x = 0;
			frontTransArrowShape.scaleX = frontTransArrowShape.scaleY = 0.1;
			frontTransArrowShape.rotation = vm.frontPropAngle.value;
			            
            frontContainer.addChild(leftRudderShape, rightRudderShape, mainLeftGaugeShape, mainRightGaugeShape, frontTransArrowShape);
        }
        

        function updateLabels() {
            canvasWidth = canvas.offsetWidth;
            canvasHeight = canvas.offsetHeight;
            var canvasX = canvas.offsetLeft;
            var canvasY = canvas.offsetTop;
        	
            var delta = canvasHeight*5/100;
            
            vm.frontTrans.x = canvasX + 0.1*canvasWidth/2;
            vm.frontTrans.y = canvasY + canvasHeight*(50 - 38)/100;

            vm.frontPropAngle.x = vm.frontTrans.x;
            vm.frontPropAngle.y = canvasY + canvasHeight*(50 - 25)/100;
        
            vm.mainLeft.x = vm.frontTrans.x;
            vm.mainLeft.y = canvasY + canvasHeight*(50 + 0)/100;

            vm.mainRight.x = canvasX + 1.4*canvasWidth/2;
            vm.mainRight.y = vm.mainLeft.y

            vm.angle.x = canvasX + 1*canvasWidth/2;
            vm.angle.y = canvasY + canvasHeight*(50 + 37)/100;
        }
    }
})();

/*
    Propulsion Pp component
 */

(function () {
    'use strict';

    /** Propulsion component **/
    angular.module('tsa.tsaViews.propulsionPp', []).component('tsaPropulsionPp', {
        templateUrl: "app/tsa-views/propulsion-pp/propulsion-pp.html",
        controllerAs: "ctrl",
        controller: propulsionControllerAt,
        bindings: {
            config: '=',
            cardId: '@',
            onChangeCardType: '&'
        }
    });

    /** Propulsion controller **/
    function propulsionControllerAt(acquisitionService, $timeout, $scope, easelJsUtils, themeService, NAVIGATION,
        PP_ENGINE, PP_VDR) {
        var vm = this;
        var TAG = '[propulsionControllerAt] ';

        /**------------------------------ PROPERTIES -------------------------------**/

        //Canvas
        vm.canvasId = "canvas-propulsion-pp-" + vm.cardId;

        vm.DA = {
            'deviceId': 'PP_ENGINE',
            'frameId': PP_ENGINE.FRAME,
            'datas': [
                { 'dataId': PP_ENGINE.DA1_POWER },
                { 'dataId': PP_ENGINE.DA2_POWER },
                { 'dataId': PP_ENGINE.DA3_POWER },
                { 'dataId': PP_ENGINE.DA4_POWER }
            ]
        };

        vm.PROP_VDR = {
            'deviceId': 'PP_VDR',
            'frameId': PP_VDR.FRAME,
            'trans1': { 'id': PP_VDR.TRANS1_PROP, 'value': undefined, 'lastTimeStamp': 0},
            'trans2': { 'id': PP_VDR.TRANS2_PROP, 'value': undefined, 'lastTimeStamp': 0},
            'trans3': { 'id': PP_VDR.TRANS3_PROP, 'value': undefined, 'lastTimeStamp': 0},
            'trans4': { 'id': PP_VDR.TRANS4_PROP, 'value': undefined, 'lastTimeStamp': 0},
            'starboardRudder': { 'id': PP_VDR.RUDDER_STARBOARD, 'value': undefined, 'lastTimeStamp': 0},
            'portRudder': { 'id': PP_VDR.RUDDER_PORT, 'value': undefined, 'lastTimeStamp': 0}
        }

        vm.PROP_ENGINE = {
            'deviceId': 'PP_ENGINE',
            'frameId': PP_ENGINE.FRAME,
            'starboardMain': { 'id': PP_ENGINE.MAIN_STARBOARD, 'value': undefined, 'lastTimeStamp': 0},
            'portMain': { 'id': PP_ENGINE.MAIN_PORT, 'value': undefined, 'lastTimeStamp': 0}
        }

        var canvas;
        var stage;
        var backContainer;
        var frontContainer;

        var canvasWidth;
        var canvasHeight;

        vm.readyToDraw = false;

        vm.colors = {
        	background: '#CCCCCC',
        	zero: '#EE2222',
        	tick: '#888888',
        	rudder: '#000000',
        	green: '#11CC11',
        	red: '#EE2222',
        	forward: '#1111DD',
        	backward: '#000000',
        	funkyBackground: '#F3BF52'
        };

        vm.themeChanged = function themeChanded(event, theme) {
        	console.log("propulsion at themechanged");
        }

        themeService.subscribe($scope, vm.themeChanged);

        /**------------------------------ METHODS -------------------------------**/

        /** Initialization function **/
        vm.$onInit = function init() {
            console.log(TAG + '$onInit: deviceId = ' + (this.config ? this.config.deviceId : 'config undefined'));

            vm.fontsize = 1;

            function updateData(data, frame) {
                if(frame.value.hasOwnProperty(data.id)) {
                    data.value = frame.value[data.id][0];
                    data.lastTimeStamp = (new Date()).getTime();
                }
            }

            vm.PROP_VDR.frameListener =
                acquisitionService.registerForDevice(vm.PROP_VDR.deviceId)
                .registerForFrame(vm.PROP_VDR.frameId);

            vm.PROP_VDR.frameListener.promise.then({}, {},
                function(frame) {
                    updateData(vm.PROP_VDR.trans1, frame);
                    updateData(vm.PROP_VDR.trans2, frame);
                    updateData(vm.PROP_VDR.trans3, frame);
                    updateData(vm.PROP_VDR.trans4, frame);
                    updateData(vm.PROP_VDR.portRudder, frame);
                    updateData(vm.PROP_VDR.starboardRudder, frame);

                    if(vm.readyToDraw) {
                        updateFrontContainer();
                        stage.update();
                    }
               });

            vm.PROP_ENGINE.frameListener =
                acquisitionService.registerForDevice(vm.PROP_ENGINE.deviceId)
                .registerForFrame(vm.PROP_ENGINE.frameId);

            vm.PROP_ENGINE.frameListener.promise.then({}, {},
                function(frame) {
                    updateData(vm.PROP_ENGINE.portMain, frame);
                    updateData(vm.PROP_ENGINE.starboardMain, frame);
                });

            function testTimeOut(data, now) {
                if((now - data.lastTimeStamp) > 5000) {
                    data.value = undefined;
                }
            }

            vm.timeout = setInterval(function() {
                var now = (new Date()).getTime();
                testTimeOut(vm.PROP_VDR.trans1, now);
                testTimeOut(vm.PROP_VDR.trans2, now);
                testTimeOut(vm.PROP_VDR.trans3, now);
                testTimeOut(vm.PROP_VDR.trans4, now);
                testTimeOut(vm.PROP_VDR.portRudder, now);
                testTimeOut(vm.PROP_VDR.starboardRudder, now);
                testTimeOut(vm.PROP_ENGINE.portMain, now);
                testTimeOut(vm.PROP_ENGINE.starboardMain, now);
            }, 1000);

             $timeout(function () {
                 initCanvas();
                 vm.readyToDraw = true;
             }, 500);
        };

        /** Destroy function: remove the data listeners */
        vm.$onDestroy = function destroy() {
            acquisitionService.registerForDevice(vm.PROP_VDR.deviceId).unregisterForFrame(vm.PROP_VDR.frameId);
            acquisitionService.registerForDevice(vm.PROP_ENGINE.deviceId).unregisterForFrame(vm.PROP_ENGINE.frameId);
            clearInterval(vm.timeout);
        }

        /**Initialize the canvas **/
        function initCanvas() {
            canvas = document.getElementById(vm.canvasId);
            if (canvas != null) {
                stage = new createjs.Stage(canvas);

                backContainer = new createjs.Container();
                frontContainer = new createjs.Container();

                stage.addChild(backContainer, frontContainer);

                $scope.$watch(function () {
                    return canvas.offsetHeight
                }, function () {
                    updateCanvasSize();
                });
                $scope.$watch(function () {
                    return canvas.offsetWidth
                }, function () {
                    updateCanvasSize();
                });
                updateCanvasSize();
            }
        }

        /** Create the stage if needed, reset the canvas and redraw all the shapes **/
        function updateCanvasSize() {
            if (canvas != null) {
                canvasWidth = canvas.offsetWidth;
                canvasHeight = canvas.offsetHeight;

                //Canvas size properties setting
                canvas.setAttribute('width', canvasWidth);
                canvas.setAttribute('height', canvasHeight);

                angular.element(canvas).parent().find("tsa-data-view .value").css({
                	"font-size": ""+canvasHeight/220+"em"
        		});

                angular.element(canvas).parent().find("tsa-data-view").css({
                	"width": ""+Math.round(canvasHeight/3.5)+"px !important",
                	"max-width": ""+Math.round(canvasHeight/3.5)+"px",
                });

                stage.x = canvasWidth/2;
                stage.y = canvasHeight/2;
            	var scale = canvasHeight/100;
            	stage.scaleX = stage.scaleY = scale;

                updateBackContainer();
                backContainer.cache(-canvasWidth/2, -canvasHeight/2, canvasWidth, canvasHeight, scale);
                updateFrontContainer();
                updateLabels();

                stage.update();
            }
        }

        function updateBackContainer() {
        	backContainer.removeAllChildren();

            var vesselShape = easelJsUtils.createVessel(vm.colors.tick);
            vesselShape.scaleX = 0.3;
            vesselShape.scaleY = 0.65;
            vesselShape.y = -12;
            backContainer.addChild(vesselShape);

            var backStarboardRudderShape =  easelJsUtils.createBackRudder(vm.colors.background)
            backStarboardRudderShape.y = 22;
            backStarboardRudderShape.x = -10;
            backStarboardRudderShape.scaleX = backStarboardRudderShape.scaleY = 12/100;
            backContainer.addChild(backStarboardRudderShape);

            var backPortRudderShape =  easelJsUtils.createBackRudder(vm.colors.background)
            backPortRudderShape.y = 22;
            backPortRudderShape.x = +10;
            backPortRudderShape.scaleX = backPortRudderShape.scaleY = 12/100;
            backContainer.addChild(backPortRudderShape);

			var mainStarboardBackGaugeShape = easelJsUtils.createBackGauge(vm.colors.background);
			mainStarboardBackGaugeShape.y = 2;
			mainStarboardBackGaugeShape.x = -35;
			mainStarboardBackGaugeShape.scaleX = 0.23;
			mainStarboardBackGaugeShape.scaleY = 0.3;
			mainStarboardBackGaugeShape.rotation = 270;
            backContainer.addChild(mainStarboardBackGaugeShape);

			var mainPortBackGaugeShape = easelJsUtils.createBackGauge(vm.colors.background);
			mainPortBackGaugeShape.y = 2;
			mainPortBackGaugeShape.x = +35;
			mainPortBackGaugeShape.scaleX = 0.23;
			mainPortBackGaugeShape.scaleY = 0.3;
            mainPortBackGaugeShape.rotation = 270;
            backContainer.addChild(mainPortBackGaugeShape);
        }

        function updateFrontContainer() {
         	frontContainer.removeAllChildren();

            function updateRudder(value, port) {
                if(angular.isDefined(value)) {
                    var rudderColor = vm.colors.rudder;
                    if(Math.abs(Math.round(value))>0) {
                        rudderColor = value > 0 ? vm.colors.green : vm.colors.red;
                    }

                    var rudderShape = easelJsUtils.createRudder(rudderColor);
                    rudderShape.y = 22;
                    rudderShape.x = port ? 10 : -10;
                    rudderShape.scaleX = rudderShape.scaleY = 12/100;
                    rudderShape.rotation = -value;
                    frontContainer.addChild(rudderShape);
                }
            }

            updateRudder(vm.PROP_VDR.starboardRudder.value, false);
            updateRudder(vm.PROP_VDR.portRudder.value, true)

            function updateArrow(value, y) {
                if(angular.isDefined(value)) {
                    if(Math.abs(Math.round(value))>0) {
                        var arrowColor = value > 0 ? vm.colors.green : vm.colors.red;
                        var arrowShape = easelJsUtils.createArrow(arrowColor, Math.abs(value));
                        arrowShape.y = y;
                        arrowShape.x = 0;
                        arrowShape.scaleX = arrowShape.scaleY = 0.1;
                        arrowShape.rotation = value > 0 ? 90 : 270;
                        frontContainer.addChild(arrowShape);
                    }
                }
            }

            updateArrow(vm.PROP_VDR.trans1.value, -38);
            updateArrow(vm.PROP_VDR.trans2.value, -25);
            updateArrow(vm.PROP_VDR.trans3.value, -12);
            updateArrow(vm.PROP_VDR.trans4.value, 10);

            function updateMain(value, x) {
                var mainShape = easelJsUtils.createGaugge(vm.colors.tick, vm.colors.zero,
            		value, vm.colors.forward, vm.colors.backward);
                    mainShape.y = 2;
                    mainShape.x = x;
                    mainShape.scaleX = 0.23;
                    mainShape.scaleY = 0.3;
                    mainShape.rotation = 270;
                    frontContainer.addChild(mainShape);
            }

            updateMain(vm.PROP_ENGINE.portMain.value, 35);
            updateMain(vm.PROP_ENGINE.starboardMain.value, -35);
        }


         function updateLabels() {
            canvasWidth = canvas.offsetWidth;
            canvasHeight = canvas.offsetHeight;
            var canvasX = canvas.offsetLeft;
            var canvasY = canvas.offsetTop;

            var delta = canvasHeight*5/100;

            vm.PROP_VDR.trans1.x = canvasX + 1.6*canvasWidth/2;
            vm.PROP_VDR.trans1.y = canvasY + canvasHeight*(50 - 38)/100;

            vm.PROP_VDR.trans2.x = canvasX + 1.6*canvasWidth/2;
            vm.PROP_VDR.trans2.y = canvasY + canvasHeight*(50 - 25)/100;

            vm.PROP_VDR.trans3.x = canvasX + 1.6*canvasWidth/2;
            vm.PROP_VDR.trans3.y = canvasY + canvasHeight*(50 - 12)/100;

            vm.PROP_VDR.trans4.x = canvasX + 1.6*canvasWidth/2;
            vm.PROP_VDR.trans4.y = canvasY + canvasHeight*(50 + 10)/100;

            vm.PROP_VDR.portRudder.x = canvasX + 1.05*canvasWidth/2;
            vm.PROP_VDR.portRudder.y = canvasY + canvasHeight*(50 +44)/100;

            vm.PROP_VDR.starboardRudder.x = canvasX + 0.60*canvasWidth/2;
            vm.PROP_VDR.starboardRudder.y = canvasY + canvasHeight*(50 +44)/100;

            vm.PROP_ENGINE.portMain.x = canvasX + 1.3*canvasWidth/2;
            vm.PROP_ENGINE.portMain.y = canvasY + canvasHeight*(50 + 30)/100;

            vm.PROP_ENGINE.starboardMain.x = canvasX + 0.4*canvasWidth/2;
            vm.PROP_ENGINE.starboardMain.y = canvasY + canvasHeight*(50 +30)/100;
        }
    }
})();

