(function () {
    'use strict';
    angular.module('tsa.tsaViews.thermoConfig', []).component('tsaThermoConfig', {
        templateUrl: "app/tsa-views/thermoConfig/thermoConfig.html",
        controllerAs: "ctrl",
        controller: thermoConfigController,
        bindings: {
            onChangeCardType: '&',
            cardId: '@'
        }
    });

    function thermoConfigController($log, $cookies, FileUploader) {
        var vm = this;

        /**-------------------- PROPERTIES ----------------**/
        vm.view = 'General';
        vm.thermo = {};
        vm.instrument = {};
        vm.intakeTemperatureSensor = {};
        vm.conductivitySensor = {};
        vm.temperatureSensor = {};

        /**-------------------- PUBLIC METHODS ----------------**/

        vm.init = function init() {
            vm.logger = $log.getInstance("ThermoConfig");
            var csrf = $cookies.csrftoken;
            csrf = $cookies.get("XSRF-TOKEN");
            vm.uploader = new FileUploader(
                {url: 'api/files', headers: {'X-XSRF-TOKEN': csrf}}
            );
            vm.uploader.onSuccessItem = function (item, response, status, headers) {
                vm.logger.log(response);
                vm.instrument = response.instrument;
                vm.temperatureSensor = vm.instrument.sensorArray.sensor[0].temperatureSensor;
                vm.conductivitySensor = vm.instrument.sensorArray.sensor[1].conductivitySensor;
                vm.intakeTemperatureSensor = vm.instrument.sensorArray.sensor[2].temperatureSensor;
            };
            vm.uploader.onAfterAddingFile = function (item, filter, options) {
                if (vm.uploader.queue.length > 1) {
                    vm.uploader.queue.splice(0, 1);
                }
            };
        };
    }
})();

