(function () {
    'use strict';

    /** Trawl component **/
    angular.module('tsa.tsaViews.trawl', []).component('tsaTrawl', {
        templateUrl: "app/tsa-views/trawl/trawl.html",
        controllerAs: "ctrl",
        controller: trawlController,
        bindings: {
            config: '=',
            cardId: '@',
            onChangeCardType: '&'
        }
    });

    /** Trawl controller **/
    function trawlController(acquisitionService, $timeout, TRAWL, NAVIGATION, $scope, userService) {
        var vm = this;
        var TAG = '[trawlController] ';

        /**------------------------------ PROPERTIES -------------------------------**/

        //Canvas
        vm.canvasId = "canvas-weather-" + vm.cardId;
        var trawlShape;
        var canvas;
        var stage;
        var canvasWidth;
        var canvasHeight;
        
        /**-------------------------- INITIALIZATION / DESTROY METHODS -------------------------**/

        /** Initialization function **/
        vm.$onInit = function init() {

            console.log(TAG + '$onInit: deviceId = ' + (this.config ? this.config.deviceId : 'config undefined'));

            if (this.config == undefined || this.config.deviceId == undefined) {
                console.error(TAG + 'device Id not found!');
                return;
            }

            vm.deviceId = this.config.deviceId; //Component parameter (mandatory)
            vm.device = acquisitionService.registerForDevice(vm.deviceId);

            vm.frameId = TRAWL.FRAME; //shipnav frame;

            //Create object to add x and y properties
            vm.verSpread = { id: TRAWL.VERTICAL_SPREAD };
            vm.clearance = { id: TRAWL.CLEARANCE };
            vm.doorDistance = { id: TRAWL.DOOR_DISTANCE };
            vm.horSpread = { id: TRAWL.HORIZONTAL_SPREAD };
            vm.altitude = { id: TRAWL.ALTITUDE };
            vm.immersion = { id: TRAWL.DEPTH };

            vm.temp = TRAWL.TEMP;
            vm.sounder = {
                deviceId: NAVIGATION.DEVICE_ID,
                frameId: NAVIGATION.FRAME_SHIPNAV,
                data: NAVIGATION.SOUNDER
            };

            vm.dataFormat = '%.1f';

            $timeout(function () {
                initCanvas();
                drawTrawl();
            }, 500);

        };

        /**------------------------------ PRIVATE METHODS -------------------------------**/

        /**Initialize the canvas **/
        function initCanvas() {
            canvas = document.getElementById(vm.canvasId);
            if (canvas != null) {
                stage = new createjs.Stage(canvas);
                $scope.$watch(function () {
                    return canvas.offsetHeight
                }, function () {
                    updateCanvasSize();
                });
                $scope.$watch(function () {
                    return canvas.offsetWidth
                }, function () {
                    updateCanvasSize();
                });
                updateCanvasSize();
            }
        }

        /** Create the stage if needed, reset the canvas and redraw all the shapes **/
        function updateCanvasSize() {
            if (canvas != null) {

                canvasWidth = canvas.offsetWidth;
                canvasHeight = canvas.offsetHeight;

                //Canvas size properties setting
                canvas.setAttribute('width', canvasWidth);
                canvas.setAttribute('height', canvasHeight);

                // Reset canvas
                stage.removeAllChildren();

                // Redraw shapes
                drawTrawl();
                stage.update();
            }
        }

        /** Draw the trawl shape **/
        function drawTrawl() {
            canvas = document.getElementById(vm.canvasId);
            if (canvas != null)
                stage = new createjs.Stage(canvas);

            // Create shape and set properties (color, size...)
            trawlShape = new createjs.Shape();
            stage.addChild(trawlShape);

            // Center point
            trawlShape.x = canvasWidth / 1.65;
            trawlShape.y = canvasHeight / 1.8;

            trawlShape.graphics.beginStroke('#757575').setStrokeStyle(2, 'round');


            /*           TRAWL REPRESENTATION
             ----------------------------------> X
             |
             | F1-----------D1 ---------- D2
             |        A1    |              |   A2
             |   E1   |     |      .[0,0]  |   |    E2
             |        B1    |              |   B2
          Y  V              C1----------- C2
             */

            var rectWidth = canvasWidth / 3.7;
            var rectHeight = canvasHeight / 3;

            var A1 = [-rectWidth * 0.9, - rectHeight / 6];
            var B1 = [A1[0], A1[1] + rectHeight / 3];
            var C1 = [-rectWidth / 2, rectHeight / 2];
            var D1 = [C1[0], -C1[1]];
            var E1 = [-rectWidth * 1.3, 0];

            var A2 = [-A1[0], A1[1]];
            var B2 = [-B1[0], B1[1]];
            var C2 = [-C1[0], C1[1]];
            var D2 = [-D1[0], D1[1]];
            var E2 = [-E1[0], E1[1]];

            var F1 = [-canvasWidth / 2, D1[1] * 1.3];

            var quadCurveVertOffset = D1[1] * 1.6;
            var quadCurveHorzOffset = -D1[0] * 1.7;

            var panelWidth = canvasWidth / 60;
            var panelHeight = canvasHeight / 8;

            var doorDistArrowOffset = - 0.9*rectHeight;

            trawlShape.graphics
                // Main ellipse
                .beginFill('rgba(67, 203, 44, 0.1)')
                .beginStroke('#8BC34A')

                .moveTo(A1[0], A1[1])
                .lineTo(D1[0], D1[1])
                .quadraticCurveTo(0, quadCurveVertOffset, D2[0], D2[1])
                .lineTo(A2[0], A2[1])
                .lineTo(B2[0], B2[1])
                .lineTo(C2[0], C2[1])
                .quadraticCurveTo(0, -quadCurveVertOffset, C1[0], C1[1])
                .lineTo(B1[0], B1[1])
                .lineTo(A1[0], A1[1])

                .endFill()
                .moveTo(D1[0], D1[1])
                .quadraticCurveTo(-quadCurveHorzOffset, 0, C1[0], C1[1])
                .moveTo(D2[0], D2[1])
                .quadraticCurveTo(quadCurveHorzOffset, 0, C2[0], C2[1])

                //Panels
                .beginStroke('#757575')
                .moveTo(A1[0], A1[1])
                .lineTo(E1[0], E1[1])
                .lineTo(B1[0], B1[1])
                .moveTo(A2[0], A2[1])
                .lineTo(E2[0], E2[1])
                .lineTo(B2[0], B2[1])
                .beginFill('#757575')
                .drawRect(E1[0] - panelWidth / 2, E1[1] - panelHeight / 2, panelWidth, panelHeight)
                .drawRect(E2[0] - panelWidth / 2, E1[1] - panelHeight / 2, panelWidth, panelHeight)

                //Dashed line
                .beginStroke('#757575').setStrokeStyle(1, 'round').setStrokeDash([5, 5], 0)
                .moveTo(F1[0], F1[1])
                .lineTo(0, F1[1])
                .moveTo(E1[0], E1[1])
                .lineTo(E1[0], E1[1] + doorDistArrowOffset)
                .moveTo(E2[0], E2[1])
                .lineTo(E2[0], E2[1] + doorDistArrowOffset);


            //Arrow 1: door distance
            drawArrow([E1[0], doorDistArrowOffset], [E2[0], doorDistArrowOffset], vm.doorDistance);
            //vm.doorDistance.y -= 20 if label 
            //Arrow 2: horizontal spread
            drawArrow([A1[0] + 3, A1[1] * 0.7], [A2[0] - 3, A2[1] * 0.7], vm.horSpread);
            vm.horSpread.x -= rectWidth / 3;
            //vm.horSpread.y -= 20; if label 
            //Arrow 3: vertical spread
            drawArrow([0, D1[1] * 1.33 + 5], [0, C1[1] * 1.33 - 5], vm.verSpread);
            vm.verSpread.y += rectHeight / 3;
            //Arrow 4: clearance
            drawArrow([0, C1[1] * 1.3 + 5], [0, canvasHeight - trawlShape.y - 5 - 0.05 * canvasHeight], vm.clearance);
            //Arrow 5: altitude
            drawArrow([F1[0], F1[1] + 5], [F1[0], canvasHeight - trawlShape.y - 5 - 0.05 * canvasHeight], vm.altitude);
            //Arrow 6: immersion
            drawArrow([F1[0], F1[1] - 5], [F1[0], -trawlShape.y + 5 + 0.05 * canvasHeight], vm.immersion);

            //drawGround();
            drawSurfaceAndGround();
            
            stage.update();
        }

        /** Draw an arrow with a label **/
        function drawArrow(startPoint, endPoint, label) {
            var arrowShape = new createjs.Shape();
            stage.addChild(arrowShape);
            arrowShape.graphics.beginStroke('#757575').setStrokeStyle(2);//.setStrokeDash([5, 5], 0);1a75ae

            arrowShape.x = trawlShape.x;
            arrowShape.y = trawlShape.y;

            var headLength = 10;   // length of head in pixels
            var angle = Math.atan2(endPoint[1] - startPoint[1], endPoint[0] - startPoint[0]);
            arrowShape.graphics
                .moveTo(startPoint[0], startPoint[1])
                .lineTo(startPoint[0] + headLength * Math.cos(angle - Math.PI / 6), startPoint[1] + headLength * Math.sin(angle - Math.PI / 6))
                .moveTo(startPoint[0], startPoint[1])
                .lineTo(startPoint[0] + headLength * Math.cos(angle + Math.PI / 6), startPoint[1] + headLength * Math.sin(angle + Math.PI / 6))
                .moveTo(startPoint[0], startPoint[1])
                .lineTo(endPoint[0], endPoint[1])
                .moveTo(endPoint[0], endPoint[1])
                .lineTo(endPoint[0] - headLength * Math.cos(angle - Math.PI / 6), endPoint[1] - headLength * Math.sin(angle - Math.PI / 6))
                .moveTo(endPoint[0], endPoint[1])
                .lineTo(endPoint[0] - headLength * Math.cos(angle + Math.PI / 6), endPoint[1] - headLength * Math.sin(angle + Math.PI / 6));

            //Set the label position
            if (label != undefined) {
                label.x = trawlShape.x + (startPoint[0] + endPoint[0]) / 2 - 50;  // - 100 with labels;
                label.y = trawlShape.y + (startPoint[1] + endPoint[1]) / 2 - 14;  // - 12 with labels;
            }
        }

        /** Draw the surface and the ground **/
        function drawSurfaceAndGround() {
            var surfShape = new createjs.Shape();

            var startPointSurface = [0.02 * canvasWidth, 0.05 * canvasHeight];
            var endPointSurface = [canvasWidth - startPointSurface[0], startPointSurface[1]];
            var startPointGround = [startPointSurface[0], canvasHeight - startPointSurface[1]];
            var endPointGround = [endPointSurface[0], startPointGround[1]];

            stage.addChild(surfShape);

            surfShape.graphics.beginStroke('')
                .setStrokeStyle(1, 'round')
                .beginFill('rgba(44, 151, 203, 0.1)');

            var index = startPointSurface[0];//+ 5;
            while (index < endPointSurface[0] - 60) {
                surfShape.graphics.moveTo(index, startPointSurface[1])
                    .arcTo(index + 30, startPointSurface[1] + 10, index + 60, startPointSurface[1], 95);
                index += 60;
            }
            surfShape.graphics.lineTo(endPointSurface[0], endPointSurface[1])
                .lineTo(endPointGround[0], endPointGround[1])
                .lineTo(startPointGround[0], startPointGround[1])
                .lineTo(startPointSurface[0], startPointSurface[1]);


            surfShape.graphics.endFill().beginStroke('#757575')
                .setStrokeStyle(1, 'round');

            var index = startPointSurface[0];//+ 5;
            while (index < endPointSurface[0] - 60) {
                surfShape.graphics.moveTo(index, startPointSurface[1])
                    .arcTo(index + 30, startPointSurface[1] + 10, index + 60, startPointSurface[1], 95);
                index += 60;
            }
            surfShape.graphics.lineTo(endPointSurface[0], endPointSurface[1]);


            surfShape.graphics.moveTo(startPointGround[0], startPointGround[1]).lineTo(endPointGround[0], endPointGround[1]);
            index = startPointGround[0] + 10;
            while (index < endPointGround[0]) {
                surfShape.graphics.moveTo(index, startPointGround[1]).lineTo(index - 10, startPointGround[1] + 10);
                index += 20;
            }

        }

    }

})();

(function () {
    'use strict';
    angular.module('tsa.tsaViews.map').factory("TrawlingServiceProvider", TrawlingServiceProvider);

    function TrawlingServiceProvider($interval, $log, acquisitionService,statusService, TRAWLING) {

        var trawlingService = function() {
            var logger = $log.getInstance("TrawlingService");

            var vm = this;

            /** Data listeners **/
            var trawlingFrameListener;
            vm.isAcquiring = {value: false};
            vm.trawling = {
                lat: undefined,
                long: undefined,
                starboardunwound: undefined,
                portunwound: undefined,
                verSpread: undefined,
                horSpread: undefined,
                waterropealt: undefined,
                comment: undefined
            };
            vm.lastMessageTimestamp;

            /** Called when a Shipnav frame has been received **/
            function parseTrawlingFrame(receivedFrame) {
                var timestamp = receivedFrame.timestamp;
                vm.trawling.lat = parseFloat(receivedFrame.value[TRAWLING.LAT]);
                vm.trawling.long = parseFloat(receivedFrame.value[TRAWLING.LONG]);
                vm.trawling.starboardunwound = parseFloat(receivedFrame.value[TRAWLING.STARBOARDUNWOUND]);
                vm.trawling.portunwound = parseFloat(receivedFrame.value[TRAWLING.PORTUNWOUND]);
                vm.trawling.verSpread = parseFloat(receivedFrame.value[TRAWLING.VERTICAL_SPREAD]);
                vm.trawling.horSpread = parseFloat(receivedFrame.value[TRAWLING.HORIZONTAL_SPREAD]);
                vm.trawling.waterropealt = parseFloat(receivedFrame.value[TRAWLING.ALTITUDE]);
                vm.trawling.comment = parseFloat(receivedFrame.value[TRAWLING.COMMENT]);

                vm.isAcquiring.value = true;
            }


            return {
/*
                clearMultibeamArray: function () {
                    vm.multibeamArray = [];
                },
                setLengthVector: function (lengthVector) {
                    vm.lengthVector = lengthVector;
                },
                setMapComponent: function (mapComponent) {
                    vm.mapComponent = mapComponent;
                },
                */
                isAcquiring: function () {
                    return vm.isAcquiring;
                },
                /** Initialize the data listeners (subscriptions to the acquisition service...) **/
                initDataListener: function initDataListener(trawlingDeviceId, frameTrawlingId) {

                    vm.lastMessageTimestamp = statusService.getServerTime().time;

                    //1 - Shipnav frame listener
                    trawlingFrameListener = acquisitionService.registerForDevice(trawlingDeviceId).registerForFrame(frameTrawlingId);
                    trawlingFrameListener.promise.then({}, {}, parseTrawlingFrame);
                },

                destroyDataListener: function destroyDataListener(trawlingDeviceId) {
                    if (trawlingFrameListener !== undefined)
                        acquisitionService.registerForDevice(trawlingDeviceId).unregisterForFrame(trawlingFrameListener);
                }

            };
        };
        return {
          getInstance: function () {
              return new trawlingService();
          }
        }
    }
})();

(function () {
    'use strict';

    /** Trawling component **/
    angular.module('tsa.tsaViews.trawling', []).component('tsaTrawling', {
        templateUrl: "app/tsa-views/trawling/trawling.html",
        controllerAs: "ctrl",
        controller: trawlingController,
        bindings: {
            config: '=',
            cardId: '@',
            onChangeCardType: '&'
        }
    });

    /** Trawling controller **/
    function trawlingController(acquisitionService, userService, $timeout, $http, TRAWLING, TrawlingServiceProvider) {
        var vm = this;
        var TAG = '[trawlingController] ';
        var trawlingService = TrawlingServiceProvider.getInstance();
        /**------------------------------ PROPERTIES -------------------------------**/

        //Canvas
        vm.canvasId = "canvas-trawling-" + vm.cardId;

        /**-------------------------- INITIALIZATION / DESTROY METHODS -------------------------**/

        /** Initialization function **/
        vm.$onInit = function init() {

            vm.currentUserProvider = userService.getCurrentUserProvider();
            if (vm.currentUserProvider.currentUser) {
                vm.isAdminLogged = (vm.currentUserProvider.currentUser.login === 'admin' || vm.currentUserProvider.currentUser.login === 'europe');
            } else {
                vm.isAdminLogged = false;
            }
            console.log(TAG + '$onInit: deviceId = ' + (this.config ? this.config.deviceId : 'config undefined'));

            if (this.config === undefined || this.config.deviceId === undefined) {
                console.error(TAG + 'device Id not found!');
                return;
            }

            vm.horspread = 0;
            vm.horspread_mean = 0;
            vm.horspread_mean_nb = 0;
            vm.verspread = 0;
            vm.verspread_mean = 0;
            vm.verspread_mean_nb = 0;
            vm.altitude = 0;
            vm.altitude_mean = 0;
            vm.altitude_mean_nb = 0;
            vm.starboardunwound = 0;
            vm.starboardunwound_mean = 0;
            vm.starboardunwound_mean_nb = 0;
            vm.portunwound = 0;
            vm.portunwound_mean = 0;
            vm.portunwound_mean_nb = 0;

            vm.deviceId = this.config.deviceId; //Component parameter (mandatory)
            vm.frameId = this.config.frameId;
            vm.frameListener = acquisitionService.registerForDevice(vm.deviceId).registerForFrame(vm.frameId);
            vm.frameListener.promise.then({}, {}, function (frame) {
                var nbValue = 1;
                if (frame.value[TRAWLING.HORIZONTAL_SPREAD_SUM]) {
                    vm.horspread = parseFloat(frame.value[TRAWLING.HORIZONTAL_SPREAD_SUM]);
                    nbValue = parseInt(frame.value[TRAWLING.HORIZONTAL_SPREAD_NB]);
                    if (nbValue !== 0) {
                        vm.horspread_mean_nb = nbValue;
                        vm.horspread_mean = (frame.value[TRAWLING.HORIZONTAL_SPREAD_NB] !== undefined) ? vm.horspread / nbValue : vm.horspread_mean;
                    }
                }


                if (frame.value[TRAWLING.VERTICAL_SPREAD_SUM]) {
                    vm.verspread = parseFloat(frame.value[TRAWLING.VERTICAL_SPREAD_SUM]);
                    nbValue = parseInt(frame.value[TRAWLING.VERTICAL_SPREAD_NB]);
                    if (nbValue !== 0) {
                        vm.verspread_mean_nb = nbValue;
                        vm.verspread_mean = (frame.value[TRAWLING.VERTICAL_SPREAD_NB] !== undefined) ? vm.verspread / nbValue : vm.verspread_mean;
                    }
                }

                if (frame.value[TRAWLING.ALTITUDE_SUM]) {
                    vm.altitude = parseFloat(frame.value[TRAWLING.ALTITUDE_SUM]);
                    nbValue = parseInt(frame.value[TRAWLING.ALTITUDE_NB]);
                    if (nbValue !== 0) {
                        vm.altitude_mean_nb = nbValue;
                        vm.altitude_mean = (frame.value[TRAWLING.ALTITUDE_NB] !== undefined) ? vm.altitude / nbValue : vm.altitude_mean;
                    }
                }

                if (frame.value[TRAWLING.STARBOARDUNWOUND_SUM]) {
                    vm.starboardunwound = parseFloat(frame.value[TRAWLING.STARBOARDUNWOUND_SUM]);
                    nbValue = parseInt(frame.value[TRAWLING.STARBOARDUNWOUND_NB]);
                    if (nbValue !== 0) {
                        vm.starboardunwound_mean_nb = nbValue;
                        vm.starboardunwound_mean = (frame.value[TRAWLING.STARBOARDUNWOUND_NB] !== undefined) ? vm.starboardunwound / nbValue : vm.starboardunwound_mean;
                    }
                }

                if (frame.value[TRAWLING.PORTUNWOUND_SUM]) {
                    vm.portunwound = parseFloat(frame.value[TRAWLING.PORTUNWOUND_SUM]);
                    nbValue = parseInt(frame.value[TRAWLING.PORTUNWOUND_NB]);
                    if (nbValue !== 0) {
                        vm.portunwound_mean_nb = nbValue;
                        vm.portunwound_mean = (frame.value[TRAWLING.PORTUNWOUND_NB] !== undefined) ? vm.portunwound / nbValue : vm.portunwound_mean;
                    }
                }

            });

            var commentListener = acquisitionService.registerForDevice(vm.deviceId).registerForData(TRAWLING.FRAME, TRAWLING.COMMENT);
            commentListener.promise.then({}, {}, function (data) {
                if (data.value === 'trawling') {
                    vm.isAcquiring = true;
                }
                if (data.value === 'stop') {
                    vm.isAcquiring = false;
                }

            });
            vm.frameId = TRAWLING.FRAME; //shipnav frame;
            trawlingService.initDataListener(vm.deviceId, vm.frameId);
            vm.dataFormat = '%.1f';
            vm.lat = {id: TRAWLING.LAT};
            vm.lon = {id: TRAWLING.LONG};
            vm.comment = {id: TRAWLING.COMMENT};
            vm.distance_straight = {id: TRAWLING.DISTANCE_STRAIGHT};
            vm.distance_real = {id: TRAWLING.DISTANCE_REAL};
            vm.ratio = {id: TRAWLING.RATIO};
        };

        vm.startAcquire = function startAcquire() {
            vm.isAcquiring = true;
            return $http.put('/api/devices/EU_TRAWLING/acquire', true);
        };

        vm.stopAcquire = function stopAcquire() {
            vm.isAcquiring = false;
            return $http.put('/api/devices/EU_TRAWLING/acquire', false);
        };
    }

})();

(function () {
    'use strict';

    /** Video component declaration **/
    angular.module('tsa.tsaViews.video', []).component('tsaVideo', {
        templateUrl: "app/tsa-views/video/video.html",
        controllerAs: "ctrl",
        controller: videoController,
        bindings: {
            config: '=',
            cardId: '@',
            onChangeCardType: '&'
        }
    });


    /** Video controller */
    function videoController($interval, $timeout, $scope, $log, userService, themeService, videoStreamingService, VIDEO_PARAMS) {

        /**------------------------------ PROPERTIES -------------------------------**/
        var vm = this;
        vm.logger = $log.getInstance("video-tsa-view");
        vm.videoId = "video-" + vm.cardId;
        var timeoutPromise;

        /** Initialization function **/
        vm.$onInit = function init() {
            if (angular.isUndefined(this.config)
                || angular.isUndefined(this.config.url)) {
                vm.logger.error('url not found!');
                return;
            }

            vm.title = this.config.deviceId;
            vm.url = this.config.url;
            vm.janusStreamIndex = this.config.janusStreamIndex;
            vm.currentUser = userService.getCurrentUser();
            vm.janus = null;

            timeoutPromise = $timeout(function () {

                var parentDiv = angular.element('#' + vm.videoId);

                var videoElm = angular.element('<video width=800 height=600 autoplay="true"/>');
                parentDiv.append(videoElm);

                // Use Janus
                videoStreamingService.startJanus(function (janus) {
                    vm.janus = janus;
                    videoStreamingService.startStream(janus, vm.janusStreamIndex);
                }, videoElm[0]);

            }, 50);
        };

        /** Destroy function **/
        vm.$onDestroy = function () {
            $timeout.cancel(timeoutPromise);
            if (vm.janus) {
                vm.janus.destroy();
            }
        };
    }
})();

