(function () {
    'use strict';

    /** Weather component */
    angular.module('tsa.tsaViews.weather', []).component('tsaWeather', {
        templateUrl: "app/tsa-views/weather/weather.html",
        controllerAs: "ctrl",
        controller: weatherController,
        bindings: {
            config: '=',
            cardId: '@',
            onChangeCardType: '&',
            mode: '@'
        }
    });

    /** Weather controller */
    function weatherController($log, acquisitionService, WEATHER, NAVIGATION, $scope, $timeout) {
        var vm = this;
        vm.logger = $log.getInstance("weather-tsa-view");

        /**------------------------------ PRIVATE PROPERTIES -------------------------------**/

        //Mode (Default / Anemometer)
        vm.MODE = { DEFAULT: 'default', ANEMOMETER: 'anemometer' };

        //Canvas
        vm.canvasId = "canvas-weather-" + vm.cardId;
        var canvas;
        var canvasSize;
        var canvasWidth;
        var canvasHeight;
        var stage;

        //Shapes
        var boatShape;
        var boatHeadingShape;
        var trueWindShape;
        var relWindShape;
        var relHeading;

        // Shape properties
        var trueWindColor = "#E53935";
        var relWindColor = "#1a75ae";

        //Data listeners
        var frameListener;
        var boatHeadingListener;
        var boatHeadingValue;

        /**-------------------------- INITIALIZATION / DESTROY METHODS -------------------------**/

        /** Initialization function  */
        vm.$onInit = function init() {
            if (angular.isUndefined(this.config) || angular.isUndefined(this.config.deviceId) || angular.isUndefined(this.config.frameId)) {
                vm.logger.error('config and/or deviceId and/or frameId not defined!');
                return;
            }

            vm.logger.debug('vm.currentMode: ' + vm.currentMode + '; vm.mode: ' + vm.mode + '; this.mode: ' + this.mode);

            vm.weatherDeviceId = this.config.deviceId;
            vm.weatherFrame = this.config.frameId;

            vm.weatherDataList = WEATHER.DATA_TO_DISPLAY;

            vm.trueWindSpeedId = WEATHER.TRUE_AIR_SPEED;
            vm.trueWindHeadingId = WEATHER.TRUE_AIR_HEADING;
            vm.relWindSpeedId = WEATHER.REL_AIR_SPEED;
            vm.relWindHeadingId = WEATHER.REL_AIR_HEADING;

            // Boat heading listener initialisation (not done in "Anemometer" mode)
            if (vm.mode != vm.MODE.ANEMOMETER) {
                boatHeadingListener = acquisitionService.registerForDevice(this.config.navId).registerForData(NAVIGATION.FRAME_SHIPNAV, NAVIGATION.HEADING);
                boatHeadingListener.promise.then({}, {}, function (head) {
                    boatHeadingValue = head.value;
                    if (stage != null && boatShape == null) {
                        drawBoat();
                    }
                    if (boatShape !== undefined) {
                        boatShape.rotation = head.value - 90;
                        boatHeadingShape.rotation = head.value - 90;

                        if (angular.isDefined(relWindShape))
                            relWindShape.rotation = parseFloat(boatHeadingValue) + parseFloat(relHeading) - 90;
                        stage.update();
                    }
                });
            }

            // Wind listener initisalisation
            frameListener = acquisitionService.registerForDevice(vm.weatherDeviceId).registerForFrame(WEATHER.FRAME);
            frameListener.promise.then({}, {}, function (frame) {
                if (angular.isDefined(stage)) {
                    var trueSpeed = frame.value[WEATHER.TRUE_AIR_SPEED];
                    var trueHeading = frame.value[WEATHER.TRUE_AIR_HEADING];
                    var relSpeed = frame.value[WEATHER.REL_AIR_SPEED];
                    relHeading = frame.value[WEATHER.REL_AIR_HEADING];

                    stage.removeChild(trueWindShape);
                    stage.removeChild(relWindShape);
                    if (angular.isDefined(boatHeadingValue)) { //If boath heading is known, draw rel and true flags
                        trueWindShape = drawWindFlag(trueSpeed, trueWindColor, trueHeading);
                        relWindShape = drawWindFlag(relSpeed, relWindColor, parseFloat(boatHeadingValue) + parseFloat(relHeading));
                    }
                    else relWindShape = drawWindFlag(relSpeed, relWindColor, parseFloat(relHeading)); // else draw only rel flag
                    stage.update();
                }
            });

            //Wait the canvas creation, and then call the init function
            $timeout(function () {
                initCanvas();
            }, 500);
        };

        /** Destroy function: remove the data listeners */
        vm.$onDestroy = function () {
            if (angular.isDefined(frameListener))
                acquisitionService.registerForDevice(vm.weatherDeviceId).unregisterForFrame(frameListener);
            if (angular.isDefined(boatHeadingListener))
                acquisitionService.registerForDevice(vm.config.navId).unregisterForData(boatHeadingListener);
        }

        /**------------------------------ DRAW METHODS -------------------------------**/

        function vector_dirmag_xy( direction, magnitude) {
            var angle = direction * Math.PI / 180.0;
            var xEast = magnitude * Math.sin( angle);
            var yNorth = magnitude * Math.sin( angle);
            return { "xEast": xEast, "yNorth": yNorth};
        }

        function vector_xy_dirmag( xEast, yNorth) {
            var direction, magnitude;

            if(yNorth == 0.0) {
                if(xEast >= 0.0) {
                    direction = 90.0;
                } else {
                    direction = 270.0;
                }
            } else {
                direction = Math.atan( xEast / yNorth) * 180.0 / Math.PI;
                if( yNorth < 0.0) {
                    direction += 180.0;
                }
            }

            magnitude = Math.sqrt( xEast*xEast + yNorth*yNorth);
            return { "direction": direction, "magnitude": magnitude};
        }

        function computeTrueWind(relativeWindDirection, relativeWindMagnitude,
            heading, cog, sog) {

            // Direction du vent apparent / au nord geographique
            var relativeWindNorthDirection = relativeWindDirection + 180.0;
            if(relativeWindNorthDirection > 360.0) relativeWindNorthDirection -= 360.0;

            relativeWindNorthDirection += heading;
            if(relativeWindNorthDirection > 360.0) relativeWindNorthDirection -= 360.0;

            var groundCoords = vector_dirmag_xy( cog, sog);
            var relativeWindCoords =  vector_dirmag_xy( relativeWindNorthDirection, relativeWindMagnitude);

            var trueWindxEast = groundCoords.xEast + relativeWindCoords.xEast;
            var trueWindyNorth = groundCoords.yNorth + relativeWindCoords.yNorth;

            var trueWind = vector_xy_dirmag(trueWindxEast, trueWindyNorth);

            trueWind.direction -= 180.0;
            if(trueWind.direction < 0.0) trueWind.direction += 360.0;

            return trueWind;
        }


        /** Initialize the canvas **/
        function initCanvas() {
            canvas = document.getElementById(vm.canvasId);
            if (canvas != null) {
                stage = new createjs.Stage(canvas);
                drawBoat();
                $scope.$watch(function () {
                    return canvas.offsetHeight
                }, function () {
                    updateCanvasSize();
                });
                $scope.$watch(function () {
                    return canvas.offsetWidth
                }, function () {
                    updateCanvasSize();
                });
                updateCanvasSize();
            }
        }

        /** Create the stage if needed, reset the canvas and redraw all the shapes **/
        function updateCanvasSize() {
            if (canvas != null) {
                canvasSize = canvas.offsetWidth < canvas.offsetHeight ? canvas.offsetWidth : canvas.offsetHeight;
                canvasWidth = canvas.offsetWidth;
                canvasHeight = canvas.offsetHeight;

                //Canvas size properties setting
                canvas.setAttribute('width', canvasWidth);
                canvas.setAttribute('height', canvasHeight);

                // Reset canvas
                stage.removeAllChildren();

                // Redraw shapes
                drawCompass();
                drawBoat();
                if (trueWindShape != null)
                    trueWindShape = drawWindFlag(trueWindShape.speed, trueWindColor, trueWindShape.rotation + 90);
                if (relWindShape != null)
                    relWindShape = drawWindFlag(relWindShape.speed, relWindColor, relWindShape.rotation + 90);
                stage.update();
            }
        }

        /** Draw the boat and its heading **/
        function drawBoat() {
            // Create shape and set properties (color, size...)
            boatShape = new createjs.Shape();
            boatShape.x = canvasWidth / 2;
            boatShape.y = canvasHeight / 2;
            boatShape.rotation = angular.isDefined(boatHeadingValue) ? boatHeadingValue - 90 : -90;
            boatShape.graphics.beginStroke('#757575').setStrokeStyle(1, 'round');
            stage.addChild(boatShape);

            boatHeadingShape = new createjs.Shape();
            boatHeadingShape.x = canvasWidth / 2;
            boatHeadingShape.y = canvasHeight / 2;
            boatHeadingShape.rotation = boatShape.rotation;
            boatHeadingShape.graphics.beginStroke('#757575').setStrokeStyle(1).setStrokeDash([10, 5]);
            stage.addChild(boatHeadingShape);

            var boatLength = canvasSize * 0.5;
            var boatWidth = boatLength / 4;

            //Heading line
            boatHeadingShape.graphics.moveTo(-boatLength * (3 / 4), 0);
            boatHeadingShape.graphics.lineTo(boatLength * 3 / 4, 0);
            //Boat
            boatShape.graphics.moveTo(boatLength / 5, boatWidth / 2)
                .lineTo(-boatLength / 2, boatWidth / 2)
                .lineTo(-boatLength / 2, -boatWidth / 2)
                .lineTo(boatLength / 5, -boatWidth / 2)
                .bezierCurveTo(boatLength / 1.5, -boatWidth / 4, boatLength / 1.5, boatWidth / 4, boatLength / 5, boatWidth / 2);
        }

        /** Draw wind shape **/
        function drawWindFlag(speed, color, angle) {
            var shape = new createjs.Shape();
            shape.x = canvasWidth / 2;
            shape.y = canvasHeight / 2;
            shape.rotation = angle - 90;
            shape.speed = speed;
            stage.addChild(shape);
            shape.graphics.beginStroke(color).setStrokeStyle(2);
            var flagLength = canvasSize / 3;

            if (speed === 0) {//if the true wind is null
                shape.graphics.drawCircle(0, 0, 20);
            }
            else if (speed > 200) {//if the true wind is too big
                vm.logger.error("Error true wind speed too high");
            }
            else {//if the true wind is not null and not too big
                shape.graphics.moveTo(0, 0);
                shape.graphics.lineTo(flagLength, 0);
                shape.graphics.beginFill(color);
                drawBarb(shape, speed, flagLength);
            }
            return shape;
        }

        /** function used to draw the pennants and the flags on the wind barb (hampe de vent) **/
        function drawBarb(shape, windSpeed, flagLength) {
            var currentPosition = flagLength; // virtual mark in order to know where to put pennants,flags and half-flags
            var pennant = Math.floor(windSpeed / 50);           // for each 50 knots we add one pennant
            var flag = Math.floor((windSpeed % 50) / 10);       // for each 10 knots we add one flag (max 4)
            var halfFlag = Math.floor((windSpeed % 10) / 5);    // for each 5 knots we add one half-flag (max 1)

            //Draw pennants
            for (var i = 0; i < pennant; i++) {
                shape.graphics.moveTo(currentPosition, 0).lineTo(currentPosition - 12, 20).lineTo(currentPosition - 20, 0);
                currentPosition -= 20;
            }
            if (pennant !== 0)
                currentPosition -= 10;
            //Draw flags
            for (i = 0; i < flag; i++) {
                shape.graphics.moveTo(currentPosition, 0).lineTo(currentPosition + 8, 20);
                currentPosition -= 10;
            }
            //Draw half flags
            for (i = 0; i < halfFlag; i++) {
                shape.graphics.moveTo(currentPosition, 0).lineTo(currentPosition + 4, 10);
                currentPosition -= 10;
            }
        }

        /** Draw the compass **/
        function drawCompass() {
            var compassShape = new createjs.Shape();
            compassShape.x = canvasWidth / 2;
            compassShape.y = canvasHeight / 2;
            stage.addChild(compassShape);
            compassShape.graphics.beginStroke('#757575').setStrokeStyle(3);
            var radius = canvasSize / 2 - 20;

            for (var i = 0; i < 24; i++) {
                var angle = i * Math.PI / 12;
                var strokeLength = i % 3 === 0 ? 7 : 2;
                //Draw text
                if (i % 2 === 0 && i % 6 !== 0) {
                    var xPosition = (radius + strokeLength + 20) * Math.cos(angle) - 6; //offset to adjust the text position
                    var yPosition = (radius + strokeLength + 20) * Math.sin(angle);
                    var text = ((i + 6) % 24) * 360 / 24;
                    xPosition -= 10; //offset to adjust the text position
                    drawText(text, xPosition, yPosition);
                }
                //Draw stroke
                compassShape.graphics.moveTo((radius - strokeLength) * Math.cos(angle), (radius - strokeLength) * Math.sin(angle));
                compassShape.graphics.lineTo((radius + strokeLength) * Math.cos(angle), (radius + strokeLength) * Math.sin(angle));

            }
        }

        /** Draw a text at a specific position **/
        function drawText(text, x, y) {
            var textShape = new createjs.Text(text, "18px Arial", '#757575');
            textShape.x = canvasWidth / 2 + x;
            textShape.y = canvasHeight / 2 + y;
            textShape.textBaseline = "middle";
            return stage.addChild(textShape);

        }
    }

})();

(function () {
    'use strict';

    /** Winch component declaration **/
    (function () {
        'use strict';
        angular.module('tsa.tsaViews.winch', []).component('tsaWinch', {
            templateUrl: "app/tsa-views/winch/winch.html",
            controllerAs: "ctrl",
            controller: winchController,
            bindings: {
                config: '=',
                cardId: '@',
                onChangeCardType: '&'
            }
        });
    })();

    /** Winch controller */
    function winchController($timeout, $interval, $log, $scope, acquisitionService, graphUtils, statusService, themeService, WINCH, NAVIGATION) {
        var vm = this;
        vm.logger = $log.getInstance("winch-tsa-view");

        /**------------------------------ PROPERTIES -------------------------------**/

        vm.canvasId = "canvas-winch-" + vm.cardId;
        var intervalPromise;
        var timeoutPromise;
        vm.bufferLoading = {};

        var winchFrameListener;

        /**  Time scales **/
        vm.scales = graphUtils.scales;
        vm.selectedScale = '1 min';
        vm.spinningText="views.winch.spinning";

        /** Chart properties **/
        var chart = undefined;
        var chartData = {
            datasets: [{
                label: "Data 1",
                xAxisID: "x-axis-1",
                yAxisID: "y-axis-1",
                borderColor: 'rgb(26, 117, 174)',
                backgroundColor: 'rgba(26, 117, 174, 0.3)',
                data: []
            }]
        };

        /**-------------------------- INITIALIZATION / DESTROY METHODS -------------------------**/

        /** Initialization function **/
        vm.$onInit = function init() {
            if (angular.isUndefined(this.config) || angular.isUndefined(this.config.deviceId) || angular.isUndefined(this.config.frameId)) {
                vm.logger.error('config and/or deviceId and/or frameId not defined!');
                return;
            }

            vm.deviceId = this.config.deviceId;
            vm.frameId = this.config.frameId;

            vm.tension = WINCH.TENSION;
            vm.spinning = WINCH.SPINNING;
            vm.length = WINCH.LENGTH;

            vm.deviceSoundingId = this.config.sounder.deviceSoundingId;
            vm.frameSounding = this.config.sounder.frameSounding;
            vm.sounding = this.config.sounder.sounding;

            //Register for Tension Chart
            vm.device = acquisitionService.registerForDevice(vm.deviceId);
            vm.device.recordData(vm.frameId, vm.tension);

            winchFrameListener = acquisitionService.registerForDevice(vm.deviceId).registerForFrame(vm.frameId);
            winchFrameListener.promise.then({}, {}, parseWinchFrame);
            function parseWinchFrame(receivedFrame) {
                var spinning = parseFloat(receivedFrame.value[WINCH.SPINNING]);
                vm.spinningText=(spinning < 0) ? "views.winch.spinning.negative":"views.winch.spinning.positive";

            }

            //Wait the canvas creation, and then create the chart
            timeoutPromise = $timeout(initTimer, 200);

            // Change gridLinesColor of Graph on every change of service variable
            themeService.subscribe($scope, function(event, theme) {
            	vm.theme = theme;
            	reloadChart();
            });

            if (angular.isDefined(vm.config.scale))
                vm.selectedScale = vm.config.scale;
        };

        /** Destroy function **/
        vm.$onDestroy = function () {
            if (winchFrameListener !== undefined)
                acquisitionService.registerForDevice(vm.deviceId).unregisterForFrame(winchFrameListener);

            vm.device.stopRecordData(vm.frameId, vm.tension);
            $timeout.cancel(timeoutPromise);
            if (intervalPromise !== undefined)
                $interval.cancel(intervalPromise);
        };

        /**------------------------------ METHODS -------------------------------**/

        /** Generate the chart options */
        function getChartOptions() {
            var chartOptions = graphUtils.getChartOptions(vm.scales[vm.selectedScale].stepSize);
            chartOptions.scales.yAxes = [{
                id: "y-axis-1",
                gridLines: { color: vm.theme.gridLinesColor },
                ticks: { beginAtZero: true, min: 0, suggestedMax: 10 }
            }];
            return chartOptions;
        }

        /** Redraw the chart **/
        function reloadChart() {
            if (chart !== undefined)
                chart.destroy();
            var canvas = document.getElementById(vm.canvasId);
            if (canvas) {
                chart = Chart.Scatter(canvas, { data: chartData, options: getChartOptions() });
                updateGraph();
            }
            vm.config.scale = vm.selectedScale;
        }

        /** Start or update the timer (intervalPromise) */
        var initTimer = function initTimer() {
            if (intervalPromise !== undefined)
                $interval.cancel(intervalPromise);

            //Load archived data
            if (angular.isDefined(vm.device.getData(vm.frameId, vm.tension))) {
                var bufferId = vm.scales[vm.selectedScale].bufferId;
                vm.bufferLoading[vm.selectedScale] = vm.bufferLoading[vm.selectedScale] ? ++vm.bufferLoading[vm.selectedScale] : 1;
                var selectedScale = vm.selectedScale;
                var requestCallback = function () {
                    vm.bufferLoading[selectedScale]--;
                    if (chart !== undefined)
                        updateGraph();
                };
                vm.device.getData(vm.frameId, vm.tension).buffers[bufferId].loadArchivedFrames().then(requestCallback, requestCallback);
            }
            reloadChart();
            intervalPromise = $interval(updateGraph, vm.scales[vm.selectedScale].scale / 1000);
        };

        /** Update the graph with the recorded data (called by the timer (intervalPromise)) */
        function updateGraph() {
            //Step 1: update X axis
            var currentDate = statusService.getServerTime().time;
            chart.options.scales.xAxes[0].ticks.min = (currentDate - vm.scales[vm.selectedScale].scale);
            chart.options.scales.xAxes[0].ticks.max = currentDate;

            //Step 2: update data
            var bufferId = vm.scales[vm.selectedScale].bufferId;
            chartData.datasets[0].data = [];
            var recordedData = vm.device.getData(vm.frameId, vm.tension);
            if (recordedData !== undefined)
                angular.forEach(recordedData.buffers[bufferId].frames, function (frame) {
                    chartData.datasets[0].data.push({ x: frame.timestamp, y: frame.value });
                });

            chart.update();
        }

        /** Called when a new X scale is selected */
        vm.onChangeXScale = initTimer;

    }
})();

