(function () {
    'use strict';

    /** Winch Alarm Pp component **/
    angular.module('tsa.tsaViews.winchAlarmPp', []).component('tsaWinchAlarmPp', {
        templateUrl: "app/tsa-views/winch-alarm-pp/winch-alarm-pp.html",
        controllerAs: "ctrl",
        controller: winchAlarmPpController,
        bindings: {
            config: '=',
            cardId: '@',
            onChangeCardType: '&'
        }
    });

    /** Winch Alarm Pp controller **/
    function winchAlarmPpController(acquisitionService, $timeout, $scope, PP_WINCH_ALARM, $interval) {
        var vm = this;
        var TAG = '[winchAlarmPpController] ';

        var alarmInterval;
        var last = -1;
        var frameListener;

        /**------------------------------ PROPERTIES -------------------------------**/

        vm.alarms = [
        ];

        vm.deviceId = 'PP_KleyWinch';

        /**------------------------------ METHODS -------------------------------**/

        /** Initialization function **/
        vm.$onInit = function init() {
//            console.log(TAG + '$onInit: deviceId = ' + (this.config ? this.config.deviceId : 'config undefined'));

            alarmInterval = $interval(alarmSwipe, 5000);

            frameListener =
                acquisitionService.registerForDevice(vm.deviceId)
                    .registerForFrame(PP_WINCH_ALARM.FRAME);

            frameListener.promise.then({}, {},
                function(frame) {
                    newAlarm(
                        frame.value[PP_WINCH_ALARM.CODE][0],
                        frame.value[PP_WINCH_ALARM.TEXT][0],
                        frame.value[PP_WINCH_ALARM.LEVEL][0],
                        frame.value[PP_WINCH_ALARM.ENDDATE][0]
                    );
            });
        };

        /** Destroy function: remove the data listeners */
        vm.$onDestroy = function destroy() {
            acquisitionService.registerForDevice(vm.deviceId).unregisterForFrame(PP_WINCH_ALARM.FRAME);
            $interval.cancel(alarmInterval);
        }

        function alarmSwipe() {
            if( (last > 0) && (((new Date().getTime()) - last) > (2*60*1000))) {
                last = -1;
                vm.alarms.length = 0;
            }
        }

        function newAlarm( code, text, level, enddate) {
            var index = vm.alarms.findIndex(function(item) {
                return code === item.code;
            });
            if( enddate >= 100000.0) {
                if( index < 0) {
                    vm.alarms.push(
                        { 'code': code, 'text': text, 'level': level, 'enddate': enddate}
                    );
                }
            } else if( enddate === 0.0) {
                if( index >= 0) {
                    vm.alarms = vm.alarms.splice(index, 1);
                }
            }
        }
    }
})();

