(function () {
    'use strict';
    angular
        .module('tsa.pages.acquisition')
        .controller('editDeviceController', EditDeviceController);

    /** @ngInject */
    function EditDeviceController($mdDialog, restService, $stateParams, dialogService, $state, userService, AUTHORITIES) {
        /** Authorisation checking **/
        if (!userService.checkAuthority(AUTHORITIES.ADMIN))
            return;

        var vm = this;
        vm.title = 'New device';

        vm.channels = [];

        /** Device properties **/
        vm.device = {
            deviceId: "UNIQUE_DEVICE_ID",
            name: "my-device",
            frames: [],
            period: "1000",
            drivers: [],
            triggerMode: "FRAME",
            triggerFrameName: "$TEST",
            triggerInterval: ""
        };
        /** Trigger value **/
        vm.triggerValue = "$TEST";

        /**-------------------- PRIVATE METHODS ----------------**/

	    /**
	     * Init function (automatically called)
         */
        (function init() {
            if ($stateParams.id !== undefined) {
                restService.getConfiguration().then(function (result) {
                    angular.forEach(result.devices, function (device) {
                        if (device.deviceId === $stateParams.id) {
                            vm.device = device;
                            vm.title = vm.device.deviceId;
                            switch (vm.device.triggerMode) {
                                case 'FRAME':
                                    vm.triggerValue = vm.device.triggerFrameName;
                                    break;
                                case 'TIME':
                                    vm.triggerValue = vm.device.triggerInterval;
                                    break;
                            }
                            var channelIds = [];
                            angular.forEach(vm.device.drivers, function(d) {
                                var exist=0;
                                angular.forEach(channelIds, function(c) {
                                    if (c === d.channelId) {
                                        exist=1;
                                    }
                                });
                                if (exist === 0) {
                                    channelIds.push(d.channelId);
                                }
                            })
                            angular.forEach(channelIds, function(c) {
                                vm.channels
                            })
                        }
                    });
                });
            }
        })();

        /**-------------------- VIEW-MODEL METHODS ----------------**/

        /**
         * Remove driver: remove the selected driver, if the channel has not anymore driver, it is removed.
         */
        vm.removeChannel = function removeChannel(channel) {
            var index = vm.channels.indexOf(channel);
            if (index != -1)
                vm.channels.splice(index, 1);
            //if (vm.channels.length == 0)
            //    delete vm.channels;
        };

        /**
         * Remove driver: remove the selected driver, if the channel has not anymore driver, it is removed.
         */
        vm.removeDriver = function removeDriver(index) {
            if (index != -1)
                vm.device.drivers.splice(index, 1);
            //if (vm.driver.length == 0)
            //    delete vm.driver;
        };

        /**
         * Add channel: open a dialog window to add a new channel
         */
        vm.addChannel = function addChannel() {
            $mdDialog.show({
                controller: 'addChannelController',
                controllerAs: 'ctrl',
                templateUrl: 'app/pages/acquisition/addChannel/addChannel.html',
                clickOutsideToClose: true,
                locals: {channel: undefined}
            }).then(function (result) {
                console.log('new channel: ' + result.channel);
                var exist=0;
                angular.forEach(vm.channels, function(channel) {
                    if (channel.channelId === result.channel.channelId ||
                        channel.url === result.channel.url) {
                        exist=1;
                    }
                });

                if (exist === 0) {
                    vm.channels.push(result.channel);
                }
            });
        };

        /**
         * Add driver: open a dialog window to add a new driver
         */
        vm.addDriver = function addDriver() {
            $mdDialog.show({
                controller: 'addDriverController',
                controllerAs: 'ctrl',
                templateUrl: 'app/pages/acquisition/addDriver/addDriver.html',
                clickOutsideToClose: true,
                locals: {channelList: vm.channels, driver: undefined}
            }).then(function (result) {
                console.log('new driver: ' + result.driver.driverName);
                vm.device.drivers.push(result.driver);
                angular.forEach(result.frames, function(f) {
                    vm.device.frames.push({name:f, major:1, minor:0})
                });
            });
        };

	    /**
	     * Edit driver: open a dialog window to edit the selected driver
         */
        vm.editDriver = function editDriver(driver){
            $mdDialog.show({
                controller: 'addDriverController',
                controllerAs: 'ctrl',
                templateUrl: 'app/pages/acquisition/addDriver/addDriver.html',
                clickOutsideToClose: true,
                locals: {channelList: vm.channels, driver: driver}
            }).then(function (result) {
                vm.device.drivers.push(result.driver);
            });
        };


        /**
         * Save the current device.
         */
        vm.save = function save() {
            //Build the trigger properties
            switch (vm.device.triggerMode) {
                case 'FRAME':
                    vm.device.triggerFrameName = vm.triggerValue;
                    break;
                case 'TIME':
                    vm.device.triggerInterval = vm.triggerValue;
                    break;
            }

            restService.putDevice(vm.device.deviceId, vm.device).then (function (success) {
                dialogService.showDialog('Device saved','Successful device creation!');
                $state.go('acquisition');
            });
        };

    }
})();

