(function () {
    'use strict';
    angular
        .module('tsa.pages.acquisition')
        .controller('addDriverController', AddDriverController);

    /** @ngInject */
    function AddDriverController($mdDialog, restService, driver, channelList) {

        /**-------------------- View-model properties ----------------**/
        var vm = this;

        /** Title **/
        vm.title = "New driver";

        vm.headerLine = false;

        /** Driver properties **/
        vm.driver = {
            channelId: "",
            driverClassName: "",
            header: "",
            fields: [],
            parameters: {
                regex: "",
                separator: ","
            }
        };

        vm.frames = [];

        vm.channelList = [];

        /** Driver list **/
        vm.driverList = [];
        restService.getDrivers().then(function (result) {
            vm.driverList = result;
        });

        /** Frame list **/
        vm.frameList = [];
        restService.getDescriptions().then(function (result) {
            vm.frameList = result;
        });

        /** Fields **/
        function Field(fieldIndex, frame, data, option) {
            this.frame = frame;
            this.data = data;
            this.fieldIndex = fieldIndex;
            this.option = option;
        }
        vm.fieldList = [];

        /** For controller edition **/
        loadParams();

        vm.changeDriverSelected = function changeDriverSelected() {
            if (vm.driver.driverClassName.match("(NMEADriver|RegexDriver)")) {
                vm.headerLine = true;
            }
        };

        /**-------------------- Private methods ----------------**/

        /**
         * Load parameters: if a driver and a channel have been specified, they are loaded.
         */
        function loadParams() {
            if (driver !== undefined) {
                vm.driver = driver;
                vm.title = "Edit driver '";
                angular.forEach(vm.driver.fields, function (field) {
                    vm.fieldList.push(new Field(field.fieldIndex, field.frame, field.data, field.option));
                });
            }
            if (channelList !== undefined) {
                angular.forEach( channelList, function(channel) {
                    vm.channelList.push(channel);
                });
            }
        }

        /**-------------------- View-model methods ----------------**/

        vm.addField = function addField() {
            vm.fieldList.push(new Field());
        };

        vm.removeField = function removeField(field) {
            var index = vm.fieldList.indexOf(field);
            if (index != -1)
                vm.fieldList.splice(index, 1);
        };

        vm.cancel = function cancel() {
            $mdDialog.cancel();
        };

        vm.save = function save() {
            vm.driver.fields = [];
            angular.forEach(vm.fieldList, function (field) {
                var frame = field.frame.split(":")[0];
                var data = field.frame.split(":")[1];
                vm.driver.fields.push(new Field(field.fieldIndex,
                                                frame,
                                                data,
                                                field.option));
                var exist = 0;
                angular.forEach(vm.frames, function (f) {
                    if(frame === f) {
                        exist = 1;
                    }
                });
                if (exist === 0) {
                    vm.frames.push(frame);
                }
            });

            $mdDialog.hide({driver: vm.driver, frames:vm.frames});
        };
    }
})();

(function () {
    'use strict';
    angular.module('tsa.services.dialog').controller('dataSelectionController', dataSelectionController);

    /**  Add data dialog's controller */
    function dataSelectionController($mdDialog, deviceList) {

        /** ------------------------------ DATA CLASS-----------------------------**/
        function Data(id, frameId, deviceId) {
            this.id = id;
            this.frameId = frameId;
            this.deviceId = deviceId;
        }

        /** ---------------------------- DATA LIST CLASS -------------------------**/
        function DataList() {
            this.dataList = [];
        }

        /* Add a new data (if it doesn't already exist ) */
        DataList.prototype.add = function add(id, frameId, deviceId) {
            if (!this.includes(id, frameId, deviceId))
                this.dataList.push(new Data(id, frameId, deviceId));
        };

        /* Remove a data from the list */
        DataList.prototype.remove = function remove(id, frameId, deviceId) {
            var index = undefined;
            angular.forEach(this.dataList, function (data) {
                if (data.id == id & data.frameId == frameId & data.deviceId == deviceId)
                    this.splice(this.indexOf(data), 1);
            }, this.dataList);
        };

        /* Check if the list contains the specified data */
        DataList.prototype.includes = function includes(id, frameId, deviceId) {
            var dataIsIncluded = false;
            angular.forEach(this.dataList, function (data) {
                if (data.id == id & data.frameId == frameId & data.deviceId == deviceId)
                    dataIsIncluded = true;
            });
            return dataIsIncluded;
        };

        /** ------------------------------ PROPERTIES ----------------------------------**/
        var vm = this;
        vm.deviceList = deviceList;
        vm.selectedDataList = new DataList();

        /** ---------------------------- VIEW-MODEL METHODS ----------------------------**/

        /* Check if a specified frame is selected */
        vm.frameIsSelected = function frameIsSelected(frame, frameId, deviceId) {
            var oneDataIsSelected = false;
            var allDataSelected = true;
            angular.forEach(frame, function (data) {
                if (vm.selectedDataList.includes(data.name, frameId, deviceId))
                    oneDataIsSelected = true;
                else allDataSelected = false;
            });
            return allDataSelected ? true : oneDataIsSelected ? 'indeterminate' : false;
        }

        /* Called when a data is selected */
        vm.selectData = function selectData(id, frameId, deviceId) {
            //If the data is already selected, unselect it
            if (vm.selectedDataList.includes(id, frameId, deviceId))
                vm.selectedDataList.remove(id, frameId, deviceId);
            //else select it
            else vm.selectedDataList.add(id, frameId, deviceId);
        }

        /* Called when a frame is selected */
        vm.selectFrame = function selectFrame(frame, frameId, deviceId) {
            //if the frame is already selected, unselect it (= unselect each data)
            if (vm.frameIsSelected(frame, frameId, deviceId) === true) {
                angular.forEach(frame, function (data) {
                    vm.selectedDataList.remove(data.name, frameId, deviceId);
                });
            }
            else {// else select all the unselected data
                angular.forEach(frame, function (data) {
                    if (!vm.selectedDataList.includes(data.name, frameId, deviceId))
                        vm.selectedDataList.add(data.name, frameId, deviceId);
                });
            }
        }

        /* Called when the close button is called */
        vm.cancel = function () {
            $mdDialog.cancel();
        };

        /* Called when the "Add" button is called */
        vm.answer = function () {
            if (vm.selectedDataList.dataList.length > 0)
                $mdDialog.hide(vm.selectedDataList.dataList);
        };

    }
})();

(function () {
    'use strict';
    angular.module('tsa.services.dialog').controller('dateRangeSelectionController', dateRangeSelectionController);

    /**  Add data dialog's controller */
    function dateRangeSelectionController($mdDialog, startDate, endDate) {

        /** ------------------------------ PROPERTIES ----------------------------------**/
        var vm = this;
        vm.startDate = startDate;
        vm.startTime = (startDate.getUTCHours() > 9 ? '' : '0') + startDate.getUTCHours()
            + (startDate.getUTCMinutes() > 9 ? ':' : ':0') + startDate.getUTCMinutes()
            + (startDate.getUTCSeconds() > 9 ? ':' : ':0') + startDate.getUTCSeconds();
        vm.endDate = endDate;
        vm.endTime = (endDate.getUTCHours() > 9 ? '' : '0') + endDate.getUTCHours()
            + (endDate.getUTCMinutes() > 9 ? ':' : ':0') + endDate.getUTCMinutes()
            + (endDate.getUTCSeconds() > 9 ? ':' : ':0') + endDate.getUTCSeconds();

        /** ---------------------------- VIEW-MODEL METHODS ----------------------------**/

        /* Called when the close button is called */
        vm.cancel = function () {
            $mdDialog.cancel();
        };

        /* Called when the "Add" button is called */
        vm.answer = function () {
            // start date
            var hours = vm.startTime.split(':');
            vm.startDate.setHours(parseInt(hours[0], 10) - vm.startDate.getTimezoneOffset() / 60, hours[1], hours[2]);

            // end date
            hours = vm.endTime.split(':');
            vm.endDate.setHours(parseInt(hours[0], 10) - vm.endDate.getTimezoneOffset() / 60, hours[1], hours[2]);

            $mdDialog.hide({ start: vm.startDate, end: vm.endDate });
        };
    }
})();

(function () {
    'use strict';
    angular.module('tsa.services.dialog').controller('messagingController', messagingController);

    /**  Add data dialog's controller */
    function messagingController($mdDialog, messagingService) {

        /** ------------------------------ PROPERTIES ----------------------------------**/
        var vm = this;

        vm.messageList = messagingService.messageList;
        vm.currentMessage = undefined;

        /**---------------- initialize the message list ---------------------**/
        (function init() {
            messagingService.getLastMessages();
        })();

        /** ---------------------------- VIEW-MODEL METHODS ----------------------------**/

        /* Called when the close button is called */
        vm.close = function close() {
            $mdDialog.cancel();
        };

        vm.send= function send(){
            if (angular.isDefined(vm.currentMessage))
                messagingService.send(vm.currentMessage)
                    .then(function successCallBack (response) {
                        vm.currentMessage = undefined;
                    }, function callBackError (response) {
                        console.log('message has not been send ' +response.$resolved);
                    })
        };

        //allow tab in messaging textarea
        vm.tabFunction=function tabFunction(e) {
            var keyCode = e.keyCode || e.which;
            //Allow tab as first input
            if (vm.currentMessage===undefined){
                vm.currentMessage="";
            }
            if (keyCode === 9) {
                e.preventDefault();
                var start=e.target.selectionStart;
                var end=e.target.selectionEnd;

                // set textarea value to: text before caret + tab + text after caret
                vm.currentMessage=vm.currentMessage.substring(0,start)
                    +'\t'
                    +vm.currentMessage.substring(end);

                // put caret at right position again
                angular.element(e.target).val(vm.currentMessage);
                e.target.selectionStart=e.target.selectionEnd=start+1;
            }
        };
    }
})();

