/**
 * Messaging service
 */
(function () {
    'use strict';


    angular.module("tsa.services").service("messagingService", messagingService);
    function messagingService (dialogService, statusService, userService, Message, restService, $filter) {
        var self = this;

        self.connectionTime=statusService.getServerTime().time;
        self.messageList=[];
        self.lastMessage={};

        /**------------------------------ PUBLIC SERVICE API -------------------------------**/

        self.send=function send(message){
            // get current user or if undefined, set a default one
            var user= getUser();
            function getUser(){
                if (angular.isDefined(userService.getCurrentUser())){
                    return user=userService.getCurrentUser().name;
                }else {
                    return user="user";
                }
            }

            //Post a message
            var msg= new Message(user, message, statusService.getServerTime().time);
            return restService.sendMessage(msg)
              .then(function success(response) {
                  //refresh messages list
                  self.getLastMessages();
                  return response;
            }, function error(response){
                  return response;
            });
        };

        //Get the 10 last messages posted
        self.getLastMessages=function getLastMessages(){
            return restService.getLastMessages()
                .then(function success(response) {
                    angular.copy(response, self.messageList);
                }, function error(response){
                    return response;
                });
        };


        //Get the last message posted (tsa-views/message).
        self.getLastMessage=function getLastMessage(){
            return restService.getLastMessage()
                .then(function success(response) {
                    if (self.lastMessage.messageTime!==response.messageTime){
                        angular.copy(response, self.lastMessage);
                        //Toast pop up if message newer than connection time
                        if (self.connectionTime < response.messageTime){
                            dialogService.showToastChat($filter('translate')('toast.newMessage'));
                            self.connectionTime=response.messageTime;
                        }
                    }
                }, function error(response){
                    return response;
                });
        };

        //{{"toast.newMessage"|| translate}}
    }
})();

