(function () {
    'use strict';
    angular
        .module('tsa.services.dialog')
        .controller('viewSelectionController', ViewSelectionController);

    function ViewSelectionController($mdDialog, acquisitionService, viewProvider) {
        var vm = this;

        // Retrieve the device list
        vm.viewProvider = viewProvider;
        vm.viewList = viewProvider.getViewList();
        vm.mosaicList = viewProvider.getMosaicList();
        
        // Watch the device list updates
        var deviceListListener = acquisitionService.registerForDeviceList();
        deviceListListener.promise.then({}, {}, function () {
            vm.viewList = viewProvider.getViewList();
        });

        /** Called when the dialog is closed without selection **/
        vm.cancel = function () {
            viewProvider.stopRefreshMosaic();
            acquisitionService.unregisterForDeviceList(deviceListListener);
            $mdDialog.cancel();
        };

        /** Called when a view has been selected **/
        vm.selectView = function selectView(view) {
            viewProvider.stopRefreshMosaic();
            acquisitionService.unregisterForDeviceList(deviceListListener);
            $mdDialog.hide({view: view.type, config: view.config, title: view.title, icon: view.icon});
        };

    }
})();

(function () {
    'use strict';

    /** Component **/
    angular.module('tsa.tsaViews.codeNameFields', [])
        .component('tsaCodeNameFields', {
            templateUrl: "app/tsa-views/casino/codeNameFields/codeNameFields.html",
            controller: codeNameFieldsController,
            controllerAs: "ctrl",
            bindings: {
                itemType: '@',
                listItems: '<',
                itemCode: '<',
                onItemChange: '&',
                itemReadonly: '<',
                itemFollow: '<'
            }
        });

    /** Controller **/
    function codeNameFieldsController() {
        var vm = this;

        /**------------------------------ PROPERTIES -------------------------------**/
        vm.selectedItem;

        /**-------------------------- INITIALIZATION / DESTROY METHODS -------------------------**/

        /** Init function: select initial item **/
        vm.$onInit = function () {
            if(vm.listItems && vm.itemCode){
                angular.forEach(vm.listItems, function(item) {
                    if (item.code === vm.itemCode){
                        vm.selectedItem = item;
                        vm.updateItem();
                    }
                })
            }
        };

        /** OnChanges: Called whenever one-way bindings (itemCode, listItems) are updated. */
        vm.$onChanges = function (changesObj) {
            if (changesObj['listItems'] && angular.isDefined(changesObj['listItems'].currentValue)) {
                vm.selectedItem = undefined;
            }
            if (changesObj['itemCode'] && angular.isDefined(changesObj['itemCode'].currentValue)) {
                if(vm.listItems){
                    angular.forEach(vm.listItems, function(item) {
                        if (item.code === changesObj['itemCode'].currentValue){
                            vm.selectedItem = item;
                        }
                    })
                }
            }
        };

        /** Called when the view is destroyed. */
        vm.$onDestroy = function onDestroy() {
            // nothing to do
        };

        /**--------------------------  METHODS -------------------------**/
        vm.updateItem = function() {
            // send event
            vm.onItemChange({$item: vm.selectedItem});
        }
    }
})();

(function () {
    'use strict';

    /** Component **/
    angular.module('tsa.pages.dataAccess.paramSelector', [])
        .component('tsaParamSelector', {
            templateUrl: "app/pages/dataAccess/components/paramSelector/paramSelector.html",
            controller: paramSelectorController,
            controllerAs: "ctrl",
            bindings: {
                getParams: '=',
                mode: '<'
            }
        });

    /** Controller **/
    function paramSelectorController(dialogService) {

        /**------------------------------ PROPERTIES ----------------------------------**/
        var vm = this;
        vm.simpleSampingInterval = 60;
        vm.maxValues = 100000;
        vm.timeStampingStrategy = 'resampling';
        vm.reference = 'reference';
        vm.resampleInterval = 1;
        vm.intervalStampFraction = 1;

        /**------------------- PUBLIC METHOD (used by others components) -----------------**/

        /* Get the selected data range */
        vm.getParams = function getParams() {
            var result = { maxValues: vm.maxValues };

            //Simple mode
            if (vm.mode == 'simple') {
                result['timeStampingStrategy'] = 'resampling';
                result['intervalOrRef'] = vm.simpleSampingInterval;
                return result;
            }

            //Advanced mode
            result['timeStampingStrategy'] = vm.timeStampingStrategy;
            switch (vm.timeStampingStrategy) {
                case 'reference':
                    result['intervalOrRef'] = vm.reference;
                    break;
                case 'resampling':
                    result['intervalOrRef'] = vm.resampleInterval;
                    result['intervalStampFraction'] = vm.intervalStampFraction;
                    break;
            }
            return result;
        }
    }
})();

