(function () {
    'use strict';

    /** Component **/
    angular.module('tsa.pages.dataAccess.resultCard', [])
        .component('tsaResultCard', {
            templateUrl: "app/pages/dataAccess/components/resultCard/resultCard.html",
            controller: resultCardController,
            controllerAs: "ctrl",
            bindings: {
                frames: '<',
                description: '<'
            }
        });

    /** Controller **/
    function resultCardController($timeout, $filter, $scope, graphUtils, themeService) {

        /**------------------------------ PROPERTIES ----------------------------------**/
        var vm = this;
        vm.displayMode = "table";
        vm.chartData = { datasets: [] };

        // Chart properties
        vm.canvasId = "resultView";
        vm.chart = undefined;
        var chartColorIndex = 0;
        var chartColors = [
            "rgb(26, 117, 174)", //Blue ifremer
            "#8BC34A",  // Light green
            "#FF5722",  // Orange
            "#3F51B5",  // Indigo
            "#F44336",  // Red
            "#9C27B0",  // Purple
            "#FDD835",  // yellow
            "#2196F3",  // Blue
            "#E91E63",  // Pink
            "#009688",  // blue-green
            "#673AB7",  // Deep purple
            "#795548",  // Brown
            "#E91E63",  // Pink
            "#CDDC39"   // Lime
        ];


        /**------------------------------ CHART TAB METHODS -------------------------------**/

        /** DataSet constructor **/
        function DataSet(id, color) {
            this.label = id;
            this.data = [];
            this.hidden = false;
            this.borderColor = chartColors[chartColorIndex % chartColors.length];
            chartColorIndex++;
            angular.forEach(vm.frames, function (frame) {
                this.data.push({ x: frame['time'], y: frame[id] });
            }, this);
        }

        /** Redraw the chart **/
        function reloadChart() {
            if (vm.chart !== undefined)
                vm.chart.destroy();
            var canvas = document.getElementById(vm.canvasId);
            //canvas.addEventListener('click', function(event) {console.log(event) }, false);
            if (canvas)
                vm.chart = Chart.Scatter(canvas, { data: vm.chartData, options: graphUtils.getChartOptions() });
        }

        /** Update the chart with new frame list */
        vm.updateChart = function updateChart() {
            chartColorIndex = 0;
            vm.chartData.datasets = [];
            if (angular.isDefined(vm.frames) && vm.frames.length > 0) {
                angular.forEach(vm.frames[0], function (value, key) {
                    if (key != "time" && key != "$$hashKey")
                        vm.chartData.datasets.push(new DataSet(key));
                })
                if (vm.chart != undefined)
                    vm.chart.update();
            }
        }

        /**------------------------------ MAP TAB METHODS -------------------------------**/


    /**------------------------------ PUBLIC VIEW-MODEL METHODS ----------------------------**/

    /** Initialization function **/
    vm.$onInit = function init() {
        var timeoutPromise = $timeout(reloadChart, 200);
        //var timeoutPromise = $timeout(vm.showMap, 200);
        // Change gridLinesColor of Graph on every change of service variable
        themeService.subscribe($scope, reloadChart);
    };

    vm.$onChanges = function (changesObj) {
        vm.updateChart();
        if (vm.frames != undefined && vm.frames.length > 0) {
            vm.framesTable = vm.frames.slice();
            angular.forEach(vm.framesTable, function (frame) {
                frame.time = $filter('date')(new Date(frame.time), "dd MMM yyyy; HH:mm:ss", "+0");
            });
        }
    };


}
})();

(function () {
    'use strict';

    /** Component **/
    angular.module('tsa.pages.dataAccess.variableSelector', [])
        .component('tsaVariableSelector', {
            templateUrl: "app/pages/dataAccess/components/variableSelector/variableSelector.html",
            controller: variableSelectorController,
            controllerAs: "ctrl",
            bindings: {
                getVariableList: '=',
                mode: '<'
            }
        });

    /** Controller **/
    function variableSelectorController($filter, acquisitionService, archiveService, dialogService, restService, DATA_ACCESS) {

        /**------------------- PREDEFINED DATA LIST (get from /frontend/parameters during the bootstrap step)------------------------**/
        var navigationDataList = DATA_ACCESS.NAVIGATION;
        var weatherDataList = DATA_ACCESS.WEATHER;
        var thermosalDataList = DATA_ACCESS.THERMOSAL;

        /**------------------------------ PROPERTIES -----------------------------**/
        var vm = this;

//        vm.aggregateList = ["raw", "hold", "nearest", "interpolate", "total", "count", "average"];
        vm.aggregateList = ["RAW", "HOLD", "NEAREST", "INTERPOLATE", "TOTAL", "COUNT", "AVERAGE"];

        vm.simpleDataList = [];
        /* USE FOR TEST
            { id: 'airpressure', frameId: 'weather', deviceId: 'BATOS001', colname: 'Air pressure' },
            { id: 'relairspeed', frameId: 'weather', deviceId: 'BATOS001', colname: 'Rel air speed' }
        ];*/

        vm.advancedDataList = [];
        /* USE FOR TEST
            { varid: "GYRO001!gyro!head", colname: "heading", aggregate: "raw", units: "", default: "default", min: "0", max: "360" },
            { varid: "BATOS001!weather!airtemp", colname: "air temperature", aggregate: "raw", units: "", default: "default", min: "0", max: "360" },
            new archiveService.Variable('watertemp', 'weather', 'BATOS001')];*/

        /**------------------------------ PRIVATE METHODS -----------------------------**/

        function addDataToSimpleList(dataList) {
            var dataAlreadyPresent = [];
            angular.forEach(dataList, function (data) {
                if (data.colname === undefined) //Add colname if undefined
                    data.colname = data.id;

                angular.forEach(vm.simpleDataList, function (dataInTheList) {
                    if (dataInTheList.id === data.id && dataInTheList.frameId === data.frameId && dataInTheList.deviceId === data.deviceId)
                        dataAlreadyPresent.push(data);
                });

                if (!dataAlreadyPresent.includes(data))
                    vm.simpleDataList.push(data);
            });

            if (dataAlreadyPresent.length > 0)
                dialogService.showDialog($filter('translate')("pages.data-access.data-selection"),
                    $filter('translate')("pages.data-access.data-already-in-table"));
        }


        /**------------------- PUBLIC METHOD (used by others components) -----------------**/

        /* Get the selected data range */
        vm.getVariableList = function getVariableList() {
            if (vm.mode === 'advanced') {
                return vm.advancedDataList;
            }
            else {
                var variableList = [];
                angular.forEach(vm.simpleDataList, function (data) {
                    variableList.push(new archiveService.Variable(data.id, data.frameId, data.deviceId, undefined, data.colname));
                });
                return variableList;
            }
        };

        /**------------------------------ VIEW MODEL METHODS -------------------------------**/

        /* Add new data in the custom data table */
        vm.addData = function addData() {
            switch (vm.mode) {
                case 'simple':
                    restService.getArchiveDevices().then(
                        function success(deviceList) { dialogService.showSelectData(deviceList).then(addDataToSimpleList); },
                        function error() {
                            dialogService.showDialog($filter('translate')("pages.data-access.error"),
                                $filter('translate')("pages.data-access.device-list-unavailable"));
                        });
                    break;
                case 'advanced':
                    vm.advancedDataList.push({
                        varid: "",
                        colname: "",
                        aggregate: "",
                        units: "",
                        default: "",
                        min: "",
                        max: ""
                    });
                    break;
            }
        };

        /* Clear all the list */
        vm.clearAll = function clearAll() {
            switch (vm.mode) {
                case 'simple': vm.simpleDataList = []; break;
                case 'advanced': vm.advancedDataList = []; break;
            }
        };

        /* Add the predefined navigation data list */
        vm.addNavData = function addNavData() {
            addDataToSimpleList(navigationDataList);
        };

        /* Add the predefined weather data list */
        vm.addWeatherData = function addWeatherData() {
            addDataToSimpleList(weatherDataList);

        };

        /* Add the predefined thermosalinometer data list */
        vm.addTSData = function addTSData() {
            addDataToSimpleList(thermosalDataList);
        }
    }
})();

